'use strict';
// const TYPE = process.type;
let UI = {};
// if (TYPE === 'browser') {
//     UI = require('./browser/index');
// } else if (TYPE === 'renderer') {
//
// }
const components = require('./renderer/index');
// 
const { updateTranslate, registerTranslators } = require('./utils/utils');
UI = {
    ...components,
    /**
     * 
     * Registers a custom node on the current page
     * 
     * Use with caution and it will be removed later
     *
     * @param tagName 
     * @param element 
     */
    register(tagName, element) {
        if (window && !window.customElements.get(tagName)) {
            window.customElements.define(tagName, element);
        }
    },
    registerTranslator(fn) {
        if (typeof fn !== 'function') {
            throw new Error('Translator is not a function');
        }
        registerTranslators(fn);
    },
    updateTranslate(componentList = components) {
        updateTranslate(componentList);
    },
};
module.exports = UI;
