'use strict';
const fs = require('fs');
const Chroma = require('chroma-js');
import { ComplexBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
export class GradientPicker extends ComplexBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.activeOrbital = null;
        this.activeProgress = null;
        this._map = {};
        this._value = {};
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$preview = this.shadowRoot.querySelector('.preview');
        this.$previewAlpha = this.shadowRoot.querySelector('.preview .alpha .background');
        this.$previewHybrid = this.shadowRoot.querySelector('.preview .hybrid .background');
        this.$previewColor = this.shadowRoot.querySelector('.preview .color .background');
        this.$alphas = this.shadowRoot.querySelector('.preview .alpha .keys');
        this.$colors = this.shadowRoot.querySelector('.preview .color .keys');
        this.$location = this.shadowRoot.querySelector('.location');
        this.$color = this.shadowRoot.querySelector('.operation .color');
        this.$color.$root = this.$location.$root = this.$previewColor.$root = this.$previewAlpha.$root = this.$preview.$root = this;
    }
    /**
     * 
     * @param {filePath} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    // ////////////////////
    get value() {
        return this._value;
    }
    set value(val) {
        if (!val) {
            this.removeAttribute('value');
            return;
        }
        if (typeof val !== 'object') {
            val = { alpha: [], color: [] };
        }
        try {
            this.setAttribute('value', JSON.stringify(val));
        }
        catch (error) {
            console.warn(error);
            this.removeAttribute('value');
        }
    }
    get fixed() {
        return this.hasAttribute('fixed');
    }
    set fixed(val) {
        if (val) {
            this.setAttribute('fixed', '');
        }
        else {
            this.removeAttribute('fixed');
        }
    }
    get invalid() {
        return this.hasAttribute('invalid');
    }
    set invalid(val) {
        if (val) {
            this.setAttribute('invalid', '');
        }
        else {
            this.removeAttribute('invalid');
        }
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        this.shadowRoot.querySelector('section')?.setAttribute('tabindex', '-1');
        this._location = 0;
        // 
        this.addEventListener('blur', this._onBlur);
        this.$alphas.addEventListener('keydown', (e) => this._onSliderKeysKeyDown(e, 'alpha'));
        this.$colors.addEventListener('keydown', (e) => this._onSliderKeysKeyDown(e, 'color'));
        this.$preview.addEventListener('mouseup', this._onPreviewMouseDown, true);
        this.$location.addEventListener('change', this._onLocationChange);
        this.$color.addEventListener('change', this._onColorChange);
        this.$previewAlpha.addEventListener('click', this._onAlphaClick);
        this.$previewColor.addEventListener('click', this._onColorClick);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.removeEventListener('blur', this._onBlur);
        this.$alphas.removeEventListener('keydown', this._onSliderKeysKeyDown);
        this.$colors.removeEventListener('keydown', this._onSliderKeysKeyDown);
        this.$preview.removeEventListener('mouseup', this._onPreviewMouseDown, true);
        this.$location.removeEventListener('change', this._onLocationChange);
        this.$color.removeEventListener('change', this._onColorChange);
        this.$previewAlpha.removeEventListener('click', this._onAlphaClick);
        this.$previewColor.removeEventListener('click', this._onColorClick);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'value',
            'disabled',
            'readonly',
            'focused',
            'invalid',
            'path',
        ];
    }
    /**
     * @description attribute 
     * @param attr
     * @param newVal
     * @param oldVal
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'value':
                if (oldVal === newVal) {
                    break;
                }
                this._onValueChanged(newVal);
                break;
            case 'disabled':
                if (newVal === null) {
                    this.disabled = false;
                }
                break;
            case 'invalid':
                this._updateHybrid();
                break;
        }
    }
    // ////////////////////
    /**
     * @description color / alpha 
     * @param event
     * @param type
     */
    _onSliderKeysKeyDown(event, type) {
        switch (event?.key) {
            case 'Delete':
                this._onDeleteKeyForSliderKeyHandle(event.target, type);
                break;
            default:
                break;
        }
    }
    /**
     * @description color / alpha  delete 
     * @param el
     * @param type
     * */
    _onDeleteKeyForSliderKeyHandle(el, type) {
        if (el && type) {
            const keyIndex = el.getAttribute('index');
            const values = (type === 'alpha' ? this._value.alpha : this._value.color) ?? {};
            const targetKey = values[keyIndex];
            if (targetKey) {
                const index = values.indexOf(targetKey);
                values.splice(index, 1);
                values.sort((a, b) => { return a.progress - b.progress; });
                this._updateAlpha();
                this._updateHybrid();
                this.setAttribute('value', JSON.stringify(this._value));
                this._updateActive(type, targetKey);
            }
        }
    }
    /**
     *  value 
     */
    _onValueChanged(val) {
        this.invalid = false;
        if (val) {
            try {
                this._value = JSON.parse(val);
            }
            catch (error) {
                console.warn(error);
                this.value = null;
            }
        }
        else {
            this._value = {};
        }
        this._updateAlpha();
        this._updateAlphaKeys();
        this._updateColor();
        this._updateColorKeys();
        this._updateHybrid();
    }
    /**
     * gradient-picker 
     */
    _onBlur() {
        if (this.disabled) {
            return;
        }
        this.dispatch('confirm');
    }
    /**
     * 
     */
    _onAlphaClick() {
        const $elem = this;
        const alphaKey = {
            value: 1,
            progress: $elem.$root._location,
        };
        $elem.$root._value.alpha = $elem.$root._value.alpha || [];
        $elem.$root._value.alpha.push(alphaKey);
        $elem.$root._value.alpha.sort((a, b) => { return a.progress - b.progress; });
        $elem.$root.setAttribute('value', JSON.stringify($elem.$root._value));
        $elem.$root._updateActive('alpha', alphaKey);
    }
    /**
     * 
     */
    _onColorClick() {
        const $elem = this;
        const colorKey = {
            value: {
                r: 255,
                g: 255,
                b: 255,
            },
            progress: $elem.$root._location,
        };
        $elem.$root._value.color = $elem.$root._value.color || [];
        $elem.$root._value.color.push(colorKey);
        $elem.$root._value.color.sort((a, b) => { return a.progress - b.progress; });
        $elem.$root.setAttribute('value', JSON.stringify($elem.$root._value));
        $elem.$root._updateActive('color', colorKey);
    }
    /**
     * 
     * 
     */
    _onAlphaKeyMouseDown(event) {
        const $elem = this;
        const stagingPageX = event.pageX;
        const stagingPageY = event.pageY;
        const index = this.getAttribute('index') || '';
        const targetAlphaKey = $elem.$root._value.alpha[index];
        const targetElement = event.target.tagName === 'SPAN' ? event.target.parentElement : event.target;
        const stagingLeft = parseInt(targetElement.style.left, 10);
        const stagingProgress = targetAlphaKey.progress;
        $elem.$root._updateActive('alpha', targetAlphaKey);
        const mousemove = (event) => {
            const offset = (event.pageX - stagingPageX) / ($elem.$root.$preview.clientWidth - 40);
            targetAlphaKey.progress = Math.min(Math.max(stagingProgress + offset, 0), 1);
            targetElement.style.left = Math.min(Math.max(stagingLeft + (event.pageX - stagingPageX), 0), $elem.$root.$preview.clientWidth - 40) + 'px';
            const index = $elem.$root._value.alpha.indexOf(targetAlphaKey);
            if (Math.abs(event.pageY - stagingPageY) > 20) {
                if (index !== -1) {
                    $elem.$root._value.alpha.splice(index, 1);
                    targetElement.hidden = true;
                    $elem.$root._updateActive('', null);
                }
            }
            else {
                if (index === -1) {
                    $elem.$root._value.alpha.push(targetAlphaKey);
                    targetElement.hidden = false;
                    $elem.$root._updateActive('alpha', targetAlphaKey);
                }
            }
            $elem.$root._value.alpha.sort((a, b) => { return a.progress - b.progress; });
            $elem.$root._updateAlpha();
            $elem.$root._updateHybrid();
        };
        const mouseup = () => {
            document.removeEventListener('mousemove', mousemove);
            document.removeEventListener('mouseup', mouseup);
            // const index = $elem.$root._value.alpha.indexOf(targetAlphaKey);
            $elem.$root.setAttribute('value', JSON.stringify($elem.$root._value));
            // if (event.pageX - stagingPageX === 0 && event.pageY - stagingPageY === 0 && index !== -1) {
            $elem.$root._updateActive('alpha', targetAlphaKey);
            // }
        };
        document.addEventListener('mousemove', mousemove);
        document.addEventListener('mouseup', mouseup);
    }
    /**
     * 
     * 
     */
    _onColorKeyMouseDown(event) {
        const $elem = this;
        const stagingPageX = event.pageX;
        const stagingPageY = event.pageY;
        const index = this.getAttribute('index') || '';
        const targetColorKey = $elem.$root._value.color[index];
        const targetElement = event.target.tagName === 'SPAN' ? event.target.parentElement : event.target;
        const stagingLeft = parseInt(targetElement.style.left, 10);
        const stagingProgress = targetColorKey.progress;
        $elem.$root._updateActive('color', targetColorKey);
        const mousemove = (event) => {
            const offset = (event.pageX - stagingPageX) / ($elem.$root.$preview.clientWidth - 40);
            targetColorKey.progress = Math.min(Math.max(stagingProgress + offset, 0), 1);
            targetElement.style.left = Math.min(Math.max(stagingLeft + (event.pageX - stagingPageX), 0), $elem.$root.$preview.clientWidth - 40) + 'px';
            const index = $elem.$root._value.color.indexOf(targetColorKey);
            if (Math.abs(event.pageY - stagingPageY) > 20) {
                if (index !== -1) {
                    $elem.$root._value.color.splice(index, 1);
                    targetElement.hidden = true;
                    $elem.$root._updateActive('', null);
                }
            }
            else {
                if (index === -1) {
                    $elem.$root._value.color.push(targetColorKey);
                    targetElement.hidden = false;
                    $elem.$root._updateActive('color', targetColorKey);
                }
            }
            $elem.$root._value.color.sort((a, b) => { return a.progress - b.progress; });
            $elem.$root._updateColor();
            $elem.$root._updateHybrid();
        };
        const mouseup = () => {
            document.removeEventListener('mousemove', mousemove);
            document.removeEventListener('mouseup', mouseup);
            // const index = $elem.$root._value.color.indexOf(targetColorKey);
            $elem.$root.setAttribute('value', JSON.stringify($elem.$root._value));
            // if (event.pageX - stagingPageX === 0 && event.pageY - stagingPageY === 0 && index !== -1) {
            $elem.$root._updateActive('color', targetColorKey);
            // }
        };
        document.addEventListener('mousemove', mousemove);
        document.addEventListener('mouseup', mouseup);
    }
    // ////////////////////
    // 
    /**
     *  preview  _location 
     *  key 
     */
    _onPreviewMouseDown(event) {
        const $elem = this;
        let offsetX = event.offsetX;
        let target = event.target;
        while (target !== $elem.$root.$preview) {
            offsetX += target.offsetLeft;
            target = target.offsetParent;
        }
        $elem.$root._location = Math.min(Math.max((offsetX - 20) / ($elem.$root.$preview.clientWidth - 40), 0), 1);
    }
    /**
     * location 
     */
    _onLocationChange(event) {
        const $elem = this;
        if ($elem.$root.activeOrbital === 'alpha') {
            $elem.$root._value.alpha.some((item) => {
                if (item.progress !== $elem.$root.activeProgress) {
                    return false;
                }
                $elem.$root.activeProgress = item.progress = event.target.value / 100;
            });
            $elem.$root._value.alpha.sort((a, b) => { return a.progress - b.progress; });
            $elem.$root._updateAlpha();
            $elem.$root._updateAlphaKeys();
        }
        else if ($elem.$root.activeOrbital === 'color') {
            $elem.$root._value.color.some((item) => {
                if (item.progress !== $elem.$root.activeProgress) {
                    return false;
                }
                $elem.$root.activeProgress = item.progress = event.target.value / 100;
            });
            $elem.$root._value.color.sort((a, b) => { return a.progress - b.progress; });
            $elem.$root._updateColor();
            $elem.$root._updateColorKeys();
        }
        $elem.$root._updateHybrid();
    }
    /**
     * 
     */
    _onColorChange(event) {
        const $elem = this;
        if ($elem.$root.activeOrbital === 'color') {
            $elem.$root._value.color.some((item) => {
                if (item.progress !== $elem.$root.activeProgress) {
                    return false;
                }
                item.value = Chroma(event.target.value, 'rgb').hex();
            });
            $elem.$root._updateColor();
            $elem.$root._updateColorKeys();
        }
        else if ($elem.$root.activeOrbital === 'alpha') {
            $elem.$root._value.alpha.some((item) => {
                if (item.progress !== $elem.$root.activeProgress) {
                    return false;
                }
                item.value = event.target.value[3] / 255;
            });
            $elem.$root._updateAlpha();
            $elem.$root._updateAlphaKeys();
        }
        $elem.$root._updateHybrid();
    }
    /**
     * 
     */
    _mountAlphaKeysEvent() {
        const alphaKeys = this.$alphas.querySelectorAll('div');
        alphaKeys.forEach((alphaKey) => {
            alphaKey.$root = this;
            alphaKey.addEventListener('mousedown', this._onAlphaKeyMouseDown);
        });
    }
    /**
     * 
     */
    _mountColorKeysEvent() {
        const colorKeys = this.$colors.querySelectorAll('div');
        colorKeys.forEach((colorKey) => {
            colorKey.$root = this;
            colorKey.addEventListener('mousedown', this._onColorKeyMouseDown);
        });
    }
    /**
     * 
     */
    _updateColor() {
        const value = this.invalid ? { color: [], alpha: [] } : (this.value || { color: [], alpha: [] });
        let colorStyle = '';
        // 
        if (value.color && value.color.length) {
            colorStyle = 'linear-gradient(to right';
            value.color.forEach((item, index) => {
                const rgb = Chroma(item.value).rgb();
                if (index === 0 && item.progress !== 0) {
                    colorStyle += `, rgb(${rgb[0]}, ${rgb[1]}, ${rgb[2]}) 0%`;
                }
                colorStyle += `, rgb(${rgb[0]}, ${rgb[1]}, ${rgb[2]}) ${item.progress * 100 | 0}%`;
                if (this.fixed && value.color[index + 1]) {
                    const nextRgb = Chroma(value.color[index + 1].value).rgb();
                    colorStyle += `, rgb(${nextRgb[0]}, ${nextRgb[1]}, ${nextRgb[2]}) ${(item.progress * 100 | 0) - 0.01}%`;
                }
                if (index === value.color.length - 1 && item.progress !== 1) {
                    colorStyle += `, rgb(${rgb[0]}, ${rgb[1]}, ${rgb[2]}) 100%`;
                }
            });
            colorStyle += ')';
        }
        this.$previewColor.style.background = colorStyle;
    }
    /**
     * 
     */
    _updateColorKeys() {
        const value = this.invalid ? { color: [], alpha: [] } : (this.value || { color: [], alpha: [] });
        let colorKeysHTML = '';
        // color-picker displaynone 0
        const clientWidth = this.$colors.clientWidth || 448;
        value.color && value.color.forEach((item, index) => {
            const rgb = Chroma(item.value).rgb();
            const focused = this.activeOrbital === 'color' && this.activeProgress === item.progress;
            colorKeysHTML += `<div tabindex="${index}" index="${index}" ${focused ? 'focused' : ''} style="left: ${item.progress * clientWidth}px;"><span style="background: rgb(${rgb[0]}, ${rgb[1]}, ${rgb[2]})"></span></div>`;
        });
        this.$colors.innerHTML = colorKeysHTML;
        this._mountColorKeysEvent();
    }
    /**
     * 
     */
    _updateAlpha() {
        const value = this.invalid ? { color: [], alpha: [] } : (this.value || { color: [], alpha: [] });
        let alphaStyle = '';
        // 
        if (value.alpha && value.alpha.length) {
            alphaStyle = 'linear-gradient(to right';
            value.alpha.forEach((item, index) => {
                if (index === 0 && item.progress !== 0) {
                    alphaStyle += `, rgba(255,255,255,${item.value}) 0%`;
                }
                alphaStyle += `, rgba(255,255,255,${item.value}) ${item.progress * 100 | 0}%`;
                if (this.fixed && value.alpha[index + 1]) {
                    alphaStyle += `, rgba(255,255,255,${value.alpha[index + 1].value}) ${(item.progress * 100 | 0) - 0.01}%`;
                }
                if (index === value.alpha.length - 1 && item.progress !== 1) {
                    alphaStyle += `, rgba(255,255,255,${item.value}) 100%`;
                }
            });
            alphaStyle += ')';
        }
        this.$previewAlpha.style.background = alphaStyle;
    }
    /**
     * 
     */
    _updateAlphaKeys() {
        const value = this.invalid ? { color: [], alpha: [] } : (this.value || { color: [], alpha: [] });
        let alphaKeysHTML = '';
        // color-picker displaynone 0
        const clientWidth = this.$alphas.clientWidth || 448;
        // 
        value.alpha && value.alpha.forEach((item, index) => {
            const alpha = item.value * 255;
            const focused = this.activeOrbital === 'alpha' && this.activeProgress === item.progress;
            alphaKeysHTML += `<div tabindex="${index}" index="${index}" ${focused ? 'focused' : ''} style="left: ${item.progress * clientWidth}px;"><span style="background: rgb(${alpha}, ${alpha}, ${alpha})"></span></div>`;
        });
        this.$alphas.innerHTML = alphaKeysHTML;
        this._mountAlphaKeysEvent();
    }
    /**
     * 
     */
    _updateHybrid() {
        // 
        const value = this.invalid ? { color: [], alpha: [] } : (this.value || { color: [], alpha: [] });
        // 
        const alphas = value.alpha || [{ value: 1, progress: 0 }];
        const colors = value.color || [{ value: '#ffffff', progress: 0 }];
        //  color  alpha 
        const map = this._map = {
            0: { r: 255, g: 255, b: 255, a: 255 },
            1: { r: 255, g: 255, b: 255, a: 255 }, // 
        };
        // 
        alphas.forEach((item, index) => {
            const color = map[item.progress] = map[item.progress] || {};
            color.a = item.value * 255 | 0;
            if (index === alphas.length - 1) {
                map[1].a = item.value * 255 | 0;
            }
            if (index === 0) {
                map[0].a = item.value * 255 | 0;
            }
        });
        // 
        colors.forEach((item, index) => {
            const rgb = Chroma(item.value).rgb();
            const color = map[item.progress] = map[item.progress] || {};
            color.r = rgb[0];
            color.g = rgb[1];
            color.b = rgb[2];
            if (index === colors.length - 1) {
                map[1].r = rgb[0];
                map[1].g = rgb[1];
                map[1].b = rgb[2];
            }
            if (index === 0) {
                map[0].r = rgb[0];
                map[0].g = rgb[1];
                map[0].b = rgb[2];
            }
        });
        // 
        const keys = Object.keys(map).sort();
        //  style 
        function fill(index, name) {
            const key = keys[index];
            let _i;
            _i = index;
            let prev;
            do {
                _i -= 1;
                prev = map[keys[_i]];
            } while (prev[name] === undefined);
            _i = index;
            let next;
            do {
                _i += 1;
                next = map[keys[_i]];
            } while (next[name] === undefined);
            const progress = (key - keys[index - 1]) / (keys[index + 1] - keys[index - 1]);
            return ((next[name] - prev[name]) * progress) + prev[name];
        }
        //  style
        let hybridStyle = 'linear-gradient(to right';
        keys.forEach((key, index) => {
            const color = map[key];
            if (color.r === undefined) {
                color.r = Math.round(fill(index, 'r'));
            }
            if (color.g === undefined) {
                color.g = Math.round(fill(index, 'g'));
            }
            if (color.b === undefined) {
                color.b = Math.round(fill(index, 'b'));
            }
            if (color.a === undefined) {
                color.a = Math.round(fill(index, 'a'));
            }
            color.progress = key;
        });
        keys.forEach((key, index) => {
            const color = map[key];
            hybridStyle += `, rgba(${color.r}, ${color.g}, ${color.b}, ${color.a / 255}) ${key * 100 | 0}%`;
            if (this.fixed && keys[index + 1]) {
                const nextColor = map[keys[index + 1]];
                hybridStyle += `, rgb(${nextColor.r}, ${nextColor.g}, ${nextColor.b}, ${nextColor.a / 255}) ${(key * 100 | 0) - 0.01}%`;
            }
            color.progress = key;
        });
        hybridStyle += ')';
        this.$previewHybrid.style.background = hybridStyle;
        this.dispatch('change');
    }
    /**
     * @description  color-picker 
     * @param orbital
     * @param key
     */
    _updateActive(orbital, key) {
        this.activeOrbital = orbital;
        this.activeProgress = key ? key.progress : 0;
        this.$location.parentElement.disabled = !this.activeOrbital;
        //  color-picker 
        if (this.activeOrbital === 'color') {
            this.$color.removeAttribute('disabled-color');
            this.$color.setAttribute('disabled-alpha', '');
        }
        else if (this.activeOrbital === 'alpha') {
            this.$color.setAttribute('disabled-color', '');
            this.$color.removeAttribute('disabled-alpha');
        }
        else {
            this.$color.setAttribute('disabled-color', '');
            this.$color.setAttribute('disabled-alpha', '');
        }
        //  color-picker + num-input 
        if (orbital === 'alpha') {
            this.$location.value = (key.progress * 100).toFixed(2);
            this.$color.value = [255, 255, 255, key.value * 255];
        }
        else if (orbital === 'color') {
            this.$location.value = (key.progress * 100).toFixed(2);
            let rgba = [key.value.r, key.value.g, key.value.b, 255];
            //  key.value = '#333333';
            if (typeof key.value === 'string') {
                rgba = Chroma(key.value).rgba();
                rgba[3] = 255;
            }
            this.$color.value = rgba;
        }
        else {
            this.$location.value = 0;
            this.$color.value = [255, 255, 255, 255];
        }
    }
}
