'use strict';
const fs = require('fs');
import { DisplayBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
/**
 *   Attribute
 *
 *   value
 *   message
 */
export class Progress extends DisplayBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$bar = this.shadowRoot.querySelector('.bar');
        this.$percent = this.shadowRoot.querySelector('.percent');
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        return Number(this.getAttribute('value')) || 0;
    }
    set value(val) {
        if (val) {
            if (val < 0) {
                val = 0;
            }
            else if (val > 100) {
                val = 100;
            }
            this.setAttribute('value', String(val));
        }
        else {
            this.removeAttribute('value');
        }
    }
    get message() {
        return this.getAttribute('message') || '';
    }
    set message(val) {
        if (val) {
            this.setAttribute('message', val);
        }
        else {
            this.removeAttribute('message');
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'value',
            'path',
            'percent',
        ];
    }
    /**
     * attribute 
     *
     * o oldValue
     * nnewValue
     */
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'value':
                this._onValueChanged(n);
                break;
            case 'percent':
                break;
            default:
                break;
        }
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        // 
        if (this.shadowRoot) {
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    /**
     *  value 
     */
    _onValueChanged(val) {
        let value = parseFloat(val);
        if (isNaN(value)) {
            value = 0;
        }
        else if (value < 0) {
            value = 0;
        }
        else if (value > 100) {
            value = 100;
        }
        this.$bar.style.width = value + '%';
        //  message  value
        if (this.message) {
            return;
        }
        this.$percent.innerHTML = value + '%';
    }
}
