let _translateFn;
const prefix = 'i18n:';
export const translate = (val) => {
    let txt = val;
    if (val && val.trim().startsWith(prefix) && _translateFn) {
        const reg = new RegExp(prefix + '([^\\s]+)', 'g');
        txt = val.replace(reg, ($0, $1) => {
            let text = _translateFn($1);
            if (!text) {
                console.debug(`${$1} is not defined in i18n`);
                text = $1;
            }
            return text;
        });
    }
    return txt;
};
// 
export const interimTranslate = (val) => {
    let txt = val;
    if (val && _translateFn) {
        txt = _translateFn(val);
    }
    return txt;
};
export const updateTranslate = (components) => {
    if (components) {
        const componentList = Object.values(components);
        for (const component of componentList) {
            // @ts-ignore
            if (component?._updateTranslate) {
                // @ts-ignore
                component?._updateTranslate();
            }
        }
    }
};
export const registerTranslators = (fn) => {
    if (typeof fn !== 'function') {
        throw new Error('Translator is not a function');
    }
    _translateFn = fn;
};
/**
 *  Electron  true 
 */
export function isElectronENV() {
    const flag = window && window.process && window.process.versions && window.process.versions['electron'];
    return flag;
}
export const Tooltip = {
    $uiTooltip: null,
    $uiLabel: null,
    $el: null,
    animationId: null,
    bind($el) {
        if ($el?._onTooltipShow) {
            return;
        }
        $el._onTooltipShow = this.show.bind(this);
        $el._onTooltipHide = this.hide.bind(this);
        $el.addEventListener('mouseover', $el._onTooltipShow);
        $el.addEventListener('mouseleave', $el._onTooltipHide);
    },
    unbind($el) {
        if ($el._onTooltipShow) {
            $el.removeEventListener('mouseover', $el._onTooltipShow);
            $el.removeEventListener('mouseleave', $el._onTooltipHide);
            if ($el === this.$el) {
                $el._onTooltipHide();
            }
            delete $el._onTooltipShow;
            delete $el._onTooltipHide;
        }
    },
    show(event) {
        const value = event.currentTarget.getAttribute('tooltip');
        if (value === null || value.trim() === '') {
            return;
        }
        if (!this.$uiTooltip) {
            this.createGlobalElement();
        }
        if (!this.$uiTooltip.isConnected) {
            document.body.append(this.$uiTooltip);
        }
        this.$el = event.currentTarget;
        this.$uiLabel.setAttribute('value', value.replace(/\n/g, '<br>'));
        const pointer = { x: event.clientX, y: event.clientY };
        window.cancelAnimationFrame(this.animationId);
        this.animationId = window.requestAnimationFrame(() => {
            this.autoPosition(pointer);
        });
    },
    update($el) {
        if ($el && this.$uiLabel) {
            const value = $el.getAttribute('tooltip');
            this.$uiLabel.setAttribute('value', value?.replace(/\n/g, '<br>'));
        }
    },
    autoPosition(pointer) {
        if (!this.$el) {
            return;
        }
        /**
         * 
         * 
         * 
         * 
         */
        const elRect = this.$el.getBoundingClientRect();
        let tipRect = this.$uiTooltip.getBoundingClientRect();
        const maxWidth = window.document.body.clientWidth;
        const maxHeight = window.document.body.clientHeight;
        //  
        let direction = 'top';
        //  
        if (pointer.y < 40) {
            direction = 'bottom';
        }
        //  
        if (maxWidth - elRect.left + elRect.width < 100) {
            direction = 'left';
        }
        //  
        if (maxHeight - pointer.y < 40) {
            direction = 'top';
        }
        let needToResizeWidth = false;
        //    
        if (direction === 'top' || direction === 'bottom') {
            if (tipRect.width > maxWidth) {
                needToResizeWidth = true;
            }
        }
        if (direction === 'left') {
            if (elRect.left < tipRect.width) {
                needToResizeWidth = true;
            }
        }
        if (direction === 'right') {
            if (tipRect.width + elRect.left + elRect.width > maxWidth) {
                needToResizeWidth = true;
            }
        }
        if (needToResizeWidth) {
            this.$uiTooltip.style.width = `${tipRect.width * 0.5}px`;
            tipRect = this.$uiTooltip.getBoundingClientRect();
        }
        // center middle
        let top = pointer.y - tipRect.height / 2;
        let left = pointer.x - tipRect.width / 2;
        let arrowDirection = '';
        let arrowAdjustNumber = '';
        let arrowAdjustDirection = '';
        if (direction === 'top') {
            top = elRect.top - tipRect.height;
            arrowDirection = 'bottom';
            arrowAdjustDirection = 'center';
        }
        if (direction === 'bottom') {
            top = elRect.top + elRect.height;
            arrowDirection = 'top';
            arrowAdjustDirection = 'center';
        }
        if (direction === 'right') {
            left = elRect.left + elRect.width;
            arrowDirection = 'left';
            arrowAdjustDirection = 'middle';
        }
        if (direction === 'left') {
            left = elRect.left - tipRect.width;
            arrowDirection = 'right';
            arrowAdjustDirection = 'middle';
        }
        if (top + tipRect.height > maxHeight) {
            const adjust = maxHeight - top - tipRect.height;
            top += adjust;
            arrowAdjustNumber = (0 - adjust).toString();
        }
        if (left < 0) {
            arrowAdjustNumber = left.toString();
            left = 0;
        }
        if (left + tipRect.width > maxWidth) {
            const adjust = maxWidth - left - tipRect.width;
            left += adjust;
            arrowAdjustNumber = (0 - adjust).toString();
        }
        this.$uiTooltip.style.top = `${top}px`;
        this.$uiTooltip.style.left = `${left}px`;
        this.$uiTooltip.style['animation-name'] = `fadein-from-${arrowDirection}`;
        this.$uiTooltip.setAttribute('arrow', `${arrowDirection} ${arrowAdjustDirection}${arrowAdjustNumber}`);
    },
    hide() {
        if (!this.$uiTooltip || !this.$uiTooltip.isConnected) {
            return;
        }
        this.$uiTooltip.style.top = null;
        this.$uiTooltip.style.left = null;
        this.$uiTooltip.style.width = null;
        this.$uiTooltip.remove();
        this.$el = null;
    },
    createGlobalElement() {
        this.$uiTooltip = document.createElement('ui-tooltip');
        this.$uiTooltip.setAttribute('animation', '');
        this.$uiTooltip.style.position = 'fixed';
        this.$uiTooltip.style.zIndex = '10000';
        this.$uiLabel = document.createElement('ui-label');
        this.$uiLabel.style['line-height'] = '20px';
        this.$uiLabel.style['word-break'] = 'break-word';
        this.$uiTooltip.append(this.$uiLabel);
    },
};
