'use strict';
const fs = require('fs');
import { OperationBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
/**
 * Attribute
 *
 *   disabled: 
 *
 */
export class Tab extends OperationBase {
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${HTML}`;
        this.$slot = this.shadowRoot.querySelector('slot');
        this.$buttons = this.shadowRoot.querySelector('.buttons');
        //  slot  dom 
        this.$slot.addEventListener('slotchange', this._slotChange.bind(this));
        this._update();
    }
    /**
     * ,selectoption
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'value',
        ];
    }
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'value':
                this._update();
                break;
            default:
                break;
        }
    }
    // ///////////////////////////////////////////////////
    get value() {
        const value = parseInt(this.getAttribute('value') || '0');
        return isNaN(value) ? 0 : value;
    }
    set value(val) {
        const value = val !== undefined ? Number(val) : NaN;
        if (isNaN(value)) {
            value === undefined;
        }
        if (val !== undefined) {
            this.setAttribute('value', String(value));
        }
        else {
            this.removeAttribute('value');
        }
        this._update();
    }
    /**
     *  disabled 
     * @param val
     */
    _onDisabledChanged(val) {
        const $list = this.$buttons.querySelectorAll('ui-button');
        $list.forEach((item) => {
            if (val) {
                item.setAttribute('disabled', '');
            }
            else {
                item.removeAttribute('disabled');
            }
        });
    }
    /**
     * 
     */
    connectedCallback() {
        // 
        super.connectedCallback();
        // 
        instanceArray.push(this);
    }
    /**
     * 
     */
    disconnectedCallback() {
        // 
        super.disconnectedCallback();
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    // /////////////////////////////////////////////////////
    // 
    _slotChange() {
        this.$buttons.innerHTML = this.innerHTML;
        let value = this.value;
        const disabled = this.disabled;
        const $list = this.$buttons.querySelectorAll('ui-button');
        $list.forEach((item, index) => {
            // 
            item.removeEventListener('confirm', item.__ui_bind__);
            item.__ui_bind__ = (event) => {
                event.stopPropagation();
                value = this.value = index;
                this.dispatch('change');
                this.dispatch('confirm');
            };
            item.addEventListener('confirm', item.__ui_bind__);
            if (index === value) {
                item.setAttribute('pressed', '');
            }
            else {
                item.removeAttribute('pressed');
            }
            if (disabled) {
                item.setAttribute('disabled', '');
            }
            else {
                item.removeAttribute('disabled');
            }
        });
    }
    _update() {
        const value = this.value;
        const $list = this.$buttons.querySelectorAll('ui-button');
        $list.forEach((item, index) => {
            if (index === value) {
                item.setAttribute('pressed', '');
            }
            else {
                item.removeAttribute('pressed');
            }
        });
    }
}
