'use strict';
import { Base } from '../base';
/**
 * 
 *
 * inline
 *
 * 
 *   input 
 *   change 
 *   confirm 
 *   cancel 
 *
 * 
 *   value
 *   placeholder
 *   readonly
 *   invalid
 *   disabled
 */
export class EditBase extends Base {
    /**
     * 
     */
    constructor() {
        super();
        this.defaultTabIndex = '0';
        // 
        this.addEventListener('focus', this._onFocus);
        this.addEventListener('blur', this._onBlur);
        this.addEventListener('mousedown', this._onMouseDown);
        this.addEventListener('keyup', this._onKeyUp);
        this.addEventListener('keydown', this._onKeyDown);
    }
    get readonly() {
        return this.hasAttribute('readonly');
    }
    set readonly(val) {
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    get invalid() {
        return this.hasAttribute('invalid');
    }
    set invalid(val) {
        if (val) {
            this.setAttribute('invalid', '');
        }
        else {
            this.removeAttribute('invalid');
        }
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', val);
            this.setAttribute('tabindex', '-1');
        }
        else {
            this.removeAttribute('disabled');
            this.setAttribute('tabindex', '0');
        }
    }
    get focused() {
        return this.hasAttribute('focused');
    }
    set focused(val) {
        if (val) {
            this.setAttribute('focused', '');
        }
        else {
            this.removeAttribute('focused');
        }
        this.dispatch('focus-changed');
    }
    get placeholder() {
        return this.getAttribute('placeholder');
    }
    set placeholder(val) {
        val += '';
        this.setAttribute('placeholder', val);
    }
    /**
     * Element  HTML 
     */
    _onConnected() {
        const disabled = this.getAttribute('disabled') !== null;
        const tabindex = this.getAttribute('tabindex') || this.defaultTabIndex;
        //  tabindex
        this.setAttribute('tabindex', disabled ? '-1' : tabindex);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'value',
            'focused',
            'disabled',
            'readonly',
            'invalid',
            'path',
        ];
    }
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'value':
                if (o !== n) {
                    this._onValueChanged(n);
                }
                break;
            case 'disabled':
                this._onDisabledChanged(n !== null);
                break;
            case 'focused':
                if (o !== n) {
                    this._onFocusedChanged(n !== null);
                }
                break;
            case 'readonly':
                this._onReadonlyChanged(n !== null);
                break;
            case 'invalid':
                this._onInvalidChanged(n !== null);
                break;
            default:
                break;
        }
    }
    // ////////////////////
    // 
    /**
     *  value 
     * @param val
     */
    _onValueChanged(val) { }
    /**
     *  disabled 
     * @param val
     */
    _onDisabledChanged(val) { }
    /**
     *  focused 
     * @param val
     */
    _onFocusedChanged(val) { }
    /**
     *  readonly 
     * @param val
     */
    _onReadonlyChanged(val) { }
    /**
     *  invalid 
     * @param val
     */
    _onInvalidChanged(val) { }
    /**
     *  focused
     * @param event
     */
    _onFocus(event) {
        if (this.disabled) {
            return;
        }
        this.focused = true;
    }
    /**
     *  focused
     *
     * @param event
     */
    _onBlur(event) {
        this.removeAttribute('focused');
    }
    /**
     *
     * @param { string } val placeholder
     * */
    _onPlaceholderChanged(val) { }
    /**
     * 
     *  disabled  readonly 
     * @param event
     */
    _onMouseDown(event) { }
    /**
     * 
     * @param event
     */
    _onKeyDown(event) { }
    /**
     * 
     * @param event
     */
    _onKeyUp(event) { }
}
