import { Hermite } from "./hermite";
/**
 * 
 */
export function getCurveInstance(type, canvas, grid, config) {
    let curveInstance;
    switch (type) {
        case 'hermit':
            curveInstance = new Hermite(canvas, grid, config);
            break;
        default:
            break;
    }
    return curveInstance;
}
/**
 * 
 */
export function getCurveClass(type) {
    let curveClass;
    switch (type) {
        case 'hermit':
            curveClass = Hermite;
            break;
        default:
            break;
    }
    return curveClass;
}
