'use strict';
const fs = require('fs');
import { EditBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
import { mathUtils } from '../../../utils/utils';
import vm from 'vm';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
// 
const defaultConfig = {
    step: 0.001,
    preci: 6,
    errorValue: 0, //  NaN 
};
/**
 * @description Attribute
 * @description disabled: 
 * @description readonly: 
 * @description invalid: 
 * @description value: 
 * @description unit: 
 * @description step: 
 * @description max: 
 * @description min: 
 */
export class NumInput extends EditBase {
    constructor() {
        super();
        this.ignoreInputFocus = false;
        this._shiftStepNum = 0;
        this._staging = null;
        this._isMouseDown = false;
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        //  tab 
        this.$input = this.shadowRoot.querySelector('input');
        this.$up = this.shadowRoot.querySelector('.up');
        this.$down = this.shadowRoot.querySelector('.down');
        this.$label = this.shadowRoot.querySelector('.label span');
        this.$content = this.shadowRoot.querySelector('.content');
        this.$unit = this.shadowRoot.querySelector('.unit span');
        this.$input.$root = this.$up.$root = this.$down.$root = this.$unit.$root = this.$label.$root = this.$content.$root = this;
        //  value  esc 
        this._staging = null;
        this.addEventListener('focus', this._onFocusChange);
        this.addEventListener('blur', this._onBlurChange);
        // 
        this.$input.addEventListener('focus', this._onInputFocus);
        this.$input.addEventListener('blur', this._onInputBlur);
        this.$input.addEventListener('input', this._onInputChange);
        this.$input.addEventListener('wheel', this._onInputWheel);
        this.$unit.addEventListener('mousedown', this._onUnitClick);
        // 
        this.$up.addEventListener('mousedown', this._onUpMouseDown);
        this.$down.addEventListener('mousedown', this._onDownMouseDown);
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // , css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    /**
    * @description  tooltip step 
    **/
    static _changeToolTipHandle(step) {
        defaultConfig.step = step;
        instanceArray.forEach((node) => {
            node._setTooltip();
        });
    }
    /**
     * @description  defaultConfig step 
     **/
    static _setStep(step) {
        defaultConfig.step = step;
    }
    get value() {
        const inputValue = this.$input.value.trim();
        if (!window.isFinite(inputValue)) {
            const value = this.getAttribute('value');
            const number = Number(value);
            return !isNaN(number) ? number : defaultConfig.errorValue;
        }
        if (inputValue === '' && this._staging !== null) {
            return Number(this._staging);
        }
        return Number(inputValue);
    }
    set value(val) {
        if (this.$input.value == val) {
            return;
        }
        val = this.calcValue(val);
        this.setAttribute('value', String(val));
    }
    get label() {
        return this.getAttribute('label') || '';
    }
    set label(val) {
        if (val) {
            this.setAttribute('label', val);
        }
        else {
            this.removeAttribute('label');
        }
    }
    get unit() {
        return this.getAttribute('unit') || '';
    }
    set unit(val) {
        val += '';
        this.setAttribute('unit', val);
    }
    get preci() {
        const preci = this.getAttribute('preci');
        if (isNaN(Number(preci)) || [null, '', undefined].includes(preci)) {
            return defaultConfig.preci;
        }
        return Number(preci);
    }
    set preci(val) {
        val -= 0;
        val = mathUtils.clamp(val, 0, 20); // toFixed  0-20 
        this.setAttribute('preci', String(val));
    }
    get step() {
        let step = this.getAttribute('step');
        if (isNaN(Number(step)) || [null, '', undefined].includes(step)) {
            step = defaultConfig.step;
        }
        const rang = mathUtils.comPreci(step);
        // :  0.5() 0  1
        if (rang > this.preci) {
            step = Math.pow(10, 0 - this.preci);
        }
        return Number(step);
    }
    set step(val) {
        val -= 0;
        this.setAttribute('step', String(val));
    }
    get max() {
        let max = this.getAttribute('max');
        if (max === null) {
            return null;
        }
        max = Number(max);
        if (typeof max !== 'number' || isNaN(max)) {
            return null;
        }
        return max;
    }
    set max(val) {
        if (val === null) {
            this.removeAttribute('max');
        }
        else {
            const oldVal = this.getAttribute('max');
            val.toString() !== oldVal && this.setAttribute('max', String(val));
        }
    }
    get min() {
        let min = this.getAttribute('min');
        if (min === null) {
            return null;
        }
        min = Number(min);
        if (typeof min !== 'number' || isNaN(min)) {
            return null;
        }
        return min;
    }
    set min(val) {
        if (val === null) {
            this.removeAttribute('min');
        }
        else {
            const oldVal = this.getAttribute('min');
            val !== oldVal && this.setAttribute('min', String(val));
        }
    }
    get state() {
        return this.getAttribute('state') || '';
    }
    set state(str) {
        this.setAttribute('state', str);
    }
    /**
     * @description 
     */
    connectedCallback() {
        super.connectedCallback();
        this.$content.addEventListener('mousedown', this._OnContentCursorResizeMouseDown);
        // 
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        this._setTooltip();
    }
    /**
     * @description 
     */
    disconnectedCallback() {
        super.disconnectedCallback();
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'placeholder',
            'label',
            'unit',
            'preci',
            'step',
            'max',
            'min',
            'label',
        ];
    }
    /**
     * @description 
     * @param attr 
     * @param oldVal 
     * @param newVal 
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'label':
                this._onLabelChanged(newVal);
                break;
            case 'unit':
                this._onUnitChanged(newVal);
                break;
            case 'preci':
                this._onPreciChanged();
                break;
            case 'step':
                this._onStepChanged();
                break;
            case 'max':
                this._onMaxChanged(newVal);
                break;
            case 'min':
                this._onMinChanged(newVal);
                break;
            case 'readonly':
                this._onReadonlyChanged(newVal);
                break;
            case 'focused':
                this._onFocusedChanged(newVal);
                break;
            default:
                break;
        }
    }
    _onFocusChange() {
        if (!this._isMouseDown) {
            this.focused = true;
            this.$input.focus();
        }
    }
    _onBlurChange() {
        this._isMouseDown = false;
    }
    /**
     * @description  readonly 
     */
    _onReadonlyChanged(val) {
        if (val !== null) {
            this.$input.setAttribute('readonly', '');
        }
        else {
            this.$input.removeAttribute('readonly');
        }
    }
    /**
     * @description  focused 
     */
    _onFocusedChanged(val) {
        if (val !== null) {
            this.$content.style.cursor = 'text';
            this.$input.style.pointerEvents = 'auto';
            this.$content.removeAttribute('cursorresize');
        }
        else {
            this.$content.style.cursor = 'ew-resize';
            this.$input.style.pointerEvents = 'none';
            this.$content.setAttribute('cursorresize', '');
        }
    }
    /**
     * @description  label 
     * @param val
     */
    _onLabelChanged(val) {
        this.$label.innerHTML = val ?? '';
        this.$label.hidden = !val;
    }
    /**
     * @description  label 
     * @param val
     */
    _onUnitChanged(val) {
        this.$unit.innerHTML = val ?? '';
    }
    /**
     * @description  preci 
     */
    _onPreciChanged() {
        this._setTooltip();
    }
    /**
     * @description  step 
     */
    _onStepChanged() {
        this._setTooltip();
    }
    /**
     * @description  max 
     * @param val
     */
    _onMaxChanged(val) {
        this.max = val;
        const valMax = this.getAttribute('value');
        if (valMax === null) {
            return;
        }
        const newVal = mathUtils.clamp(this.value, this.min, this.max);
        if (this.value !== newVal) {
            this.value = newVal;
            this.dispatch('confirm');
        }
        this._setTooltip();
    }
    /**
     * @description  min 
     * @param val
     */
    _onMinChanged(val) {
        this.min = val;
        const valMin = this.getAttribute('value');
        if (valMin === null) {
            return;
        }
        const newVal2 = mathUtils.clamp(this.value, this.min, this.max);
        if (this.value !== newVal2) {
            this.value = newVal2;
            this.dispatch('confirm');
        }
        this._setTooltip();
    }
    /**
     * @description  invalid 
     * @param val
     */
    _onInvalidChanged(val) {
        this.$input.value = !val ? this.value : '-';
    }
    /**
     * @description  value 
     * @param val
     */
    _onValueChanged(val) {
        //  value  input.value
        if (this.invalid) {
            return;
        }
        //  input  value 
        if (this._staging !== null) {
            return;
        }
        this.$input.value = this.calcValue(val);
        this._checkState();
    }
    /**
     * @description  disabled 
     */
    _onDisabledChanged(val) {
        this.$input.disabled = val;
    }
    // ////////////////////
    // 
    _setTooltip({ min = this.min, max = this.max, preci = this.preci, step = this.step } = {}) {
        const tooltip = [];
        if (min !== null) {
            tooltip.push(`Min: ${min}`);
        }
        if (max !== null) {
            tooltip.push(`Max: ${max}`);
        }
        if (preci !== null) {
            tooltip.push(`Decimals: ${preci}`);
        }
        if (step !== null) {
            tooltip.push(`Step: ${step}`);
        }
        this.setAttribute('tooltip', tooltip.join('\n'));
    }
    /**
     * @description input 
     */
    _onInputFocus() {
        const $elem = this;
        // 
        if ($elem.disabled) {
            return;
        }
        // 
        $elem.$root._staging = $elem.$root.value;
        // 
        $elem.select();
    }
    /**
     * @description input 
     */
    _onInputBlur() {
        const $elem = this;
        if ($elem.$root._staging !== $elem.value) {
            $elem.value = $elem.$root.calcValue($elem.value);
            $elem.setAttribute('value', $elem.value);
            $elem.$root._confirm(true);
        }
        else {
            $elem.value = $elem.$root.value;
        }
        // 
        $elem.$root._staging = null;
    }
    /**
     * @description input 
     */
    _onInputChange(e) {
        const $elem = this;
        if (isNaN(this.value)) {
            $elem.$root.setAttribute('value', defaultConfig.errorValue.toString());
            return;
        }
        $elem.$root.setAttribute('value', String(this.value));
        //  change 
        $elem.$root.dispatch('change');
    }
    /**
     * @description 
     * @param event
     */
    _onInputWheel(event) {
        const $elem = this;
        if (!$elem.matches(':focus')) {
            return;
        }
        event.stopPropagation();
        event.preventDefault();
        const step = $elem.$root._shiftStepNum;
        //  mac  mini shift + 
        const deltaY = Math.sign(event.deltaY);
        const deltaX = Math.sign(event.deltaX);
        const delta = event.deltaY === 0 ? deltaX : deltaY;
        // -1 , 1 
        if (delta === -1) {
            $elem.$root.stepUp(step);
        }
        else {
            $elem.$root.stepDown(step);
        }
    }
    /**
     * @description 
     * @param event
     */
    _onUnitClick(event) {
        const $elem = this;
        event.stopPropagation();
        event.preventDefault();
        $elem.$root._onInputBlur.call($elem);
        //  change 
        $elem.$root.dispatch('unit-click');
    }
    /**
     * @description input 
     * @param event
     */
    _onKeyDown(event) {
        // 
        if (this.disabled || this.readonly) {
            return;
        }
        switch (event.keyCode) {
            case 13: // 
                this._keydownEnter();
                break;
            case 16: // shift
                this._shiftStepNum = this.step * 10;
                this._setTooltip({ step: this._shiftStepNum });
                break;
            case 27: // esc
                this._cancel();
                break;
            case 38: // up
                event.preventDefault();
                this.stepUp(this._shiftStepNum);
                break;
            case 40: // down
                event.preventDefault();
                this.stepDown(this._shiftStepNum);
                break;
        }
    }
    /**
     * @description 
     * @param event
     */
    _onKeyUp(event) {
        if (this.disabled || this.readonly) {
            return;
        }
        switch (event.keyCode) {
            case 16: // shift
                this._shiftStepNum = 0;
                this._setTooltip();
                break;
            default:
                break;
        }
    }
    // ////////////////////////
    _keydownEnter() {
        if (this._staging !== this.$input.value) {
            this.$input.value = this.calcValue(this.$input.value);
            this.$input.setAttribute('value', this.$input.value);
            this._confirm();
        }
    }
    _confirm(ignore) {
        if (this.invalid) {
            return;
        }
        const value = this.value;
        const inputFocused = this._staging !== null;
        //  confirm 
        if (this._staging !== null && this._staging !== value) {
            this._staging = value;
            this.dispatch('change');
            this.dispatch('confirm');
        }
        // 
        if (!this.invalid) {
            this.$input.value = value;
        }
        if (!ignore) {
            if (inputFocused) {
                this.focus();
            }
            else {
                this.$input.focus();
            }
        }
    }
    _cancel() {
        const inputFocused = this._staging !== null;
        // 
        if (this._staging !== null && this._staging !== this.value) {
            this.value = this.$input.value = this._staging;
            this.dispatch('change');
            this.dispatch('cancel');
            this.dispatch('confirm');
        }
        // 
        if (!this.invalid) {
            this.$input.value = this.value;
        }
        if (inputFocused) {
            this.focus();
        }
    }
    /**
     * @description up 
     * @param event
     */
    _onUpMouseDown(event) {
        const $elem = this;
        event.stopPropagation();
        $elem.$root._onUpDownMouseDown($elem, 'stepUp', event);
    }
    /**
     * @description down 
     * @param event
     */
    _onDownMouseDown(event) {
        const $elem = this;
        event.stopPropagation();
        $elem.$root._onUpDownMouseDown($elem, 'stepDown', event);
    }
    /**
     * @description 
     * @param $elem
     * @param method
     */
    _onUpDownMouseDown($elem, method, event) {
        const $root = this;
        const step = event.shiftKey ? this.step * 10 : undefined;
        // 
        if ($root.disabled || $root.readonly) {
            return;
        }
        $elem.setAttribute('pressed', '');
        $root[method](step);
        clearTimeout($root._timer);
        clearInterval($root._timer);
        //  300 ms 
        $root._timer = setTimeout(() => {
            $root._timer = setInterval(() => {
                $root[method](step);
            }, 50);
        }, 300);
        $root._bindMouseUp = $root._onUpDownMouseUp.bind($root);
        document.addEventListener('mouseup', $root._bindMouseUp, true);
    }
    /**
     * @description 
     */
    _onUpDownMouseUp() {
        const $root = this;
        document.removeEventListener('mouseup', $root._bindMouseUp, true);
        if ($root._timer) {
            clearTimeout($root._timer);
            clearInterval($root._timer);
            delete $root._timer;
        }
        $root.$up.removeAttribute('pressed');
        $root.$down.removeAttribute('pressed');
        $root.dispatch('confirm');
    }
    _onFocus(event) {
        if (this.disabled || this.$content.getAttribute('cursorresize') !== null) {
            return;
        }
        this.focused = true;
        this.$input.focus();
    }
    /**
     * @description  ew-resize 
     * @param event
     */
    _OnContentCursorResizeMouseDown(event) {
        const $elem = this;
        // disabled / readonly 
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        $elem.$root._isMouseDown = true;
        if ($elem.$root.$content.hasAttribute('cursorresize')) {
            $elem._clientX = event.clientX;
            $elem._value = $elem.$root.value;
            $elem._OnContentCursorResizeMouseMove = $elem.$root._OnContentCursorResizeMouseMove.bind($elem);
            $elem._OnContentCursorResizeMouseUp = $elem.$root._OnContentCursorResizeMouseUp.bind($elem);
            document.addEventListener('mousemove', $elem._OnContentCursorResizeMouseMove, true);
            document.addEventListener('mouseup', $elem._OnContentCursorResizeMouseUp, true);
        }
    }
    _OnContentCursorResizeMouseMove(event) {
        const $elem = this;
        const distance = event.clientX - $elem._clientX;
        $elem.$root.step.toFixed($elem.$root.preci);
        const step = event.shiftKey ? $elem.$root.step * 10 : $elem.$root.step;
        const change = Number(distance * step);
        if (change) {
            $elem.$root.ignoreInputFocus = true;
        }
        $elem.$root.value = $elem._value + change;
        $elem.$root.$input.value = $elem.$root.value;
        $elem.$root.dispatch('change');
    }
    _OnContentCursorResizeMouseUp() {
        const $elem = this;
        document.removeEventListener('mousemove', $elem._OnContentCursorResizeMouseMove, true);
        document.removeEventListener('mouseup', $elem._OnContentCursorResizeMouseUp, true);
        // 
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        if (!$elem.$root.ignoreInputFocus) {
            $elem.$root.$content.style.cursor = 'text';
            $elem.$root.$content.removeAttribute('cursorresize');
            $elem.$root._onFocus();
        }
        if ($elem.$root.ignoreInputFocus) {
            $elem.$root.dispatch('confirm');
        }
        $elem.$root.ignoreInputFocus = false;
    }
    // 
    _checkState() {
        const $elem = this;
        window.cancelAnimationFrame($elem.animationId);
        $elem.animationId = window.requestAnimationFrame(() => {
            const { value, max, min } = this;
            let state = '';
            if (typeof max === 'number' && value > max) {
                state = 'error-range';
            }
            if (typeof min === 'number' && value < min) {
                state = 'error-range';
            }
            this.state = state;
        });
    }
    // ///////////////////////////////////////
    //   stepUpstepDown
    calcValue(inputValue) {
        if (!inputValue) {
            const number = Number(inputValue);
            if (isNaN(number) ||
                inputValue === null ||
                inputValue === true ||
                inputValue === false) {
                return defaultConfig.errorValue;
            }
        }
        //  1 + 2
        const sandbox = { value: defaultConfig.errorValue };
        try {
            vm.runInNewContext(`value = ${inputValue};`, sandbox);
        }
        catch (e) { }
        let value = sandbox.value;
        if (typeof this.preci === 'number') {
            const rang = mathUtils.comPreci(value);
            if (rang > this.preci) {
                value = Number(value.toFixed(this.preci));
            }
        }
        if (typeof this.max === 'number') {
            value = Math.min(this.max, value);
        }
        if (typeof this.min === 'number') {
            value = Math.max(this.min, value);
        }
        return value;
    }
    /**
     * @description step
     * @param {*} step
     */
    stepUp(step) {
        // 
        if (this.disabled || this.readonly) {
            return;
        }
        if (this._staging === null) {
            this.$input.focus();
        }
        // this.focused = true;
        const inscrease = step || this.step;
        const value = mathUtils.accAdd(this.value, inscrease);
        if (this.max !== null && value > this.max) {
            if (this._timer) {
                // 
                clearTimeout(this._timer);
                clearInterval(this._timer);
            }
            return;
        }
        if (this.invalid) {
            this.invalid = false;
        }
        this.$input.value = this.calcValue(value);
        this.setAttribute('value', this.$input.value);
        this.dispatch('change');
    }
    /**
     * @description   step 
     * @param step   5  5 -5  5
     */
    stepDown(step) {
        // 
        if (this.disabled || this.readonly) {
            return;
        }
        if (this._staging === null) {
            this.$input.focus();
        }
        const decrease = step || this.step;
        const value = mathUtils.accSub(this.value, decrease);
        if (this.min !== null && value < this.min) {
            if (this._timer) {
                // 
                clearTimeout(this._timer);
                clearInterval(this._timer);
            }
            return;
        }
        if (this.invalid) {
            this.invalid = false;
        }
        this.$input.value = this.calcValue(value);
        this.setAttribute('value', this.$input.value);
        this.dispatch('change');
    }
}
