'use strict';
const fs = require('fs');
import { EditBase } from '../base';
const { nodeList } = require('../../../utils/utils');
import { translate } from '../../../utils/utils';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
/**
 *   Attribute
 *
 *   disabled: 
 *   readonly: 
 *   invalid: 
 *
 *   value: 
 */
export class Select extends EditBase {
    // //////////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$slot = this.shadowRoot.querySelector('slot');
        this.$placeholder = this.shadowRoot.querySelector('#placeholder');
        //  tab 
        this.$child = this.$select = this.shadowRoot.querySelector('#content');
        this.$slot.$root = this.$placeholder.$root = this.$select.$root = this;
        // 
        this.$select.addEventListener('change', this._selectChange);
        //  slot  dom 
        this.$slot.addEventListener('slotchange', this._slotChange);
        this.$slot.addEventListener('DOMSubtreeModified', this._slotTextChange);
    }
    /**
     * ,selectoption
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        return this._value;
    }
    set value(val) {
        if (val !== null && val !== undefined) {
            this.setAttribute('value', val);
        }
        else {
            this.removeAttribute('value');
        }
    }
    get innerHTML() {
        return super.innerHTML;
    }
    set innerHTML(html) {
        // 
        html = html.trim();
        if (super.innerHTML !== html) {
            super.innerHTML = html;
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        nodeList.push(this);
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        const i = nodeList.indexOf(this);
        instanceArray.splice(index, 1);
        nodeList.splice(i, 1);
    }
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'placeholder',
        ];
    }
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'placeholder':
                this._onPlaceholderChanged(newVal);
                break;
            default:
                break;
        }
    }
    // //////////////////////////
    /**
     *  focused 
     * @param val
     */
    _onFocusedChanged(val) {
        if (val) {
            this.$select.focus();
        }
    }
    /**
     *  value 
     * @param val
     */
    _onValueChanged(val) {
        // 
        if (this.invalid) {
            this.invalid = false;
        }
        if (val === null) {
            this._value = '';
        }
        else {
            this._value = val;
        }
        this.$select.value = this._value;
        this._placeholderDisplay();
    }
    _placeholderDisplay() {
        //  placeholder
        if (this.value && this.value !== 'null' && this.value !== 'undifined') {
            this.$placeholder.style.display = 'none';
        }
        else {
            this.$placeholder.style.display = 'block';
        }
    }
    /**
     * placeholder 
     * @param {string} val
     */
    _onPlaceholderChanged(val) {
        this.$placeholder.innerHTML = translate(val) ?? '';
    }
    /**
     * @description 
     **/
    static _updateTranslate() {
        for (const el of instanceArray) {
            el._onPlaceholderChanged(el.getAttribute('placeholder'));
        }
    }
    /**
     *  readonly 
     * @param val
     */
    _onReadonlyChanged(val) {
        if (val) {
            this.$select.setAttribute('readonly', '');
        }
        else {
            this.$select.removeAttribute('readonly');
        }
    }
    /**
     *  invalid 
     * @param val
     */
    _onInvalidChanged(val) {
        if (val) {
            process.nextTick(() => {
                this.$select.value = '';
            });
        }
        else {
            process.nextTick(() => {
                this.$select.value = this.value;
            });
        }
    }
    /**
     *  select  change 
     */
    _selectChange() {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            $elem.value = $elem.$root.getAttribute('value');
            return;
        }
        $elem.$root.value = $elem.value;
        $elem.$root.dispatch('change');
        $elem.$root.dispatch('confirm');
    }
    /**
     *  disabled 
     */
    _onDisabledChanged(val) {
        this.$select.disabled = val;
    }
    /**
     * @description slot  option 
     */
    _resolveSlotOption($elem, node) {
        if (node.tagName === 'OPTION' || node.tagName === 'OPTGROUP') {
            const optionNode = document.importNode(node, true);
            if (optionNode.tagName === 'OPTGROUP') {
                const childrenOptions = optionNode.querySelectorAll('option');
                for (const option of childrenOptions) {
                    option.innerText = translate(option.innerText);
                }
            }
            else {
                optionNode.innerText = translate(optionNode.innerText);
            }
            $elem.$root.$select.appendChild(optionNode);
        }
    }
    /**
     * @description slotchangeselectoption
     *
     */
    _slotChange() {
        const $elem = this;
        // select
        $elem.$root.$select.innerHTML = '';
        // slot
        const nodes = $elem.assignedNodes();
        // select
        for (const node of nodes) {
            $elem.$root._resolveSlotOption($elem, node);
        }
        $elem.$root.$select.value = $elem.$root.value;
    }
    /**
     * @description  slot 
     */
    _slotTextChange() {
        const $elem = this;
        // select
        $elem.$root.$select.innerHTML = '';
        for (const node of $elem.$root.children) {
            $elem.$root._resolveSlotOption($elem, node);
        }
        $elem.$root.$select.value = $elem.$root.value;
    }
}
