'use strict';
const fs = require('fs');
const { Menu, MenuItem, getCurrentWindow } = require('@electron/remote');
import { EditBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const { nodeList } = require('../../../utils/utils');
const instanceArray = [];
let customStyle = '';
export class Bit extends EditBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.maskList = [];
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$slot = this.shadowRoot.querySelector('#slot');
        this.$placeholder = this.shadowRoot.querySelector('#placeholder');
        //  tab 
        this.$child = this.$content = this.shadowRoot.querySelector('#content');
        this.$slot.$root = this.$placeholder.$root = this.$content.$root = this;
        //  slot  dom 
        this.$slot.addEventListener('slotchange', this._slotChange);
        this.$slot.addEventListener('DOMSubtreeModified', this._slotTextChange);
    }
    /**
     * ,selectoption
     *
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        const result = this.$content.getAttribute('value');
        if (!result) {
            return 0;
        }
        return result - 0;
    }
    set value(val) {
        this.$content.innerHTML = val;
        this.$content.setAttribute('value', val);
    }
    get innerHTML() {
        return super.innerHTML;
    }
    set innerHTML(html) {
        // 
        html = html.trim();
        if (super.innerHTML !== html) {
            super.innerHTML = html;
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        this.$content.addEventListener('click', this._selectClick);
        // 
        nodeList.push(this);
        instanceArray.push(this);
        //  tabindex
        const tabindex = this.getAttribute('tabindex') || '0';
        this.$content.setAttribute('tabindex', this.disabled ? '-1' : tabindex);
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        this.removeEventListener('click', this._selectClick);
        // 
        const index = instanceArray.indexOf(this);
        const i = nodeList.indexOf(this);
        instanceArray.splice(index, 1);
        nodeList.splice(i, 1);
    }
    // ////////////////////
    // 
    /**
     *  value 
     */
    _onValueChanged(val) {
        if (this.invalid) {
            this.invalid = false;
        }
        this.value = Number(val);
        // placeholder
        if (this.value && this.value !== 0) {
            this.$placeholder.style.display = 'none';
        }
        else {
            this.$placeholder.style.display = 'block';
        }
    }
    /**
     *  invalid 
     */
    _onInvalidChanged(val) {
        if (val) {
            process.nextTick(() => {
                this.$content.value = '';
            });
        }
    }
    /**
     *  placeholder 
     */
    _onPlaceholder(val) {
        this.$placeholder.innerHTML = val;
    }
    /**
     *  focused 
     */
    _onFocusedChanged(val) {
        if (val) {
            this.$content.focus();
        }
        else {
            this.$content.blur();
        }
    }
    /**
     *  disabled 
     */
    _onDisabledChanged(val) {
        this.$content.setAttribute('tabindex', val ? '-1' : this.getAttribute('tabindex') || '0');
    }
    /**
     * 
     */
    _selectClick(event) {
        const menus = [];
        const $elem = this;
        const nothingValue = 0;
        const everythingValue = 0xffffffff;
        menus.push({
            label: 'Everything',
            checked: $elem.$root.value === everythingValue.toString(),
            type: 'checkbox',
            click() {
                $elem.$root._selectChange(everythingValue);
            },
        });
        menus.push({
            label: 'Nothing',
            checked: $elem.$root.value === 0,
            type: 'checkbox',
            click() {
                $elem.$root._selectChange(0);
            },
        });
        menus.push({ type: 'separator' });
        /**
         * 
         *  everything
         */
        let checkedAllValue = 0;
        $elem.$root.maskList.forEach((item) => {
            if (item.value === nothingValue || item.value === everythingValue) {
                return;
            }
            checkedAllValue ^= item.value;
            menus.push({
                label: item.name,
                checked: !!($elem.$root.value & item.value),
                type: 'checkbox',
                click() {
                    let rootValue = Number($elem.$root.value);
                    rootValue ^= item.value;
                    // 
                    let value = 0;
                    $elem.$root.maskList.forEach((item) => {
                        if (item.value === 0 || item.value === everythingValue) {
                            return;
                        }
                        // 
                        if (rootValue & item.value) {
                            value ^= item.value;
                        }
                    });
                    // 
                    if (value !== checkedAllValue) {
                        $elem.$root._selectChange(value);
                    }
                    else {
                        $elem.$root._selectChange(everythingValue);
                    }
                },
            });
        });
        // TODO: electronmenuselectbit
        const bitMenu = new Menu();
        menus.map((item) => {
            return bitMenu.append(new MenuItem(item));
        });
        bitMenu.popup({ window: getCurrentWindow(), x: event.pageX, y: event.pageY });
    }
    /**
     *  select  change 
     */
    _selectChange(val) {
        if (this.disabled || this.readonly) {
            return;
        }
        this.value = val;
        this.dispatch('change');
        this.dispatch('confirm');
    }
    /**
     * slotchangeselectoption
     */
    _slotChange() {
        const $elem = this;
        $elem.$root.maskList = [];
        // slot
        const nodes = $elem.assignedNodes();
        // select
        for (const node of nodes) {
            if (node.tagName !== 'OPTION' && node.tagName !== 'OPTGROUP') {
                continue;
            }
            $elem.$root.maskList.push({
                name: node.innerHTML,
                value: Number(node.value),
            });
        }
    }
    /**
     *  slot 
     */
    _slotTextChange() {
        const $elem = this;
        $elem.$root._slotChange.call($elem);
    }
    /**
     * 
     * @param event
     */
    _onKeyDown(event) {
        if (this.disabled || this.readonly) {
            return;
        }
    }
    /**
     * 
     * @param event
     */
    _onKeyUp(event) {
        if (this.disabled || this.readonly) {
            return;
        }
    }
}
