export default ":host {\n  user-select: none;\n  outline: none;\n  display: inline-block;\n  box-sizing: border-box;\n  font-size: inherit;\n  line-height: inherit;\n  font-family: inherit;\n  border: calc(var(--size-normal-border) * 1px) solid transparent;\n  border-radius: calc(var(--size-normal-radius) * 1px);\n}\n:host([disabled]) {\n  cursor: not-allowed;\n  opacity: 0.55;\n  pointer-events: none;\n}\n:host([readonly]) {\n  pointer-events: none;\n}\n:host([hidden]) {\n  display: none;\n}\n:host {\n  border: none;\n}\n:host #radio {\n  display: flex;\n  align-items: center;\n}\n.radio-box {\n  position: relative;\n  display: flex;\n  width: 12px;\n  height: 12px;\n  margin-right: 4px;\n  border-radius: 50%;\n  box-shadow: 0 0 0 1px var(--color-normal-border-normal);\n  background-color: #141414;\n}\n.radio-box[empty] {\n  margin-right: 0;\n}\n:host([checked]) .radio-box {\n  box-shadow: 0 0 0 1px #424242;\n}\n:host([checked]) .radio-box:before {\n  content: '';\n  display: inline-block;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  width: 5px;\n  height: 5px;\n  border-radius: 50%;\n  background-color: var(--color-focus-fill-emphasis);\n  transform-origin: center;\n  transform: translate(-50%, -50%);\n}\n:host([disabled]) .radio-box {\n  box-shadow: 0 0 0 1px #424242;\n}\n:host([focused]) .radio-box {\n  box-shadow: 0 0 0 1px var(--color-focus-fill-emphasis);\n}\n:host([focused][checked]) .radio-box {\n  box-shadow: 0 0 0 1px var(--color-focus-fill-emphasis);\n}\n:host(:hover) .radio-box {\n  box-shadow: 0 0 0 1px var(--color-focus-fill-emphasis);\n}\n";