'use strict';
const fs = require('fs');
const { shell } = require('electron');
import { FunctionBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
let linkHandle;
export class Link extends FunctionBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$slot = this.shadowRoot.querySelector('slot');
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        const val = this.getAttribute('value');
        if (val) {
            return val.trim();
        }
        const text = this.innerHTML;
        return text.trim();
    }
    set value(val) {
        this.setAttribute('value', val);
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    get ctrl() {
        return this.hasAttribute('ctrl');
    }
    /**
     *  link 
     * falsetrue
     * @param func
     */
    static setLinkHandle(func) {
        if (typeof func !== 'function') {
            return;
        }
        linkHandle = func;
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        // 
        this.addEventListener('click', this._onClick);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.removeEventListener('click', this._onClick);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['value', 'readonly', 'disabled', 'ctrl'];
    }
    /**
     * attribute 
     */
    attributeChangedCallback(attr, o, n) {
        switch (attr) {
            case 'disabled':
                if (n === 'false') {
                    this.disabled = false;
                }
                break;
            case 'value':
                this.$slot.innerHTML = n;
                break;
        }
    }
    // ////////////////////
    /**
     * ctrl + 
     * 
     */
    async _onClick(event) {
        const val = this.value;
        if (this.disabled || !val || (this.ctrl && !event.ctrlKey && !event.metaKey)) {
            return;
        }
        // 
        if (linkHandle) {
            const result = await linkHandle(val, this.getAttribute('type'));
            //  true,false
            if (result) {
                return true;
            }
        }
        const htmlReg = /(http:\/\/|https:\/\/)((\w|=|\?|\.|\/|&|-)+)/g;
        // 
        if (htmlReg.test(val)) {
            if (shell && shell.openExternal) {
                shell.openExternal(val);
            }
            else if (window && window.open) {
                window.open(val, '_blank');
            }
            return;
        }
        // 
        if (fs.existsSync(this.value) && shell && shell.showItemInFolder) {
            shell.showItemInFolder(this.value);
        }
    }
}
