'use strict';
import { DisplayBase } from '../base';
const fs = require('fs');
const Hljs = require('highlight.js');
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
export class Code extends DisplayBase {
    // ////////////////////
    /**
     * @description 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$pre = this.shadowRoot.querySelector('pre');
        this.$code = this.shadowRoot.querySelector('code');
        this.$slot = this.shadowRoot.querySelector('slot');
        this.$slot.$root = this;
        this.$slot.addEventListener('slotchange', this._slotTextChange);
        new MutationObserver(this.render.bind(this)).observe(this, { characterData: true, subtree: true });
    }
    /**
     * 
     * @param {filePath} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get language() {
        return this.getAttribute('language') || 'glsl';
    }
    set language(val) {
        if (!val) {
            this.removeAttribute('language');
        }
        else {
            this.setAttribute('language', val);
        }
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    get readonly() {
        return this.hasAttribute('readonly');
    }
    set readonly(val) {
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    /**
     * @description  attribute 
     */
    static get observedAttributes() {
        return [
            'language',
            'disabled',
        ];
    }
    /**
     * @description attribute 
     * @param attr 
     * @param oldVal 
     * @param newVal 
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        switch (attr) {
            case 'language':
                this.render();
                break;
        }
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        // 
        if (this.shadowRoot) {
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        this.disabled = this.hasAttribute('disabled');
        this.readonly = this.hasAttribute('readonly');
        this.render();
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    render() {
        const template = this.innerHTML;
        if (/<[^>]+>/.test(template)) {
            this.innerHTML = template.replace(/</g, '&lt;').replace(/>/g, '&gt;');
        }
        let html = this.textContent;
        // 
        const results = html.match(/( +)[^ ]/);
        if (results && results[1]) {
            html = html.replace(new RegExp('^' + results[1], 'gm'), '');
        }
        html = html.trim();
        const result = Hljs.highlight(this.language, html);
        this.$code.innerHTML = result.value;
    }
    _slotTextChange() {
        const $elem = this;
        $elem.$root.render();
    }
}
