//  remote
const { dialog } = require('@electron/remote');
const messageBoxDefaultConfig = {
    type: 'none',
    buttons: [''],
    noLink: true,
};
const openDialogDefaultConfig = {
    properties: ['openFile'],
};
// ///////////////////////////////////////
// TODO:  electron  web
const Dialog = {
    info: (message, config) => { },
    warn: (message, config) => { },
    error: (message, config) => { },
    select: (config) => { },
    save: (config) => { },
};
// ///////////////////////////////////////
Dialog.info = (message, config) => {
    return showMessageBox('info', message, config);
};
Dialog.warn = (message, config) => {
    return showMessageBox('warning', message, config);
};
Dialog.error = (message, config) => {
    return showMessageBox('error', message, config);
};
const showMessageBox = (type, message, options) => {
    options = Object.assign({}, messageBoxDefaultConfig, options);
    if (!options.title) {
        options.title = type;
    }
    options.message = message;
    options.type = type;
    return dialog.showMessageBox(options);
};
// ///////////////////////////////////////
Dialog.select = (config) => {
    const options = Object.assign({}, openDialogDefaultConfig, config);
    if (options.path) {
        options.defaultPath = options.path;
        delete options.path;
    }
    if (options.type === 'directory') {
        options.properties = ['openDirectory'];
        delete options.type;
    }
    if (options.multi) {
        options.properties.push('multiSelections');
        delete options.multi;
    }
    return dialog.showOpenDialog(options);
};
Dialog.save = (config) => {
    const options = Object.assign({}, config);
    if (options.path) {
        options.defaultPath = options.path;
        delete options.path;
    }
    return dialog.showSaveDialog(options);
};
// ///////////////////////////////////////
export { Dialog };
