'use strict';
import { EditBase } from '../base';
import STYLE from './index.less';
import HTML from './radioGroup.html';
export class RadioGroup extends EditBase {
    constructor() {
        super();
        this._disabled = false;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${HTML}`;
    }
    _onConnected() {
        super._onConnected();
        this.$slot = this.shadowRoot.querySelector('slot');
        this.$radioGroup = this.shadowRoot.querySelector('#radio-group');
        this._slotChangeBindHandle = this._onSlotChange.bind(this);
        this.$slot.addEventListener('slotchange', this._slotChangeBindHandle);
        this._radioChangeBindHandle = this._radioChange.bind(this);
        this.$radioGroup.addEventListener('change', this._radioChangeBindHandle);
    }
    _onDisconnected() {
        super._onDisconnected();
        this.$slot?.removeEventListener('slotchange', this._slotChangeBindHandle);
        this.$radioGroup?.removeEventListener('change', this._radioChangeBindHandle);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'checked',
            'default-value',
            'name',
        ];
    }
    /**
     * 
     * @param attr { string }
     * @param oldVal { string | null }
     * @param newVal { string | null }
     * */
    attributeChangedCallback(attr, oldVal, newVal) {
        switch (attr) {
            //
            case 'default-value':
                this._onDefaultValueChange(newVal);
                break;
            case 'value':
                // @ts-ignore
                this._onValueChanged(newVal);
                break;
            default:
                break;
        }
    }
    get value() {
        return this._value;
    }
    set value(val) {
        if (val !== null && val !== undefined) {
            this.setAttribute('value', val);
        }
        else {
            this.removeAttribute('value');
        }
    }
    get defaultValue() {
        return this._defaultValue;
    }
    set defaultValue(val) {
        if (val !== null && val !== undefined) {
            this.setAttribute('default-value', val);
        }
        else {
            this.removeAttribute('default-value');
        }
    }
    /**
     * @description slot 
     **/
    _onSlotChange() {
        this.$radios = this.querySelectorAll('ui-radio');
        this.$radios.forEach((el) => {
            if (this.defaultValue?.toString() === el?.value?.toString()) {
                el.checked = true;
            }
        });
    }
    /**
     * @description default-value 
     **/
    _onDefaultValueChange(val) {
        const value = val ?? '';
        this._defaultValue = value;
        this.value = value;
    }
    /**
     * @description value 
     **/
    _onValueChanged(val) {
        this._value = val ?? '';
        this.$radios?.forEach(el => {
            // @ts-ignore
            el.checked = el?.value === this._value;
        });
    }
    /**
     * @description  radio 
     **/
    _radioChange(e) {
        e.stopPropagation();
        this.$radios?.forEach(el => {
            if (el === e?.target) {
                // @ts-ignore
                el.cheked = (el === e?.target);
            }
            else {
                // @ts-ignore
                el.checked = false;
            }
        });
        this.value = e.target?.value;
        this.dispatch('change');
    }
}
