'use strict';
import * as path from 'path';
import { shell } from 'electron';
import { relative } from 'path';
import { FileUrl, fileUrlManager } from './fileURL';
import { EditBase } from '../base';
import { translate } from '../../../utils/utils';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const fs = require('fs');
const { ipcRenderer } = require('electron');
const { Menu, MenuItem, getCurrentWindow } = require('@electron/remote');
const instanceArray = [];
let customStyle = '';
// 
const defaultProtocol = {
    label: 'file://',
    path: '',
    protocol: 'file',
    description: '',
    invalidInfo: '',
};
/**
 * TODO: electron
 */
export class File extends EditBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.url = new FileUrl(this);
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$location = this.shadowRoot.querySelector('.location');
        this.$protocolLabel = this.shadowRoot.querySelector('.protocol-label');
        this.$input = this.shadowRoot.querySelector('.input');
        this.$select = this.shadowRoot.querySelector('.select');
        this.$open = this.shadowRoot.querySelector('.open');
        this.$location.$root = this.$protocolLabel.$root = this.$input.$root = this.$select.$root = this.$open.$root = this;
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    static resolveToRaw(url) {
        return fileUrlManager.resolveToRaw(url);
    }
    static resolveToUrl(raw, protocol) {
        return fileUrlManager.resolveToUrl(raw, protocol);
    }
    static getAllProtocolInfos() {
        return [defaultProtocol, ...fileUrlManager.getAllFileProtocol()];
    }
    /**
     *  value 
     */
    toAbsolutePath() {
        return this.url.raw;
    }
    // ////////////////////
    get value() {
        if (this.invalid) {
            return '';
        }
        return this.url && this.url.toString();
    }
    set value(val) {
        const pathValue = val ?? '';
        this.setAttribute('value', pathValue.toString().trim());
    }
    get type() {
        const value = this.getAttribute('type');
        if (value) {
            return value.trim();
        }
        return value;
    }
    set type(val) {
        if (val) {
            this.setAttribute('type', val);
        }
        else {
            this.removeAttribute('type');
        }
    }
    get extensions() {
        const value = this.getAttribute('extensions');
        if (value) {
            return value.trim();
        }
        return value;
    }
    set extensions(val) {
        if (val) {
            this.setAttribute('extensions', val);
        }
        else {
            this.removeAttribute('extensions');
        }
    }
    get multi() {
        return this.hasAttribute('multi');
    }
    set multi(multi) {
        if (multi) {
            this.setAttribute('multi', '');
        }
        else {
            this.removeAttribute('multi');
        }
    }
    get protocol() {
        return this.url.protocol || 'file';
    }
    set protocol(val) {
        if (this.url.protocol === val) {
            return;
        }
        this.url.protocol = val;
        this._urlChange();
    }
    // TODO:  protocols 
    // 
    get _protocols() {
        const infos = this.getAttribute('protocols');
        if (!infos) {
            return [];
        }
        return infos.split(',').map((i) => i.trim());
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
        // 
        this.$input.addEventListener('confirm', this._onInputChange);
        this.$select.addEventListener('click', this._onSelectClick);
        this.$open.addEventListener('click', this._onOpenClick);
        this.$location.addEventListener('click', this._onLocationClick);
        // label
        this.$location.style.display = 'none';
        // 
        this.addEventListener('mousedown', this._onMouseDown);
        // TODO: 
        if (this._protocols.length > 0 && !this.multi) {
            this.$location.style.display = 'flex';
            if (this.url.value) {
                this.protocol = this.url.protocol || 'file';
            }
            else {
                this.protocol = this._protocols[0];
            }
            this._urlChange();
        }
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.$input.removeEventListener('confirm', this._onInputChange);
        this.$select.removeEventListener('click', this._onSelectClick);
        this.$open.removeEventListener('click', this._onOpenClick);
        this.$location.removeEventListener('click', this._onLocationClick);
        this.removeEventListener('mousedown', this._onMouseDown);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'placeholder',
            'type',
            'extensions',
            'multi',
            // 'protocol', // '' | app | project
            // 'protocols', // 
        ];
    }
    /**
     * attribute 
     */
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'placeholder':
                this._onPlaceholderChanged(n);
                break;
            // case 'protocol':
            //     this.url.protocol = n;
            //     this._urlChange();
            //     break;
        }
    }
    // ////////////////////
    /**
     *  value 
     */
    _onValueChanged(val) {
        if (path.isAbsolute(val)) {
            this.protocol = 'file';
        }
        this._updateValue(val);
    }
    /**
     *  placeholder 
     */
    _onPlaceholderChanged(val) {
        this.$input._onPlaceholderChanged(translate(val) ?? '');
    }
    /**
     * @description 
     **/
    static _updateTranslate() {
        for (const el of instanceArray) {
            el._onPlaceholderChanged(el.getAttribute('placeholder'));
        }
    }
    /**
     *  readonly 
     */
    _onReadonlyChanged(val) {
        if (val) {
            this.$input.setAttribute('readonly', '');
        }
        else {
            this.$input.removeAttribute('readonly');
        }
    }
    /**
     *  disabled 
     */
    _onDisabledChanged(val) {
        if (val) {
            this.$input.setAttribute('disabled', '');
        }
        else {
            this.$input.removeAttribute('disabled');
        }
    }
    // ////////////////////
    // 
    /**
     * /
     */
    _onSelectClick() {
        // @ts-ignore
        const $elem = this;
        if ($elem.$root.readonly) {
            return;
        }
        // 
        ipcRenderer.send('ui-file-select-file', {
            type: $elem.$root.type,
            extensions: $elem.$root.extensions,
            multi: $elem.$root.multi || false,
            defaultPath: $elem.$root.url.raw || $elem.$root.url.protocolInfo?.path,
        });
        // /
        ipcRenderer.once('main-select-file', (event, result) => {
            if (!result || result.length === 0) {
                return;
            }
            if ($elem.$root.url.protocolInfo) {
                $elem.$root.url.relPath = relative($elem.$root.url.protocolInfo.path, result);
                $elem.$root._urlChange();
            }
            else {
                $elem.$root._updateValue(result);
            }
            $elem.$root.dispatch('change');
            $elem.$root.dispatch('confirm');
        });
    }
    /**
     * 
     */
    _onOpenClick(event) {
        // @ts-ignore
        const $elem = this;
        if (!fs.existsSync($elem.$root.toAbsolutePath())) {
            // dialog  Dialog.warn()
            ipcRenderer.send('ui-file-not-exist');
            return;
        }
        shell.showItemInFolder($elem.$root.toAbsolutePath());
    }
    /**
     * label 
     */
    _onLocationClick(event) {
        // @ts-ignore
        const $elem = this;
        let menuInfo = File.getAllProtocolInfos();
        const protocols = $elem.$root._protocols;
        if (!protocols.length) {
            return;
        }
        menuInfo = menuInfo.filter((item) => protocols.includes(item.protocol));
        // 
        const menu = new Menu();
        menuInfo.map((item) => {
            return menu.append(new MenuItem({
                label: item.protocol,
                type: 'checkbox',
                checked: $elem.$root.protocol === item.protocol || (!$elem.$root.protocol && item.protocol === 'file'),
                click() {
                    $elem.$root.url.protocol = item.protocol;
                    $elem.$root._urlChange();
                    $elem.$root.dispatch('change');
                    $elem.$root.dispatch('confirm');
                },
            }));
        });
        menu.popup({ window: getCurrentWindow() });
    }
    /**
     * input 
     */
    _onInputChange(event) {
        // @ts-ignore
        const $elem = this;
        if ($elem.$root.readonly) {
            return;
        }
        $elem.$root.url.value = event.target.value;
        $elem.$root._updateValue(event.target.value);
        $elem.$root.dispatch('change');
        $elem.$root.dispatch('confirm');
    }
    /**
     *  value
     */
    _updateValue(val) {
        if (!val) {
            this.url.relPath = '';
            this._urlChange();
            return;
        }
        this.url.value = val;
        this._urlChange();
    }
    /**
     * URL 
     */
    _urlChange() {
        this.$input.value = this.url.value;
        this.$protocolLabel.value = (this.url.protocol || defaultProtocol.protocol) + '://';
        this.$location.setAttribute('tooltip', this.url.protocolInfo?.description || defaultProtocol.description);
        if (this.url.invalid) {
            this.setAttribute('invalid', 'true');
            this.url.protocolInfo && this.$input.setAttribute('tooltip', this.url.protocolInfo.invalidInfo);
        }
        else {
            this.removeAttribute('invalid');
            this.$input.removeAttribute('tooltip');
        }
    }
}
