'use strict';
import { GridCtrl } from './grid-ctrl';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
import HTML from './index.html';
const customStyle = '';
const defaultConfig = {
    showX: true,
    showY: true,
    axisXConfig: {},
};
export class Grid extends HTMLElement {
    /**
     * 
     */
    constructor() {
        super();
        this.attachShadow({ mode: 'open' });
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        // 
        this.$curveLabelX = this.shadowRoot.querySelector('.x-axis');
        this.$curveLabelY = this.shadowRoot.querySelector('.y-axis');
        this.$ctrlCanvas = this.shadowRoot.querySelector('canvas');
    }
    ////////////////////////////
    get disabled() {
        return this.getAttribute('disabled') !== null;
    }
    set disabled(val) {
        val = !!val;
        if (val) {
            this.setAttribute('disabled', '');
            this.setAttribute('tabindex', '-1');
        }
        else {
            this.removeAttribute('disabled');
            this.setAttribute('tabindex', '0');
        }
    }
    get readonly() {
        return this.getAttribute('readonly') !== null;
    }
    set readonly(val) {
        val = !!val;
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    /**
     * 
     */
    connectedCallback() {
        if (!this.shadowRoot) {
            return;
        }
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
        if (!this.config) {
            try {
                const config = JSON.parse(this.getAttribute('config'));
                this.setConfig(config);
                this.initGrid();
            }
            catch (error) {
                this.setConfig();
                this.initGrid();
            }
        }
        else {
            if (this.$ctrlCanvas) {
                this.$ctrlCanvas.width = this.clientWidth;
                this.$ctrlCanvas.height = this.clientHeight;
                this.initGrid();
            }
        }
        this.gridCtrl?.on('change', () => {
            this.dispatch('change');
        });
    }
    // get set focused
    // get set disabled
    /**
     * TODO config
     *  attribute 
     */
    static get observedAttributes() {
        return ['focused', 'readonly', 'disabled', 'value'];
    }
    setConfig(config) {
        this.$ctrlCanvas.width = this.clientWidth;
        this.$ctrlCanvas.height = this.clientHeight;
        this.$curveLabelX.innerHTML = '';
        this.$curveLabelY.innerHTML = '';
        const axisXConfig = {
            $container: this.$curveLabelX,
        };
        const axisYConfig = {
            $container: this.$curveLabelY,
        };
        const defaultConfig = {
            showX: true,
            showY: true,
            readonly: this.readonly,
            axisMargin: 30,
            lineWidth: 1,
        };
        if (config) {
            Object.assign(defaultConfig, config);
            if (config.showX !== false) {
                Object.assign(axisXConfig, config.axisXConfig || {});
                defaultConfig.axisXConfig = axisXConfig;
            }
            if (config.showY !== false) {
                Object.assign(axisYConfig, config.axisYConfig || {});
                defaultConfig.axisYConfig = axisYConfig;
            }
        }
        else {
            defaultConfig.axisXConfig = axisXConfig;
            defaultConfig.axisYConfig = axisYConfig;
        }
        this.config = defaultConfig;
    }
    valueToPixelX(val) {
        return this.gridCtrl.valueToPixelX(val);
    }
    pixelToValueX(val) {
        return this.gridCtrl.pixelToValueX(val);
    }
    move(deltaPixelX, deltaPixelY) {
        this.gridCtrl.translate(deltaPixelX, deltaPixelY);
        this.gridCtrl?.render();
    }
    // ////////////////////
    // 
    initGrid() {
        if (this.gridCtrl) {
            this.gridCtrl.resetConfig(this.config);
            return;
        }
        this.gridCtrl = new GridCtrl(this.$ctrlCanvas, this.config);
        this.gridCtrl.init();
    }
    /**
     * 
     * @param x
     * @param y 
     * @param scale
     */
    scaleAt(x, y, scale) {
        return this.gridCtrl?.scaleAt(x, y, scale);
    }
    /**
     * 
     * @param startP 
     * @param endP 
     */
    jumpToRang(startP, endP) {
        const { start, end } = this.gridCtrl?.getValueRang();
    }
    getValueRang() {
        return this.gridCtrl?.getValueRang();
    }
    /**
     * 
     * @param eventName 
     */
    dispatch(eventName) {
        const event = document.createEvent('HTMLEvents');
        event.initEvent(eventName, true, true);
        this.dispatchEvent(event);
    }
}
