'use strict';
const fs = require('fs');
import { FunctionBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
const DEFAULT_DIRECTION = 'bottom';
const DEFAULT_ADJUST = 'center';
const DEFAULT_ADJUST_PADDING = 6;
const DEFAULT_ARROW_BOX_SIZE = 9;
const LEGAL_DIRECTIONS = ['top', 'right', 'bottom', 'left'];
const LEGAL_ADJUST_DIRECTIONS = ['top', 'middle', 'bottom', 'left', 'center', 'right'];
export class Tooltip extends FunctionBase {
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$content = this.shadowRoot.querySelector('.content');
        // @ts-ignore
        this.resizeObserver = new window.ResizeObserver(() => {
            // 
            if (!this.getBoundingClientRect().height) {
                return;
            }
            this._showArrow(this.getAttribute('arrow') || '');
        });
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    // ////////////////////
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        this._showArrow(this.getAttribute('arrow') || '');
        this.resizeObserver.observe(this.$content);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        this.resizeObserver.unobserve(this.$content);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['arrow'];
    }
    /**
     * attribute 
     */
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'arrow':
                this._showArrow(n);
                break;
            case 'disabled':
                if (n === 'false') {
                    this.disabled = false;
                }
                break;
        }
    }
    // ////////////////////
    /**
     *  arrow="bottom center"
     *  arrow="direction adjust" 
     * arrow="left"
     * arrow="left middle"
     * arrow="left top"
     * arrow="left top+20px"
     */
    _showArrow(val) {
        const parts = val.split(' ');
        let direction = parts[0] || DEFAULT_DIRECTION;
        if (!LEGAL_DIRECTIONS.includes(direction)) {
            direction = 'bottom';
        }
        let adjustDirection = parts[1] || (direction === 'left' || direction === 'right' ? 'middle' : DEFAULT_ADJUST);
        let adjustNumber = DEFAULT_ADJUST_PADDING;
        const adjustMatches = adjustDirection.match(/([a-z]+)([-+]?[\d.]+)?/);
        if (adjustMatches) {
            adjustDirection = adjustMatches[1];
        }
        if (!LEGAL_ADJUST_DIRECTIONS.includes(adjustDirection)) {
            adjustDirection = 'center';
        }
        const rect = this.$content.getBoundingClientRect();
        switch (adjustDirection) {
            case 'center':
                adjustNumber = rect.width / 2 - DEFAULT_ARROW_BOX_SIZE;
                break;
            case 'right':
                adjustNumber = rect.width - DEFAULT_ADJUST_PADDING - DEFAULT_ARROW_BOX_SIZE * 2;
                break;
            case 'middle':
                adjustNumber = rect.height / 2 - DEFAULT_ARROW_BOX_SIZE;
                break;
            case 'bottom':
                adjustNumber = rect.height - DEFAULT_ADJUST_PADDING - DEFAULT_ARROW_BOX_SIZE * 2;
                break;
        }
        if (adjustMatches && adjustMatches[2] !== undefined) {
            adjustNumber += Number(adjustMatches[2]);
        }
        // 
        if (adjustNumber < 0) {
            adjustNumber = 2;
        }
        this.$content.setAttribute('direction', direction);
        this.$content.style.setProperty('--direction-adjust', `${adjustNumber}px`);
    }
}
