'use strict';
import { FunctionBase } from '../base';
import { DragArea } from './drag-area';
import HTML from './drag-object.html';
import STYLE from './drag-object.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
/**
 *     attribute
 */
export class DragObject extends FunctionBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.missStateShowText = '';
        this.shadowRoot.innerHTML = `${CUSTOM_STYLE}<style>${STYLE}</style>${HTML}`;
        this.$name = this.shadowRoot.querySelector('.name');
        this.$area = this.shadowRoot.querySelector('ui-drag-area');
        this.$clear = this.shadowRoot.querySelector('.clear');
        this.$select = this.shadowRoot.querySelector('.select');
        this.$value = this.shadowRoot.querySelector('ui-drag-area .value');
        this.$placeholder = this.shadowRoot.querySelector('ui-drag-area .placeholder');
        this.$area.$root = this.$clear.$root = this.$select.$root = this;
    }
    get droppable() {
        return this._droppable;
    }
    set droppable(val) {
        this.setAttribute('droppable', val);
    }
    get value() {
        return this._value;
    }
    set value(val) {
        val = val ? val.toString().trim() : '';
        this.setAttribute('value', val);
    }
    get placeholder() {
        return this._placeholder;
    }
    set placeholder(val) {
        this._placeholder = val ? val.toString().trim() : '';
        this.setAttribute('placeholder', this._placeholder);
    }
    get invalid() {
        return this.hasAttribute('invalid');
    }
    set invalid(val) {
        if (val) {
            this.setAttribute('invalid', '');
        }
        else {
            this.removeAttribute('invalid');
        }
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        this._droppable = this.getAttribute('droppable') || '';
        this._value = this.getAttribute('value') || '';
        this._placeholder = this.getAttribute('placeholder') || this._droppable || 'NONE';
        this._droppable && (this.$area.droppable = this._droppable);
        this._placeholder && (this.$placeholder.innerHTML = this._placeholder);
        this.$area.setAttribute('title', this._droppable);
        // 
        this.$area.addEventListener('dragover', this._onAreaDragOver);
        this.$area.addEventListener('dragleave', this._onAreaDragLeave);
        this.$area.addEventListener('drop', this._onAreaDrop);
        this.$clear.addEventListener('click', this._onClearClick);
        this.$select.addEventListener('click', this._onSelectClick);
        this.$area.addEventListener('click', this._onAreaClick);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        this.$area.removeEventListener('dragover', this._onAreaDragOver);
        this.$area.removeEventListener('dragleave', this._onAreaDragLeave);
        this.$area.removeEventListener('drop', this._onAreaDrop);
        this.$clear.removeEventListener('click', this._onClearClick);
        this.$select.removeEventListener('click', this._onSelectClick);
        this.$area.removeEventListener('click', this._onAreaClick);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'disabled',
            'droppable',
            'value',
            'placeholder',
            'invalid',
            'readonly',
            'path',
        ];
    }
    /**
     * attribute 
     *
     * o oldValue
     * nnewValue
     */
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'droppable':
                this._onDroppableChanged(n);
                break;
            case 'value':
                this._onValueChanged(n);
                break;
            case 'placeholder':
                this._onPlaceholderChanged(n);
                break;
            case 'disabled':
                if (n === 'false') {
                    this.disabled = false;
                }
                break;
        }
    }
    // ////////////////////
    /**
     *  droppable 
     */
    _onDroppableChanged(val) {
        this._droppable = val?.trim();
        this.placeholder = this._droppable;
        this.$area.droppable = this._droppable;
        this.$name.innerHTML = this._droppable;
    }
    /**
     *  value 
     */
    async _onValueChanged(val) {
        if (this._value !== val?.trim()) {
            this._value = val;
            const name = await this._onTranslationName(this._value);
            // name === null && this._value  missing 
            if (name === null) {
                if (this._value) {
                    this.$value.innerText = this.missStateShowText;
                    this.setAttribute('missing', '');
                }
                else {
                    this.$value.innerText = '';
                    this.removeAttribute('missing');
                }
            }
            else {
                this.$value.innerText = name;
                this.removeAttribute('missing');
            }
        }
        if (this._value) {
            this.setAttribute('effective', '');
            if (this.hasAttribute('editable')) {
                this.setAttribute('editable', 'true');
            }
        }
        else {
            this.removeAttribute('effective');
            if (this.hasAttribute('editable')) {
                this.setAttribute('editable', 'false');
            }
        }
    }
    async _onTranslationName(val) {
        return val;
    }
    /**
     *  placeholder 
     */
    _onPlaceholderChanged(val) {
        this._placeholder = val?.trim();
        this.$placeholder.innerHTML = this._placeholder;
    }
    // 
    /**
     *  area 
     */
    _onAreaDragOver() {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        $elem.$root.setAttribute('state', $elem.hoving || $elem.additional ? 'allow' : 'refused');
    }
    /**
     *  area 
     */
    _onAreaDragLeave() {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        $elem.$root.removeAttribute('state');
    }
    /**
     *  area 
     */
    _onAreaDrop(event) {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        const state = $elem.$root.getAttribute('state');
        $elem.$root.removeAttribute('state');
        if (state === 'refused') {
            return;
        }
        if ($elem.additional) {
            const types = $elem.droppable;
            const list = DragArea.currentDragInfo.additional;
            if (list) {
                for (const item of list) {
                    if (types.includes(item.type)) {
                        $elem.$root.value = item.value;
                        $elem.$root.removeAttribute('invalid');
                        $elem.$root.dispatch('change');
                        $elem.$root.dispatch('confirm');
                        return;
                    }
                }
            }
        }
        else {
            if ($elem.$root.invalid) {
                $elem.$root.invalid = false;
            }
        }
        $elem.$root.value = event.dataTransfer.getData('value');
        $elem.$root.dispatch('change');
        $elem.$root.dispatch('confirm');
    }
    /**
     * 
     */
    _onClearClick(event) {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        event.stopPropagation();
        $elem.$root.value = '';
        $elem.$root.removeAttribute('invalid');
        $elem.$root.dispatch('clear');
        $elem.$root.dispatch('change');
        $elem.$root.dispatch('confirm');
    }
    /*
    * 
    */
    _onAreaClick() { }
    _onSelectClick() { }
}
