'use strict';
import { DisplayBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
import { translate } from '../../../utils/utils';
const instanceArray = [];
const prefix = 'i18n:';
/**
 *   Attribute
 *
 *   value: label 
 */
export class Label extends DisplayBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this._renderLock = false;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${HTML}`;
        this.$span = this.shadowRoot.querySelector('span');
        this.$slot = this.shadowRoot.querySelector('slot');
        this.$slot.$root = this;
        this._renderLock = false;
        this.$slot.addEventListener('slotchange', this._slotChange);
    }
    get value() {
        return this.getAttribute('value') || '';
    }
    set value(val) {
        if (val) {
            this.setAttribute('value', val);
            this.innerHTML = val;
        }
        else {
            this.removeAttribute('value');
            this.innerHTML = '';
        }
    }
    get i18n() {
        return this.hasAttribute('i18n') || this.value.startsWith(prefix);
    }
    set i18n(value) {
        if (value) {
            this.setAttribute('i18n', '');
        }
        else {
            this.removeAttribute('i18n');
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['value', 'i18n'];
    }
    /**
     * attribute 
     *
     * o oldValue
     * nnewValue
     */
    attributeChangedCallback(attr, o, n) {
        switch (attr) {
            case 'value':
            case 'i18n':
                if (this._renderLock) {
                    return;
                }
                requestAnimationFrame(() => {
                    this._render();
                    this._renderLock = false;
                });
                this._renderLock = true;
                break;
            default:
                break;
        }
    }
    /**
     * @description 
     **/
    static _updateTranslate() {
        instanceArray.forEach((el) => {
            el._render(el.getAttribute('value'));
        });
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    /**
     * 
     */
    _render(text) {
        const val = text ? text : (this.value ?? this.innerHTML);
        this.$span.innerHTML = translate(val) ?? '';
    }
    /**
     * slot 
     */
    _slotChange() {
        const $elem = this;
        if ($elem.$root.value === $elem.$root.innerHTML) {
            return;
        }
        $elem.$root.value = $elem.$root.innerHTML;
    }
}
