export default ":host {\n  user-select: none;\n  outline: none;\n  display: inline-block;\n  box-sizing: border-box;\n  font-size: inherit;\n  line-height: inherit;\n  font-family: inherit;\n  border: calc(var(--size-normal-border) * 1px) solid transparent;\n  border-radius: calc(var(--size-normal-radius) * 1px);\n}\n:host([disabled]) {\n  cursor: not-allowed;\n  opacity: 0.55;\n  pointer-events: none;\n}\n:host([readonly]) {\n  pointer-events: none;\n}\n:host([hidden]) {\n  display: none;\n}\n:host {\n  --ui-slider-bar-width: 65%;\n  position: relative;\n  white-space: nowrap;\n  display: inline-block;\n  outline: none;\n}\n:host .wrap {\n  display: flex;\n  justify-content: space-between;\n  outline: none !important;\n}\n:host .track {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n  vertical-align: top;\n  width: var(--ui-slider-bar-width);\n  cursor: pointer;\n}\n:host .track .wrapper {\n  width: 100%;\n  background: var(--color-normal-fill-emphasis);\n  height: 3px;\n  border-radius: 3px;\n  border-top: 1px solid var(--color-normal-fill-important);\n  border-bottom: 1px solid var(--color-normal-border-weaker);\n}\n:host .track .cursor {\n  position: absolute;\n  margin-left: -5px;\n}\n:host .track .cursor div {\n  background: var(--color-active-fill-important);\n  width: 10px;\n  height: 10px;\n  border-radius: 100%;\n  border: 2px solid var(--color-normal-contrast-important);\n  box-shadow: 0 0 2px 2px var(--color-normal-fill-important) inset;\n}\n:host .track .cursor:hover div {\n  border-color: var(--color-active-contrast);\n}\n:host .num-input {\n  width: calc(100% - var(--ui-slider-bar-width) - 12px);\n  height: 100%;\n  font-size: 12px;\n}\n:host([disabled]) .num-input[disabled] {\n  opacity: 1;\n}\n:host([focused]:not(disabled)) .cursor div {\n  border-color: var(--color-active-contrast);\n}\n:host([focused]:not(disabled)) .num-input {\n  border-color: var(--color-focus-fill);\n}\n:host([hide-num-input]) .num-input {\n  display: