export default "/* Tomorrow Night Eighties Theme */\n/* Original theme - https://github.com/chriskempson/tomorrow-theme */\n/* http://jmblog.github.com/color-themes-for-google-code-highlightjs */\n/* Tomorrow Red */\n.hljs-string,\n.hljs-symbol,\n.hljs-variable,\n.hljs-template-variable,\n.hljs-selector-id,\n.hljs-selector-class,\n.hljs-regexp,\n.hljs-deletion {\n  color: #f39595;\n}\n/* Tomorrow Orange */\n.hljs-number,\n.hljs-literal,\n.hljs-params,\n.hljs-link {\n  color: #ffa16d;\n}\n/* Tomorrow Yellow */\n.hljs-name,\n.hljs-attr,\n.hljs-attribute,\n.hljs-builtin-name,\n.hljs-built_in {\n  color: #dec76d;\n}\n/* Tomorrow Green */\n.hljs-bullet,\n.hljs-addition,\n.hljs-comment,\n.hljs-quote {\n  color: #8cc174;\n}\n/* Tomorrow Gray */\n.hljs-tag {\n  color: #999999;\n}\n/* Tomorrow Blue */\n.hljs-name,\n.hljs-type,\n.hljs-title,\n.hljs-section {\n  color: #5ab1e6;\n}\n/* Tomorrow Purple */\n.hljs-meta,\n.hljs-keyword,\n.hljs-selector-tag {\n  color: #e099e4;\n}\n.hljs-emphasis {\n  font-style: italic;\n}\n.hljs-strong {\n  font-weight: bold;\n}\n:host {\n  user-select: none;\n  outline: none;\n  display: inline-block;\n  box-sizing: border-box;\n  font-size: inherit;\n  line-height: inherit;\n  font-family: inherit;\n  border: calc(var(--size-normal-border) * 1px) solid transparent;\n  border-radius: calc(var(--size-normal-radius) * 1px);\n}\n:host([disabled]) {\n  cursor: not-allowed;\n  opacity: 0.4;\n  pointer-events: none;\n}\n:host {\n  font-size: 12px;\n  user-select: text;\n  outline: none;\n  display: block;\n  position: relative;\n  border-color: var(--color-normal-border);\n  background: var(--color-normal-fill-emphasis);\n  overflow: auto;\n  padding: 10px;\n  cursor: auto;\n}\n:host pre {\n  margin: 0;\n  overflow: auto;\n  display: table;\n  table-layout: fixed;\n  width: 100%;\n}\n:host pre code {\n  line-height: 1.7;\n  font-family: var(--font-editor);\n}\n:host slot {\n  display: none;\n}\n";