'use strict';
import { EditBase } from '../base';
import STYLE from './index.less';
import HTML from './index.html';
export class Radio extends EditBase {
    constructor() {
        super();
        this._checked = false;
        this._disabled = false;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${HTML}`;
        this.$radio = this.shadowRoot.querySelector('.radio-box');
        this.$slot = this.shadowRoot.querySelector('slot');
    }
    _onConnected() {
        super._onConnected();
        //  slot 
        const slotNodes = this.$slot.assignedNodes();
        if (slotNodes.length <= 0) {
            this.$radio.setAttribute('empty', '');
        }
        this.addEventListener('click', () => this._handleOnClick());
    }
    _onDisconnected() {
        super._onDisconnected();
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'checked',
        ];
    }
    /**
     * 
     * @param attr { string }
     * @param oldVal { string | null }
     * @param newVal { string | null }
     * */
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'checked':
                this._onCheckedChange(newVal);
                break;
            default:
                break;
        }
    }
    get value() {
        return this._value;
    }
    set value(val) {
        this.setAttribute('value', val);
    }
    get checked() {
        return this._checked;
    }
    set checked(val) {
        const checked = Boolean(val);
        if (checked) {
            this.setAttribute('checked', '');
        }
        else {
            this.removeAttribute('checked');
        }
    }
    /**
     *  disabled 
     * @param val { boolean }
     */
    _onDisabledChanged(val) {
        this._disabled = val;
    }
    /**
     * checked 
     * @param val
     * */
    _onCheckedChange(val) {
        const checked = val !== null;
        this._checked = checked;
    }
    /**
     * value 
     * @param val
     * */
    _onValueChanged(val) {
        this._value = val;
        this?.setAttribute('value', val);
    }
    /**
    * change
    * */
    _onChange() {
        this.checked = true;
        this.dispatch('change');
    }
    _handleOnClick() {
        this._onChange();
    }
}
