'use strict';
import { Tooltip } from '../../../utils/utils';
import { CurveCtrl } from './lib/curve-ctrl';
import STYLE from './index.less';
import HTML from './index.html';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const customStyle = '';
// Todo: 
export class CurveEditor extends HTMLElement {
    /**
     * 
     */
    constructor() {
        super();
        this.config = {
            type: 'hermit',
            showPreWrapMode: false,
            showPostWrapMode: false,
            axisMargin: 30,
            showXLabel: true,
            showYLabel: true,
            showXLine: true,
            showYLine: true,
            startXOffset: 0,
            startYOffset: 0,
            handlerSize: 45,
            gridColor: '#333846',
            sample: 1,
        };
        this.attachShadow({ mode: 'open' });
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        // 
        this.$curveLabelX = this.shadowRoot.querySelector('.x-axis');
        this.$curveLabelY = this.shadowRoot.querySelector('.y-axis');
        this.$ctrlCanvas = this.shadowRoot.querySelector('.ctrl');
    }
    ////////////////////////////
    set value(val) {
        this._value = val;
        this._render(val);
    }
    get value() {
        if (!this.curveCtrl) {
            return this._value;
        }
        return this.curveCtrl.getCurrentCurveInfo();
    }
    set focused(val) {
        val = !!val;
        if (val) {
            this.$ctrlCanvas?.setAttribute('focused', '');
        }
        else {
            this.$ctrlCanvas?.removeAttribute('focused');
        }
        this.dispatch('focus-changed');
    }
    get disabled() {
        return this.getAttribute('disabled') !== null;
    }
    set disabled(val) {
        val = !!val;
        if (val) {
            this.setAttribute('disabled', '');
            this.$ctrlCanvas?.setAttribute('tabindex', '-1');
        }
        else {
            this.removeAttribute('disabled');
            this.$ctrlCanvas?.setAttribute('tabindex', '0');
        }
    }
    get readonly() {
        return this.getAttribute('readonly') !== null;
    }
    set readonly(val) {
        val = !!val;
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    get sample() {
        return this.hasAttribute('sample') ? parseInt(this.getAttribute('sample')) : 1;
    }
    set sample(val) {
        if (val) {
            this.setAttribute('sample', val + '');
        }
        else {
            this.removeAttribute('sample');
        }
    }
    /**
     * 
     */
    connectedCallback() {
        const disabled = this.getAttribute('disabled') !== null;
        const tabindex = this.getAttribute('tabindex') || '0';
        //  tabindex
        this.setAttribute('tabindex', disabled ? '-1' : tabindex);
        if (!this.shadowRoot) {
            return;
        }
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
        if (this.hasAttribute('config')) {
            //  config 
            const configInfo = this.getAttribute('config');
            if (configInfo) {
                try {
                    const userConfig = JSON.parse(configInfo);
                    this.config = Object.assign(this.config, userConfig);
                }
                catch (error) {
                    console.log(error);
                }
            }
            this.initCurveCtrl();
        }
        if (this.hasAttribute('value')) {
            try {
                const val = JSON.parse(this.getAttribute('value'));
                this.value = val;
            }
            catch (error) {
                console.debug(error);
            }
        }
        Tooltip.bind(this);
    }
    /**
     * 
     */
    disconnectedCallback() {
        this.curveCtrl && this.curveCtrl.unregisterHandler();
        Tooltip.unbind(this);
    }
    // get set focused
    // get set disabled
    /**
     * TODO config
     *  attribute 
     */
    static get observedAttributes() {
        return ['focused', 'readonly', 'disabled', 'value', 'sample', 'config'];
    }
    /**
    * @description 
    * */
    static registerClipboardHandle(clipboard) {
        try {
            // eslint-disable-next-line no-prototype-builtins
            if (clipboard.hasOwnProperty('read') && clipboard.hasOwnProperty('write')) {
                CurveCtrl.registerClipboardHandle(clipboard);
            }
        }
        catch (e) {
            console.error(e);
        }
    }
    /**
     * Attribute 
     * @param attr
     * @param oldValue
     * @param newValue
     */
    attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            case 'readonly':
                this.curveCtrl.readonly = !!newValue;
                break;
            case 'value':
                if (!this.curveCtrl) {
                    break;
                }
                try {
                    const val = JSON.parse(newValue);
                    this._render(val);
                }
                catch (error) {
                    console.debug(error);
                }
                break;
            case 'sample':
                this.curveCtrl && this.curveCtrl.updateSample(parseInt(newValue));
                break;
        }
    }
    /**
     * 
     * @param w width
     * @param h height
     */
    resize(w, h) {
        if (!this.curveCtrl) {
            return;
        }
        this.curveCtrl.resize(w, h);
        this.curveCtrl.rePaint();
    }
    /**
     * 
     */
    rePaint() {
        if (!this.curveCtrl) {
            return;
        }
        this.curveCtrl.rePaint();
    }
    // ////////////////////
    // 
    // TODO 
    setConfig(config) {
        this.config = config;
        this.initCurveCtrl();
        this.value && this._render(this.value);
    }
    /**
     *  CurveCtrl
     */
    initCurveCtrl() {
        //  value 
        // const value: IValue | null = this.value;patchMultiToKeys
        const commonOpt = {
            showLabel: this.config.showYLabel,
            showLine: this.config.showYLine,
            lineColor: this.config.gridColor,
        };
        const curveCtrlConfig = {
            gridConfig: {
                axisXConfig: {
                    $container: this.config.showXLabel ? this.$curveLabelX : undefined,
                    maxValue: this.config.xRange && this.config.xRange[1],
                    minValue: this.config.xRange && this.config.xRange[0],
                    format: this.config.xFormat,
                    startOffset: this.config.startXOffset,
                    precision: this.config.precisionX,
                    ...commonOpt,
                },
                axisYConfig: {
                    $container: this.config.showYLabel ? this.$curveLabelY : undefined,
                    maxValue: this.config.yRange && this.config.yRange[1],
                    minValue: this.config.yRange && this.config.yRange[0],
                    format: this.config.yFormat,
                    precision: this.config.precisionY,
                    ...commonOpt,
                },
                axisMargin: this.config.axisMargin,
                // TODO 
                negative: this.config.negative,
            },
            handlerSize: this.config.handlerSize,
            spacingFrame: this.config.spacingFrame,
            sample: this.config.sample,
            xAxisName: this.config.xAxisName,
            yAxisName: this.config.yAxisName,
        };
        if (!this.config.showXLabel) {
            this.$curveLabelX.innerHTML = '';
        }
        if (!this.config.showYLabel) {
            this.$curveLabelY.innerHTML = '';
        }
        if (!this.curveCtrl) {
            this.$ctrlCanvas.width = this.clientWidth;
            this.$ctrlCanvas.height = this.clientHeight;
            this.curveCtrl = new CurveCtrl(this.$ctrlCanvas, curveCtrlConfig);
            this.curveCtrl.on('change', () => this.dispatch('change'));
            this.curveCtrl.on('confirm', () => this.dispatch('confirm'));
            this.curveCtrl.on('transform', () => this.dispatch('transform'));
        }
        else {
            this.curveCtrl.resetConfig(curveCtrlConfig);
        }
    }
    /**
     * 
     * @param xRange
     * @param yRange
     */
    setDisplayRange(xRange, yRange) {
        this.curveCtrl.setDisplayRange(xRange, yRange);
    }
    /**
     * 
     */
    clear() {
        this.curveCtrl && this.curveCtrl.clearAll();
    }
    /**
     * 
     * @param value
     */
    _render(value) {
        if (value && value.curveInfos) {
            this.curveCtrl.paint(value);
        }
        else {
            this.curveCtrl.clearAll();
            this.curveCtrl.grid.render();
        }
    }
    /**
     * 
     * @param eventName 
     */
    dispatch(eventName) {
        const event = document.createEvent('HTMLEvents');
        event.initEvent(eventName, true, true);
        this.dispatchEvent(event);
    }
}
