'use strict';
let Profile = null;
export class Setting extends HTMLElement {
    /**
     *  profile 
     */
    static setProfile(profile) {
        if (typeof profile !== 'object') {
            throw new Error(' Profile');
        }
        Profile = profile;
    }
    // /////////////////////////////////////////////////////
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'package',
        ];
    }
    attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            default:
                break;
        }
    }
    /**
     * 
     */
    constructor() {
        super();
        this.addEventListener('confirm', this._onConfirm);
        this.addEventListener('path', this._onPath);
    }
    /**
     * 
     */
    connectedCallback() { }
    /**
     * 
     */
    disconnectedCallback() { }
    // /////////////////////////////////////////////////////
    // 
    _onConfirm(event) {
        event.stopPropagation();
        event.preventDefault();
        if (!Profile) {
            return;
        }
        // @ts-ignore
        const name = this.getAttribute('package');
        if (!name) {
            return;
        }
        const target = event.target;
        if (!target) {
            return;
        }
        const path = target.getAttribute('path');
        if (!path) {
            return;
        }
        // @ts-ignore
        const value = target.value;
        // @ts-ignore
        const type = this.getAttribute('type');
        if ((type === "default" || type === "global" || type === "local") && Profile.setConfig) {
            Profile.setConfig(name, path, value, type || undefined);
        }
        else if ((type === "default" || type === "project") && Profile.setProject) {
            Profile.setProject(name, path, value, type || undefined);
        }
        else if (type === "temp" && Profile.setTemp) {
            Profile.setTemp(name, path, value);
        }
    }
    async _onPath(event) {
        event.stopPropagation();
        event.preventDefault();
        if (!Profile) {
            return;
        }
        // @ts-ignore
        const name = this.getAttribute('package');
        if (!name) {
            return;
        }
        const target = event.target;
        if (!target) {
            return;
        }
        const path = target.getAttribute('path');
        if (!path) {
            return;
        }
        // @ts-ignore
        const type = this.getAttribute('type');
        if ((type === "defaultPreferences" || type === "global" || type === "local") && Profile.getConfig) {
            // @ts-ignore
            target.value = await Profile.getConfig(name, path);
        }
        else if ((type === "defaultProject" || type === "project") && Profile.getProject) {
            // @ts-ignore
            target.value = await Profile.getProject(name, path);
        }
        else if (type === 'temp' && Profile.getTemp) {
            // @ts-ignore
            target.value = await Profile.getTemp(name, path);
        }
    }
}
