export default ":host {\n  user-select: none;\n  outline: none;\n  display: inline-block;\n  box-sizing: border-box;\n  font-size: inherit;\n  line-height: inherit;\n  font-family: inherit;\n  border: calc(var(--size-normal-border) * 1px) solid transparent;\n  border-radius: calc(var(--size-normal-radius) * 1px);\n}\n:host([disabled]) {\n  cursor: not-allowed;\n  opacity: 0.55;\n  pointer-events: none;\n}\n:host([readonly]) {\n  pointer-events: none;\n}\n:host([hidden]) {\n  display: none;\n}\n:host {\n  display: inline-flex;\n  position: relative;\n  contain: content;\n  cursor: pointer;\n  border: calc(var(--size-normal-border) * 1px) solid var(--color-normal-fill-weaker);\n  background: var(--color-normal-fill-weaker);\n  line-height: inherit;\n}\n:host ::slotted(option) {\n  display: none;\n}\n:host ::slotted(optgroup) {\n  display: none;\n}\n:host #placeholder {\n  position: absolute;\n  left: 0;\n  top: 50%;\n  transform: translate(-4px, -50%);\n  padding: 0 2em 0 0.8em;\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  pointer-events: none;\n}\n:host select {\n  -webkit-appearance: none;\n  cursor: pointer;\n  display: flex;\n  flex: 1;\n  outline: 0;\n  border: 0;\n  padding-left: 6px;\n  padding-right: 20px;\n  border-radius: calc(var(--size-normal-radius) * 1px);\n  text-decoration: none;\n  text-overflow: ellipsis;\n  background: var(--color-normal-fill-weaker);\n  width: 100%;\n  color: inherit;\n  font-size: inherit;\n  line-height: inherit;\n  margin: calc(var(--size-normal-border) * -1px) 0;\n}\n:host select option {\n  background: var(---color-normal-fill-emphasis);\n}\n:host select optgroup {\n  background: var(--color-normal-fill-emphasis);\n}\n:host(:last-child) {\n  margin-right: 0;\n}\n.arrow-triangle {\n  display: block;\n  position: absolute;\n  right: 0;\n  top: 50%;\n  transform: translate(-4px, -50%);\n  font-size: 12px;\n  pointer-events: none;\n  outline: none;\n}\n:host(:hover) {\n  border-color: var(--color-foc