'use strict';
const fs = require('fs');
import { EditBase } from '../base';
import { translate } from '../../../utils/utils';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
/**
 *   Attribute
 *
 *   disabled: 
 *   readonly: 
 *   invalid: 
 *
 *   value: 
 */
export class TextArea extends EditBase {
    // //////////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.compositionStart = false;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$textarea = this.shadowRoot.querySelector('textarea');
        //  tab 
        this.$child = this.$textarea;
        this.$textarea.$root = this;
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        return this.getAttribute('value') || '';
    }
    set value(val) {
        this.setAttribute('value', val);
    }
    get placeholder() {
        return this.$textarea.getAttribute('placeholder');
    }
    set placeholder(val) {
        val += '';
        this.$textarea.setAttribute('placeholder', val);
    }
    get autoheight() {
        return this.hasAttribute('autoheight');
    }
    set autoheight(val) {
        if (val) {
            this.setAttribute('autoheight', '');
        }
        else {
            this.removeAttribute('autoheight');
        }
    }
    get pressed() {
        return this.getAttribute('pressed') !== null;
    }
    set pressed(bool) {
        if (bool) {
            this.setAttribute('pressed', '');
        }
        else {
            this.removeAttribute('pressed');
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        // 
        this.$textarea.addEventListener('focus', this._onTextareaFocus);
        this.$textarea.addEventListener('blur', this._onTextareaBlur);
        this.$textarea.addEventListener('input', this._onTextareaChange);
        this.$textarea.addEventListener('keydown', this._onTextareaKeyDown);
        this.$textarea.addEventListener('compositionstart', this._onTextareaCompositionStart);
        this.$textarea.addEventListener('compositionend', this._onTextareaCompositionEnd);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.$textarea.removeEventListener('focus', this._onTextareaFocus);
        this.$textarea.removeEventListener('blur', this._onTextareaBlur);
        this.$textarea.removeEventListener('input', this._onTextareaChange);
        this.$textarea.removeEventListener('keydown', this._onTextareaKeyDown);
        this.$textarea.removeEventListener('compositionstart', this._onTextareaCompositionStart);
        this.$textarea.removeEventListener('compositionend', this._onTextareaCompositionEnd);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'autoheight',
            'password',
            'placeholder',
        ];
    }
    /**
     * attribute 
     * o oldValue
     * nnewValue
     */
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'autoheight':
                this._autoheightChanged(n !== null);
                break;
            case 'placeholder':
                this._onPlaceholderChanged(n);
                break;
        }
    }
    // ////////////////////
    /**
     * textarea 
     *  value 
     */
    _onTextareaFocus() {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        $elem.$root._staging = $elem.$root.value;
        $elem._staging = $elem.$root.value;
    }
    /**
     * textarea 
     */
    _onTextareaBlur() {
        const $elem = this;
        // 
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        if ($elem.$root._staging === $elem.value) {
            return;
        }
        delete $elem.$root._staging;
        $elem.$root.dispatch('confirm');
    }
    _onTextareaCompositionStart() {
        const $elem = this;
        $elem.$root.compositionStart = true;
    }
    _onTextareaCompositionEnd(event) {
        const $elem = this;
        $elem.$root.compositionStart = false;
        $elem.$root._onTextareaChange.call($elem, event);
    }
    /**
     * textarea 
     */
    _onTextareaChange() {
        const $elem = this;
        //  change  value 
        if ($elem.$root.compositionStart) {
            return;
        }
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        if ($elem._staging === $elem.value) {
            return;
        }
        $elem.$root.value = $elem.value;
        $elem._staging = $elem.value;
        $elem.$root.dispatch('change');
        /**
         * 
         * css min-height, max-height 
         */
        if ($elem.autoheight) {
            const scrollHeight = $elem.scrollHeight;
            $elem.style.height = scrollHeight / 2 + 'px';
        }
    }
    /**
     * textarea 
     */
    _onTextareaKeyDown(event) {
        const $elem = this;
        event.stopPropagation();
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        switch (event.keyCode) {
            case 27:
                if ($elem.$root._staging === $elem.value) {
                    // 
                    break;
                }
                $elem.$root.value = $elem.$root._staging;
                $elem._staging = $elem.$root.value;
                $elem.$root.dispatch('change');
                $elem.$root.dispatch('cancel');
                $elem.$root.dispatch('confirm');
                break;
        }
    }
    /**
     *  focused  textarea 
     */
    _onFocusedChanged(val) {
        if (this.disabled || this.readonly || !val) {
            this.focused = false;
            return;
        }
        this.$textarea.focus();
    }
    /**
     * placeholder 
     * */
    _onPlaceholderChanged(val) {
        this.$textarea.placeholder = translate(val) ?? '';
    }
    /**
     * @description 
     **/
    static _updateTranslate() {
        for (const el of instanceArray) {
            el._onPlaceholderChanged(el.getAttribute('placeholder'));
        }
    }
    /**
     *  disabled 
     */
    _onDisabledChanged(val) {
        this.$textarea.disabled = val;
    }
    /**
     *  invalid 
     */
    _onInvalidChanged(val) {
        if (val) {
            this.$textarea.value = '-';
        }
    }
    /**
     *  readonly 
     */
    _onReadonlyChanged(val) {
        this.$textarea.readOnly = val;
    }
    /**
     *  autoheight 
     */
    _autoheightChanged(val) {
        this.$textarea.autoheight = val;
    }
    /**
     *  value 
     */
    _onValueChanged(val) {
        this.invalid = false;
        this.$textarea.value = val;
    }
}
