'use strict';
const fs = require('fs');
import HTML from './index.html';
import STYLE from './index.less';
let customStyle = '<style id="custom-style"></style>';
const instanceArray = [];
export class Loading extends HTMLElement {
    static importStyle(src) {
        // 
        if (fs.existsSync(src)) {
            customStyle = fs.readFileSync(src, 'utf8');
            instanceArray.map((instance) => {
                const el = instance.shadowRoot.querySelector('#custom-style');
                el && (el.innerHTML = customStyle);
            });
        }
    }
    /**
     * 
     */
    constructor() {
        super();
        this.attachShadow({ mode: 'open' });
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${customStyle}${HTML}`;
    }
    connectedCallback() {
        // 
        instanceArray.push(this);
    }
    disconnectedCallback() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
}
