'use strict';
import { DragArea } from "../../function/drag/drag-area";
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = '<style class="style"></style>';
const instanceArray = [];
export class Tree extends DragArea {
    /**
     * 
     */
    constructor() {
        super();
        // 
        this.$items = [];
        this.$style = null;
        this.$content = null;
        this.$fixed = null;
        this.$list = null;
        this.$prompt = null;
        this.list = [];
        this.selectItems = [];
        this._templates = {
            left: '',
            icon: '',
            text: '',
            right: '',
            item: '',
        };
        this._templateInits = {
            left(elem) { },
            icon(elem) { },
            text(elem) { },
            right(elem) { },
            item(elem) { },
        };
        this._renders = {
            left(elem, data) { },
            icon(elem, data) { },
            text(elem, data) { },
            right(elem, data) { },
            item(elem, data) { },
        };
        // 
        this._lineHeight = 24;
        // 
        this._indent = 20;
        // 
        this._tree = {
            index: 0,
            fold: true,
            indent: 0,
            showArrow: true,
            detail: {},
            parent: null,
            children: [],
        };
        this._firstIndex = 0;
        this.attachShadow({ mode: 'open' });
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$style = this.shadowRoot.querySelector('.style');
        this.$content = this.shadowRoot.querySelector('.content');
        this.$fixed = this.shadowRoot.querySelector('.fixed');
        this.$list = this.shadowRoot.querySelector('.list');
        this.$prompt = this.shadowRoot.querySelector('.prompt');
        // 
        this.$content.addEventListener('scroll', () => {
            this.render();
        });
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'disabled',
            'readonly',
            'invalid',
            'path',
            'line-height',
            'css',
            'droppable',
        ];
    }
    /**
     * Attribute 
     * @param attr
     * @param oldValue
     * @param newValue
     */
    attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            case 'line-height':
                this._lineHeight = newValue ? parseInt(newValue) : 0;
                this.render();
                break;
            case 'indent':
                this._indent = newValue ? parseInt(newValue) : 0;
                this.render();
                break;
        }
    }
    get lineHeight() {
        return this._lineHeight;
    }
    set lineHeight(lineHeight) {
        this.setAttribute('line-height', lineHeight + '');
    }
    get indent() {
        return this._indent;
    }
    set indent(indent) {
        this.setAttribute('indent', indent + '');
    }
    get css() {
        return this.$style.innerHTML;
    }
    set css(css) {
        this.$style.innerHTML = css;
    }
    set tree(tree) {
        tree = tree || [];
        this._tree.children = tree;
        tree.forEach((item) => {
            item.parent = this._tree;
        });
        this.list = treeToList(tree);
        this.render();
    }
    get tree() {
        return this._tree.children;
    }
    /**
     *  template 
     * @param type
     * @param template
     */
    setTemplate(type, template) {
        this._templates[type] = template;
    }
    /**
     * 
     * @param type
     * @param handle
     */
    setTemplateInit(type, handle) {
        this._templateInits[type] = handle;
    }
    /**
     * 
     * @param type
     * @param handle
     */
    setRender(type, handle) {
        this._renders[type] = handle;
    }
    /**
     * 
     * @param item
     * */
    select(item) {
        if (!item) {
            return;
        }
        if (this.selectItems.includes(item)) {
            return;
        }
        this.selectItems.push(item);
    }
    /**
     * 
     * @param item
     * */
    unselect(item) {
        if (!item) {
            return;
        }
        const index = this.selectItems.indexOf(item);
        if (index === -1) {
            return;
        }
        this.selectItems.splice(index, 1);
    }
    /**
     * 
     * */
    clear() {
        this.selectItems = [];
    }
    /**
     * 
     * 
     * 
     * @param item
     */
    positioning(item) {
        if (!item) {
            return;
        }
        const top = item.index * this._lineHeight;
        if (top < this.$content.scrollTop) {
            this.$content.scrollTop = top;
        }
        else {
            const bottom = top + this._lineHeight;
            if (bottom - this.$content.clientHeight > this.$content.scrollTop) {
                this.$content.scrollTop = bottom - this.$content.clientHeight;
            }
        }
        while (item.parent) {
            item.parent.fold = false;
            item = item.parent;
        }
        // eslint-disable-next-line no-self-assign
        this.tree = this.tree;
    }
    /**
     * 
     * @param item
     */
    prompt(item) {
        if (!item) {
            this.$prompt.setAttribute('hidden', '');
            return;
        }
        this.$prompt.removeAttribute('hidden');
        this.$prompt.style.transform = `translateY(${item.index * this._lineHeight}px)`;
        let next = this.list[item.index + 1];
        while (next && next.indent > item.indent) {
            next = this.list[next.index + 1];
        }
        if (next) {
            this.$prompt.style.height = (next.index - item.index) * this._lineHeight + 'px';
        }
        else {
            this.$prompt.style.height = this._lineHeight + 'px';
        }
    }
    /**
     * 
     * @param item
     */
    collapse(item) {
        let children;
        if (!item) {
            children = this.tree;
        }
        else {
            children = [item];
        }
        treeForEach(children, (child) => {
            child.fold = true;
        });
        // eslint-disable-next-line no-self-assign
        this.tree = this.tree;
    }
    /**
     * 
     * @param item
     */
    expand(item) {
        let children;
        if (!item) {
            children = this.tree;
        }
        else {
            children = [item];
        }
        treeForEach(children, (child) => {
            child.fold = false;
        });
        if (item) {
            item.fold = false;
            while (item.parent) {
                item.parent.fold = false;
                item = item.parent;
            }
        }
        // eslint-disable-next-line no-self-assign
        this.tree = this.tree;
    }
    /**
     * 
     * @param force
     */
    render() {
        cancelAnimationFrame(this._nextRender);
        this._nextRender = requestAnimationFrame(() => {
            this._render();
        });
    }
    _render() {
        const firstIndex = this._firstIndex = Math.max(Math.floor(this.$content.scrollTop / this._lineHeight), 0);
        //  item 
        const length = Math.ceil(this.clientHeight / this._lineHeight);
        const end = firstIndex + length + 1;
        this.$list.style.transform = `translateY(${firstIndex * this._lineHeight}px)`;
        // 
        this.$fixed.style.height = this.list.length * this._lineHeight + 'px';
        let offsetIndex = 0;
        for (let i = firstIndex; i < end; i++) {
            const data = this.list[i];
            if (data) {
                let $div = this.$list.children[offsetIndex++];
                if (!$div) {
                    // div  dom 
                    $div = document.createElement('ui-drag-item');
                    $div.classList.add('item');
                    $div.innerHTML = `
                        <div class="left">${this._templates.left || ''}</div>
                        <div class="section">
                            <div class="arrow">
                                <ui-icon value="arrow-triangle"></ui-icon>
                            </div>
                            ${this._templates.icon ? '<div class="icon">' + this._templates.icon + '</div>' : ''}
                            <div class="text">${this._templates.text || ''}</div>
                        </div>
                        <div class="right">${this._templates.right || ''}</div>
                    `;
                    $div.$section = $div.querySelector('.section');
                    $div.$left = $div.querySelector('.left');
                    $div.$arrow = $div.querySelector('.arrow');
                    $div.$icon = $div.querySelector('.icon');
                    $div.$text = $div.querySelector('.text');
                    $div.$right = $div.querySelector('.right');
                    $div.$arrow.addEventListener('mousedown', (event) => {
                        event.stopPropagation();
                        event.preventDefault();
                    });
                    $div.$arrow.addEventListener('mouseup', (event) => {
                        event.stopPropagation();
                        event.preventDefault();
                    });
                    $div.$arrow.addEventListener('dblclick', (event) => {
                        event.stopPropagation();
                        event.preventDefault();
                    });
                    $div.$arrow.addEventListener('click', (event) => {
                        event.stopPropagation();
                        event.preventDefault();
                        $div.data.fold = !$div.data.fold;
                        // eslint-disable-next-line no-self-assign
                        this.tree = this.tree;
                    });
                    this._templateInits.item && this._templateInits.item($div);
                    this._templateInits.left && this._templateInits.left($div.$left);
                    this._templateInits.icon && this._templateInits.icon($div.$icon);
                    this._templateInits.text && this._templateInits.text($div.$text);
                    this._templateInits.right && this._templateInits.right($div.$right);
                    this.$list.appendChild($div);
                }
                $div.removeAttribute('hidden');
                $div.style.height = this._lineHeight + 'px';
                if (data.fold) {
                    $div.setAttribute('fold', '');
                }
                else {
                    $div.removeAttribute('fold');
                }
                if (data.showArrow === false) {
                    $div.$arrow.setAttribute('hidden', '');
                }
                else {
                    $div.$arrow.removeAttribute('hidden');
                }
                if (this.selectItems.includes(data)) {
                    $div.setAttribute('selected', '');
                }
                else {
                    $div.removeAttribute('selected');
                }
                $div.data = data;
                // @ts-ignore
                $div.$left && ($div.$left.data = data);
                // @ts-ignore
                $div.$icon && ($div.$icon.data = data);
                // @ts-ignore
                $div.$text && ($div.$text.data = data);
                // @ts-ignore
                $div.$right && ($div.$right.data = data);
                this._renders.item && this._renders.item($div, data);
                this._renders.left && this._renders.left($div.$left, data);
                this._renders.icon && this._renders.icon($div.$icon, data);
                this._renders.text && this._renders.text($div.$text, data);
                this._renders.right && this._renders.right($div.$right, data);
                $div.$section.style.paddingLeft = data.indent * this._indent + 'px';
            }
            else {
                const div = this.$list.children[offsetIndex++];
                div && div.setAttribute('hidden', '');
            }
        }
        // 
        while (offsetIndex < this.$list.children.length) {
            this.$list.children[offsetIndex++].setAttribute('hidden', '');
        }
    }
}
function treeForEach(tree, handle) {
    function step(children, indent) {
        children.forEach((item) => {
            handle(item);
            if (item.showArrow && item.children) {
                step(item.children, indent + 1);
            }
        });
    }
    step(tree, 0);
}
/**
 *  tree  list
 * @param tree
 */
function treeToList(tree) {
    const list = [];
    function step(children, indent, parent) {
        children.forEach((item) => {
            item.index = list.length;
            item.indent = indent;
            if (parent) {
                item.parent = parent;
            }
            list.push(item);
            if (item.fold !== true && item.children) {
                step(item.children, indent + 1, item);
            }
        });
    }
    step(tree, 0);
    return list;
}
