import { Grid } from '../curve-editor/lib/grid';
import { smoothScale } from '../curve-editor/lib/utils';
import { EventEmitter } from 'events';
const defaultConfig = {
    preci: 2,
    minScale: 0,
};
/** 
 *
 */
export class GridCtrl extends EventEmitter {
    constructor(canvas, config) {
        super();
        this._readonly = false;
        this.eventHandlers = null;
        if (!config.axisXConfig && config.showX) {
            config.axisXConfig = JSON.parse(JSON.stringify(defaultConfig));
        }
        if (!config.axisYConfig && config.showY) {
            config.axisYConfig = JSON.parse(JSON.stringify(defaultConfig));
        }
        this._grid = new Grid(canvas, config);
        this._config = config;
        this._readonly = !!config.readonly;
    }
    init() {
        // 
        if (!this._readonly && !this.eventHandlers) {
            this.eventHandlers = {
                wheel: this._onWheel.bind(this),
            };
            this._grid.canvas.addEventListener('wheel', this.eventHandlers.wheel);
        }
        this.render();
    }
    resetConfig(config) {
        this._grid = new Grid(this._grid.canvas, config);
        this.render();
    }
    render() {
        this._grid.render();
    }
    _onWheel(event) {
        event.stopPropagation();
        event.preventDefault();
        let changed = true;
        if (Math.abs(event.deltaX) > Math.abs(event.deltaY)) {
            this.translate(event.deltaY, 0);
        }
        else {
            changed = this.scaleAt(event.offsetX, event.clientY, -event.deltaY);
        }
        if (changed) {
            this.emit('change');
            this.render();
        }
    }
    /**
     * 
     * @returns {start, end}
     */
    getValueRang() {
        return {
            start: this.pixelToValueX(0),
            end: this.pixelToValueX(this._grid.canvas.width),
        };
    }
    /**
     * 
     * @param x
     * @param y
     * @param scale
     */
    scaleAt(x, y, scale) {
        let res = true;
        if (y > 0 && this._grid.yAxisScaleAt) {
            const { yAxisScale } = this._grid;
            this._grid.yAxisScaleAt(y, smoothScale(scale, yAxisScale));
            res = res && yAxisScale !== this._grid.yAxisScale;
        }
        if (this._grid.xAxisScaleAt) {
            const { xAxisScale } = this._grid;
            this._grid.xAxisScaleAt(x, smoothScale(scale, xAxisScale));
            res = res && xAxisScale !== this._grid.xAxisScale;
        }
        return res;
    }
    /**
     * 
     * @param x
     * @param y
     */
    translate(deltaPixelX, deltaPixelY) {
        const res = {
            x: 0,
            y: 0,
        };
        if (deltaPixelX && this._grid.transferX) {
            this._grid.transferX(deltaPixelX);
        }
        if (deltaPixelY && this._grid.transferY) {
            this._grid.transferY(deltaPixelY);
        }
    }
    /**
     *  canvas 
     * @param val
     */
    valueToPixelX(val) {
        if (!this._grid.showX) {
            return val;
        }
        if (!this._grid.xFormatReverse) {
            return this._grid.valueToPixelX(val);
        }
        // TODO  grid 
        const gridX = this._grid.xFormatReverse(val);
        return this._grid.valueToPixelX(gridX);
    }
    /**
     * canvas 
     * @param val
     * @returns
     */
    pixelToValueX(val) {
        if (!this._grid.showX) {
            return val;
        }
        return this._grid.xFormat(this._grid.pixelToValueX(val));
    }
}
