'use strict';
import { LayoutBase } from '../base';
import { translate } from '../../../utils/utils';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const renderMap = {};
const instanceArray = [];
const resizeGroup = {};
/**
 *  attribute  property
 */
export class Prop extends LayoutBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        // render  renderInfo  null
        this.renderInfo = null;
        this.resizeFlag = false;
        this.resizeGroupName = null;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$wrap = this.shadowRoot.querySelector('.wrap');
        this.$message = this.shadowRoot.querySelector('ui-label.message');
        this.$label = this.shadowRoot.querySelector('.label');
        this.$icon = this.shadowRoot.querySelector('.icon');
        this.$content = this.shadowRoot.querySelector('.content');
        this.$resizeBar = this.shadowRoot.querySelector('.resize-bar');
        this.$content.$root = this;
    }
    /**
     * 
     * @param type
     * @param handle
     */
    static registerRender(type, handle) {
        renderMap[type] = handle;
    }
    /**
     * 
     * @param type
     */
    static hasRender(type) {
        return !!renderMap[type];
    }
    get resize() {
        return this.resizeFlag;
    }
    set resize(val) {
        if (val) {
            this.setAttribute('resize', '');
            this.$resizeBar.removeAttribute('hidden');
        }
        else {
            this.removeAttribute('resize');
            this.$resizeBar.setAttribute('hidden', '');
        }
        this.resizeFlag = Boolean(val);
    }
    get resizeGroup() {
        return this.resizeGroupName;
    }
    set resizeGroup(val) {
        if (val !== null) {
            this.setAttribute('resize-group', val);
            this.catheResizeByName(val);
        }
        else {
            this.removeAttribute('resize-group');
        }
        this.resizeGroupName = val;
    }
    get message() {
        return this.getAttribute('message');
    }
    set message(val) {
        if (val) {
            this.setAttribute('message', val);
        }
        else {
            this.removeAttribute('message');
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'render',
            'resize',
            'resize-group',
            'message',
        ];
    }
    /**
     * @description attribute 
     * @param attr 
     * @param oldVal 
     * @param newVal 
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        switch (attr) {
            // 
            case 'render':
                try {
                    const info = JSON.parse(newVal);
                    this.render(info);
                }
                catch (error) {
                    console.error(error);
                }
                break;
            case 'resize':
                const resizeValBoole = (newVal !== null && newVal !== 'false');
                this.resizeFlag = resizeValBoole;
                if (resizeValBoole) {
                    this.initResize();
                    this.$resizeBar.removeAttribute('hidden');
                }
                else {
                    this.$resizeBar.setAttribute('hidden', '');
                }
                break;
            case 'resize-group':
                if (oldVal === null) {
                    if (newVal !== null) {
                        this.resizeGroupName = newVal;
                        this.catheResizeByName(newVal);
                    }
                }
                else {
                    const oldResizeGroup = resizeGroup[oldVal];
                    resizeGroup[oldVal] = oldResizeGroup.filter((v) => v !== this);
                    this.resizeGroupName = newVal;
                    this.catheResizeByName(newVal);
                }
                break;
            case 'message':
                this.$message.value = newVal;
                break;
            default:
                break;
        }
    }
    /**
     *  resize
     */
    initResize() {
        this.$resizeBar.onmousedown = (e) => {
            const startX = e.clientX;
            this.$resizeBar.left = this.$resizeBar.offsetLeft;
            // 
            document.onmousemove = (e) => {
                const endX = e.clientX;
                // endX - startX= $resizeBar.left +  = 
                let moveLen = this.$resizeBar.left + (endX - startX);
                //  -  = 
                const maxT = this.$wrap.clientWidth - this.$resizeBar.offsetWidth;
                // 32px
                if (moveLen < 32) {
                    moveLen = 32;
                }
                if (moveLen > maxT - 150) {
                    moveLen = maxT - 150;
                }
                // 150px
                // 
                this.$resizeBar.style.left = moveLen;
                this.$label.style.width = moveLen + 'px';
                this.$content.style.width = (this.$wrap.clientWidth - moveLen - 10) + 'px';
                //  resize name  prop
                if (this.resizeGroupName) {
                    resizeGroup[this.resizeGroupName]?.forEach((el) => {
                        const hasResize = el.hasAttribute('resize');
                        if (hasResize) {
                            el.shadowRoot.querySelector('.resize-bar').style.left = moveLen;
                            el.shadowRoot.querySelector('.label').style.width = moveLen + 'px';
                            el.shadowRoot.querySelector('.content').style.width = (this.$wrap.clientWidth - moveLen - 10) + 'px';
                        }
                    });
                }
            };
            // 
            document.onmouseup = (evt) => {
                document.onmousemove = null;
                document.onmouseup = null;
                //  ReleaseCapture()
                this.$resizeBar.releaseCapture && this.$resizeBar.releaseCapture();
            };
            // 
            this.$resizeBar.setCapture && this.$resizeBar.setCapture();
            return false;
        };
    }
    /**
    * @description  resize-group  prop
    * @param name resize group name
    */
    catheResizeByName(name) {
        if (name !== null) {
            if (!resizeGroup[name]) {
                resizeGroup[name] = [];
            }
            resizeGroup[name].push(this);
        }
    }
    /**
     * 
     * @param info
     */
    render(info) {
        if (!info && this.dump) {
            info = this.dump;
        }
        const type = this.getAttribute('type') || 'ui';
        if (renderMap[type]) {
            if (!this.renderInfo) {
                this.initRender(info, type);
            }
            else {
                this.updateRender(info, type);
            }
        }
    }
    /**
     * @description 
     * @param info
     * @param type
     */
    initRender(info, type) {
        const { panel, panelThis } = this._initRenderInfo(renderMap, type);
        panel.ready && panel.ready.call(panelThis);
        panel.update && panel.update.call(panelThis, info);
    }
    /**
     * @description 
     * @param info
     * @param type
     */
    updateRender(info, type) {
        const panel = renderMap[type];
        if (this.renderInfo.panel === panel) {
            panel.update.call(this.renderInfo.panelThis, info);
            return;
        }
        if (this.renderInfo.type === type) {
            this.renderInfo.panel.update.call(this.renderInfo.panelThis, info);
        }
    }
    /**
     * @description  renderInfo
     * @param renderMap
     * @param type
     * */
    _initRenderInfo(renderMap, type) {
        const panel = renderMap[type];
        let html = '';
        const panelThis = {
            $this: this,
            $: {},
        };
        if (panel.style) {
            const $style = this.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = panel.style;
        }
        if (panel.template) {
            html += panel.template;
        }
        if (html) {
            this.innerHTML = html;
        }
        if (panel.methods) {
            for (const key in panel.methods) {
                panelThis[key] = panel.methods[key];
            }
        }
        if (panel.$) {
            for (const key in panel.$) {
                const $elemList = this.querySelectorAll(panel.$[key]);
                panelThis.$[key] = $elemList.length > 1 ? $elemList : $elemList[0] || null;
            }
        }
        if (panel.listeners) {
            for (const key in panel.listeners) {
                this.addEventListener(key, (event) => {
                    panel.listeners[key].call(panelThis, event);
                });
                this.shadowRoot.addEventListener(key, (event) => {
                    panel.listeners[key].call(panelThis, event);
                });
            }
        }
        return this.renderInfo = {
            panel,
            panelThis,
            type,
        };
    }
    /**
     *  prop  label 
     * name  tooltip
     */
    setLabel($label, dump) {
        if (!dump) {
            dump = this.dump;
        }
        if (!$label || !dump) {
            return;
        }
        $label.innerHTML = this.getName(dump);
        // @TODO 
        if (dump.tooltip) {
            let tooltip = dump.tooltip;
            // @ts-ignore
            if (translate) {
                // @ts-ignore
                tooltip = translate('ENGINE.' + tooltip);
                //  ENGINE 
                if (!tooltip || tooltip === dump.tooltip) {
                    // @ts-ignore
                    tooltip = translate(dump.tooltip) || dump.tooltip;
                }
            }
            $label.setAttribute('tooltip', tooltip);
        }
        else {
            $label.removeAttribute('tooltip');
        }
    }
    /**
     *  dump  name 
     */
    getName(dump) {
        if (!dump) {
            dump = this.dump;
        }
        if (!dump) {
            return '';
        }
        if (dump.displayName) {
            return dump.displayName;
        }
        let name = dump.name || '';
        name = name.trim().replace(/^\S/, (str) => str.toUpperCase());
        name = name.replace(/_/g, ' ');
        name = name.replace(/ \S/g, (str) => ` ${str.toUpperCase()}`);
        // 
        name = name.replace(/([a-z])([A-Z])/g, '$1 $2');
        return name.trim();
    }
    /**
     * 
     */
    setReadonly(dump, element) {
        if (!dump) {
            dump = this.dump;
        }
        if (dump && dump.readonly) {
            element.setAttribute('disabled', '');
        }
        else {
            element.removeAttribute('disabled');
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        //  resize-group  prop
        if (this.resizeFlag) {
            this.initResize();
            const resizeGroupName = this.getAttribute('resize-group');
            if (resizeGroupName !== null) {
                this.catheResizeByName(resizeGroupName);
            }
        }
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
}
/**
 *  ui  label  template
 * 
 */
Prop.registerRender('ui', {
    ready() { },
    update(info) {
        const elem = this.$this;
        if (elem.hasAttribute('label')) {
            let $label = elem.querySelector('*[slot=label]');
            if (!$label) {
                $label = document.createElement('ui-label');
                $label.setAttribute('slot', 'label');
                elem.appendChild($label);
            }
            $label.setAttribute('value', elem.getAttribute('label'));
        }
        let $content = elem.querySelector('*[slot=content]');
        if (!$content) {
            $content = document.createElement(info.ui);
            $content.setAttribute('slot', 'content');
        }
        if (info?.attributes) {
            for (const key in info.attributes) {
                const value = $content.getAttribute(key);
                if (value !== info.attributes[key]) {
                    $content.setAttribute(key, info.attributes[key]);
                }
            }
        }
        if (info?.tooltip) {
            if (translate) {
                elem.setAttribute('title', translate(info.tooltip));
            }
            else {
                elem.setAttribute('title', info.tooltip);
            }
        }
        else {
            elem.removeAttribute('title');
        }
        if (info?.ui === 'ui-select' && info?.items && Array.isArray(info.items)) {
            let html = '';
            info.items.forEach((item) => {
                let label = item.label || item.value;
                if (translate) {
                    label = translate(label);
                }
                html += `<option value="${item.value}">${label}</option>`;
            });
            $content.innerHTML = html;
        }
        if ($content.parentElement !== elem) {
            elem.appendChild($content);
        }
    },
});
