'use strict';
import { Base } from '../base';
/**
 * 
 *
 * inline
 *
 * 
 *   confirm 
 *
 * 
 *   disabled
 */
export class OperationBase extends Base {
    /**
     * 
     */
    constructor() {
        super();
        // 
        this.addEventListener('focus', () => {
            this.setAttribute('focused', '');
        });
        this.addEventListener('blur', () => {
            this.removeAttribute('focused');
        });
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', val);
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    get focused() {
        return this.hasAttribute('focused');
    }
    set focused(val) {
        if (val) {
            this.setAttribute('focused', val);
        }
        else {
            this.removeAttribute('focused');
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'focused',
            'disabled',
            'readonly',
        ];
    }
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'disabled':
                this._onDisabledChanged(n !== null);
                break;
            case 'focused':
                this._onFocusedChanged(n !== null);
                break;
            default:
                break;
        }
    }
    // ////////////////////
    // 
    /**
     *  disabled 
     * @param val
     */
    _onDisabledChanged(val) { }
    /**
     *  focused 
     * @param event
     */
    _onFocusedChanged(val) { }
}
