'use strict';
const fs = require('fs');
import { OperationBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
export class Button extends OperationBase {
    /**
     * 
     */
    constructor() {
        super();
        this._outline = false;
        this._transparent = false;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$span = this.shadowRoot.querySelector('span');
    }
    /**
     * @description  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'type',
            'outline',
            'transparent',
        ];
    }
    /**
     * @description attribute  callback
     * @param attr
     * @param oldVal
     * @param newVal
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'type':
                this._onTypeChanged(newVal);
                break;
            case 'outline':
                this._onOutlineChanged(newVal);
                break;
            case 'transparent':
                this._onOutlineChanged(newVal);
                break;
            default:
                break;
        }
    }
    /**
     * @description 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        this.$span.setAttribute('tabindex', '0');
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        // 
        this.addEventListener('mousedown', this._onMouseDown);
        // 
        this.addEventListener('keyup', this._onKeyUp);
        this.addEventListener('keydown', this._onKeyDown);
    }
    /**
     * @description 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get pressed() {
        return this.getAttribute('pressed') !== null;
    }
    set pressed(bool) {
        bool = !!bool;
        if (bool) {
            this.setAttribute('pressed', '');
        }
        else {
            this.removeAttribute('pressed');
        }
    }
    get type() {
        return this._type;
    }
    set type(val) {
        if (val) {
            this.setAttribute('type', val);
        }
        else {
            this.removeAttribute('type');
        }
        this._type = val;
    }
    get outline() {
        return this._outline;
    }
    set outline(val) {
        this._outline = Boolean(val);
        if (this._outline) {
            this.setAttribute('outline', '');
        }
        else {
            this.removeAttribute('outline');
        }
    }
    get transparent() {
        return this._transparent;
    }
    set transparent(val) {
        this._transparent = Boolean(val);
        if (this._transparent) {
            this.setAttribute('transparent', '');
        }
        else {
            this.removeAttribute('transparent');
        }
    }
    // ////////////////////
    // 
    /**
     * @description  button type
     * @param type
     */
    _onTypeChanged(type) {
        switch (type) {
            case 'primary':
                break;
            case 'info':
                break;
            case 'warning':
                break;
            case 'success':
                break;
            case 'danger':
                break;
            case 'border':
                break;
            case 'icon':
                this._resolveIconWidthHeight();
                break;
            default:
                break;
        }
    }
    _resolveIconWidthHeight() {
        requestAnimationFrame(() => {
            const parentLineHeight = getComputedStyle(this).lineHeight;
            this.style.width = this.style.height = parentLineHeight ?? 0;
        });
    }
    /**
     * @description  outline
     */
    _onOutlineChanged(val) {
        this._outline = val !== null;
    }
    /**
     * @description  document  up  pressed 
     *
     */
    _onMouseDown() {
        // disabled 
        if (this.disabled) {
            return;
        }
        // 
        this.pressed = true;
        const mouseUp = () => {
            this.pressed = false;
            // @TODO  change 
            this.dispatch('change');
            this.dispatch('confirm');
            document.removeEventListener('mouseup', mouseUp, true);
        };
        document.addEventListener('mouseup', mouseUp, true);
    }
    /**
     * @description  
     * @param {Event} event
     */
    _onKeyDown(event) {
        event.preventDefault(); // 
        switch (event.keyCode) {
            case 32:
            case 13:
                this.pressed = true;
                break;
        }
    }
    /**
     * @description   press 
     * @param {Event} event
     */
    _onKeyUp(event) {
        switch (event.keyCode) {
            case 32:
            case 13:
                this.pressed = false;
                this.dispatch('change');
                this.dispatch('confirm');
                break;
        }
    }
}
