'use strict';
const fs = require('fs');
import { EditBase } from '../base';
import { translate } from '../../../utils/utils';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
export class Input extends EditBase {
    // //////////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.compositionStart = false;
        this._staging = null;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${HTML}`;
        this.$input = this.shadowRoot.querySelector('input');
        this.$clearSpan = this.shadowRoot.querySelector('span');
        this.$input.$root = this.$clearSpan.$root = this;
        //  input  esc 
        this._staging = null;
    }
    /**
     * 
     *
     * @param {string} src est
     * @returns {undefined}
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return false;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
        return true;
    }
    get value() {
        return this.getAttribute('value') || '';
    }
    set value(val) {
        this.setAttribute('value', val);
    }
    get password() {
        return this.hasAttribute('password');
    }
    set password(bool) {
        if (bool) {
            this.setAttribute('password', '');
        }
        else {
            this.removeAttribute('password');
        }
    }
    get placeholder() {
        return this.getAttribute('placeholder') || '';
    }
    set placeholder(val) {
        val += '';
        this.setAttribute('placeholder', val);
    }
    get 'auto-select'() {
        return !(this.getAttribute('auto-select') === 'false');
    }
    set 'auto-select'(bool) {
        if (bool) {
            this.$input.setAttribute('auto-select', '');
        }
        else {
            this.$input.setAttribute('auto-select', 'false');
        }
    }
    get 'show-clear'() {
        return this.getAttribute('show-clear') !== null;
    }
    set 'show-clear'(val) {
        if (val) {
            this.setAttribute('show-clear', '');
        }
        else {
            this.removeAttribute('show-clear');
        }
    }
    /**
     * 
     */
    _onConnected() {
        super._onConnected();
        // 
        instanceArray.push(this);
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
        // 
        this.$input.addEventListener('focus', this._onInputFocus);
        this.$input.addEventListener('blur', this._onInputBlur);
        this.$input.addEventListener('input', this._onInputChange);
        this.$input.addEventListener('compositionstart', this._onInputCompositionStart);
        this.$input.addEventListener('compositionend', this._onInputCompositionEnd);
        this.$clearSpan.addEventListener('click', this._onClear, true);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.$input.removeEventListener('focus', this._onInputFocus);
        this.$input.removeEventListener('blur', this._onInputBlur);
        this.$input.removeEventListener('input', this._onInputChange);
        this.$input.removeEventListener('compositionstart', this._onInputCompositionStart);
        this.$input.removeEventListener('compositionend', this._onInputCompositionEnd);
        this.$clearSpan.removeEventListener('click', this._onClear, true);
    }
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'placeholder',
            'password',
            'show-clear',
            'auto-select',
        ];
    }
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'password':
                this._onPasswordChanged(newVal !== null);
                break;
            case 'show-clear':
                this._onShowClear(newVal !== null);
                break;
            case 'placeholder':
                this._onPlaceholderChanged(newVal);
                break;
            default:
                break;
        }
    }
    /**
     * input 
     */
    _onInputChange() {
        const $elem = this;
        //  change  value 
        if ($elem.$root.compositionStart) {
            return;
        }
        //  value
        $elem.$root.value = this.value;
        //  change 
        $elem.$root.dispatch('change');
    }
    /**
     * 
     * @param event
     */
    _onFocus(event) {
        super._onFocus(event);
        // 
        if (this.disabled) {
            return;
        }
        this.$input.focus();
    }
    _confirm(ignore) {
        const inputFocused = this._staging !== null;
        //  input 
        this._onInputChange.call(this.$input);
        //  confirm 
        if (this._staging !== null && this._staging !== this.value) {
            this._staging = this.value;
            this.dispatch('change');
            this.dispatch('confirm');
        }
        if (!ignore) {
            if (inputFocused) {
                this.focus();
            }
            else {
                this.$input.focus();
            }
        }
    }
    /**
     * @description input 
     */
    _onInputBlur() {
        const $elem = this;
        if ($elem.$root._staging !== this.value) {
            $elem.$root._confirm(true);
        }
        // 
        $elem.$root._staging = null;
    }
    /**
     * @description input 
     */
    _onInputFocus() {
        const $elem = this;
        // 
        if ($elem.disabled) {
            return;
        }
        const autoSelect = $elem.$root.getAttribute('auto-select') !== 'false';
        // 
        $elem.$root._staging = $elem.value;
        if (autoSelect) {
            // 
            $elem.select();
        }
    }
    /**
     * @description  focused 
     * @param { boolean } val
     */
    _onFocusedChanged(val) {
        if (val) {
            this.$input.focus();
        }
    }
    /**
     * @description ,value
     */
    _onClear() {
        const $elem = this;
        this.style.display = 'none';
        $elem.$root._clear();
    }
    /**
     * @description  input value 
     * */
    _clear() {
        if (this.value === '') {
            return;
        }
        this.value = '';
        this.$input.value = '';
        this.dispatch('change');
        this.dispatch('confirm');
        this.$input.focus();
    }
    /**
     * @description  value 
     * */
    _onShowClear(val) {
        this.$clearSpan.style.display = (this.value !== '' && val) ? 'inline-block' : 'none';
    }
    /**
     * @description password
     *
     * */
    _onPasswordChanged(val) {
        this.$input.type = val ? 'password' : 'text';
    }
    /**
     *  invalid 
     * @param val
     */
    _onInvalidChanged(val) {
        if (val) {
            this.$input.value = '-';
        }
    }
    /**
     *  value 
     * @param val
     */
    _onValueChanged(val) {
        this.invalid = false;
        // this.$input.value = val;
        const clearAttr = this.hasAttribute('show-clear');
        if (clearAttr) {
            this._onShowClear(clearAttr);
        }
        //  input  value 
        if (this._staging !== null) {
            return;
        }
        this.$input.value = val;
    }
    /**
     *  disabled 
     * @param val
     */
    _onDisabledChanged(val) {
        this.$input.disabled = val;
    }
    /**
     *
    * @param { string } val placeholder
    * */
    _onPlaceholderChanged(val) {
        this.$input.placeholder = translate(val) ?? '';
    }
    /**
     * @description 
     **/
    static _updateTranslate() {
        for (const el of instanceArray) {
            el._onPlaceholderChanged(el.getAttribute('placeholder'));
        }
    }
    /**
     *  readonly 
     * @param val
     */
    _onReadonlyChanged(val) {
        this.$input.readOnly = val ?? false;
    }
    _cancel() {
        const inputFocused = this._staging !== null;
        // 
        if (this._staging !== null && this._staging !== this.value) {
            this.value = this.$input.value = this._staging || '';
            this.dispatch('change');
            this.dispatch('cancel');
            this.dispatch('confirm');
        }
        if (inputFocused) {
            this.focus();
        }
    }
    // ////////////////////
    // 
    /**
     * @description 
     * @description  confirm   esc cancel
     * @param {Event} event
     */
    _onKeyDown(event) {
        // 
        if (this.disabled || this.readonly) {
            return;
        }
        switch (event.keyCode) {
            // 
            case 13:
                this._confirm();
                break;
            // esc
            case 27:
                this._cancel();
                break;
        }
    }
    _onInputCompositionStart() {
        const $elem = this;
        $elem.$root.compositionStart = true;
    }
    _onInputCompositionEnd(event) {
        const $elem = this;
        $elem.$root.compositionStart = false;
        $elem.$root._onInputChange.call($elem, event);
    }
}
