'use strict';
import STYLE from './panel.less';
export class Panel extends HTMLElement {
    /**
     * 
     */
    constructor() {
        super();
        // panel  js 
        this.panel = null;
        // 
        this.panelObject = null;
        this.__listeners = {};
        this.connected = false;
        this._injectionStyle = '';
    }
    set config(config) {
        this._setPanel(config);
    }
    _setPanel(panel) {
        // 
        if (this.__listeners) {
            for (const key in this.__listeners) {
                this.removeEventListener(key, this.__listeners[key]);
                this.shadowRoot.removeEventListener(key, this.__listeners[key]);
            }
        }
        if (this.panel && this.panel.close) {
            this.panel.close.call(this.panelObject);
        }
        this.panel = panel;
        this._refresh();
    }
    connectedCallback() {
        this.connected = true;
        if (this.panel && this.panel.ready) {
            this.panel.ready.call(this.panelObject);
        }
    }
    disconnectedCallback() {
        this.connected = false;
        if (this.panel && this.panel.close) {
            this.panel.close.call(this.panelObject);
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'src',
        ];
    }
    // 
    async attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            case 'src':
                if (newValue) {
                    if (oldValue !== newValue) {
                        try {
                            // @ts-ignore
                            this._setPanel(__non_webpack_require__(`${newValue}`));
                        }
                        catch (error) {
                            console.error(error);
                            this._setPanel(null);
                        }
                    }
                }
                else {
                    this._setPanel(null);
                }
                break;
            default:
                return;
        }
    }
    /**
     * 
     * @param eventName 
     */
    dispatch(eventName, ...args) {
        const event = document.createEvent('HTMLEvents');
        event.initEvent(eventName, true, true);
        event.args = args;
        this.dispatchEvent(event);
    }
    /**
     *  update 
     * @param args
     */
    update(...args) {
        this.panel?.update && this.panel?.update.call(this.panelObject, ...args);
    }
    /**
     * 
     * @param style
     */
    injectionStyle(style) {
        this._injectionStyle = style;
        if (this.shadowRoot) {
            const $style = this.shadowRoot.querySelector('#injection-style');
            $style.innerHTML = style;
        }
    }
    /**
     * 
     */
    async canClose() {
        if (this.panel && this.panel.beforeClose) {
            const allow = await this.panel.beforeClose.call(this.panelObject);
            // @ts-ignore
            if (allow === false) {
                return false;
            }
        }
        return true;
    }
    /**
     *  method 
     * @param method
     * @param args
     */
    async callMethod(method, ...args) {
        if (this.panel && this.panel.methods && this.panel.methods[method]) {
            return await this.panel.methods[method].call(this.panelObject, ...args);
        }
    }
    // //////////////////////////////////
    // 
    _refresh() {
        const inline = this.getAttribute('inline') !== null;
        if (!inline && !this.shadowRoot) {
            this.attachShadow({ mode: 'open' });
        }
        const $root = inline ? this : this.shadowRoot;
        if (!this.panel) {
            $root.innerHTML = ``;
            return;
        }
        if (!inline) {
            $root.innerHTML = `
                    <style>${STYLE}</style>
                    <style>${this.panel.style || ''}</style>
                    <style id="injection-style"></style>
                    ${this.panel.template || ''}`;
            if (!inline && this._injectionStyle) {
                const $style = this.shadowRoot.querySelector('#injection-style');
                $style.innerHTML = this._injectionStyle;
            }
        }
        else {
            $root.innerHTML = this.panel.template || '';
        }
        this.panelObject = {
            $this: this,
            dispatch: this.dispatch.bind(this),
        };
        if (this.panel.$) {
            const domObject = {};
            for (const key in this.panel.$) {
                const selector = this.panel.$[key];
                if (typeof selector === 'string') {
                    domObject[key] = $root.querySelector(selector);
                }
            }
            this.panelObject.$ = domObject;
        }
        if (this.panel.listeners) {
            for (const key in this.panel.listeners) {
                this.__listeners[key] = (event) => {
                    if (this.panel?.listeners) {
                        this.panel.listeners[key].call(this.panelObject, event);
                    }
                };
                this.addEventListener(key, this.__listeners[key]);
                this.shadowRoot.addEventListener(key, this.__listeners[key]);
            }
        }
        if (this.panel.methods && typeof this.panel.methods === 'object') {
            for (const key in this.panel.methods) {
                this.panelObject[key] = this.panel.methods[key];
            }
        }
        if (this.connected && this.panel.ready) {
            this.panel.ready.call(this.panelObject);
        }
    }
}
