'use strict';
import STYLE from './index.less';
import { DisplayBase } from '../base';
import { translate } from '../../../utils/utils';
const Hljs = require('highlight.js');
const { Remarkable } = require('remarkable');
const { existsSync, readFileSync } = require('fs');
const { extname } = require('path');
const instanceArray = [];
export class Markdown extends DisplayBase {
    /**
     * 
     */
    constructor() {
        super();
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}</style><slot></slot><div class="content"></div>`;
        this.$slot = this.shadowRoot.querySelector('slot');
        this.$content = this.shadowRoot.querySelector('.content');
        this._refresh();
        this.$slot.addEventListener('slotchange', () => {
            this._refresh();
        });
    }
    get src() {
        return this.getAttribute('src');
    }
    set src(val) {
        if (val) {
            this.setAttribute('src', val);
        }
        else {
            this.removeAttribute('src');
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'src',
        ];
    }
    // 
    attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            case 'src':
                if (oldValue === newValue) {
                    return;
                }
                this.readMdFile(newValue);
                break;
            default:
                return;
        }
    }
    /**
     * 
     */
    connectedCallback() {
        // 
        instanceArray.push(this);
    }
    /**
     * 
     */
    disconnectedCallback() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    // //////////////////////////////////
    // 
    /**
     *  src  md 
     */
    readMdFile(file) {
        if (!existsSync(file) || extname(file).toLowerCase() !== '.md') {
            return;
        }
        try {
            const str = readFileSync(file);
            this.innerHTML = str;
        }
        catch (error) {
            console.error(error);
            return;
        }
    }
    _refresh() {
        let template = this.innerHTML;
        if (!template) {
            this.$content.innerHTML = ``;
            return;
        }
        // @ts-ignore
        if (translate) {
            template = translate(template);
        }
        // /
        const results = template.match(/(( |\t)+)[^( |\t)]/);
        if (results && results[1]) {
            template = template.replace(new RegExp('^' + results[1], 'gm'), '');
        }
        const md = new Remarkable({
            html: true,
            highlight(str, lang) {
                if (lang && Hljs.getLanguage(lang)) {
                    try {
                        return Hljs.highlight(lang, str).value;
                    }
                    catch (err) {
                        console.error(`Syntax highlight failed: ${err.message}`);
                    }
                }
                try {
                    return Hljs.highlightAuto(str).value;
                }
                catch (err) {
                    console.error(`Syntax highlight failed: ${err.message}`);
                }
                // use external default escaping
                return '';
            },
        });
        const html = md.render(template);
        this.$content.innerHTML = html;
    }
    /**
     * @description 
     **/
    static _updateTranslate() {
        for (const el of instanceArray) {
            el._refresh();
        }
    }
}
