'use strict';
const fs = require('fs');
const qrcode = require('qrcode');
import { DisplayBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
/**
 *   Attribute
 *
 *   value
 *   size
 */
export class QRCode extends DisplayBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$canvas = this.shadowRoot.querySelector('canvas');
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        return this.getAttribute('value');
    }
    set value(val) {
        if (val) {
            this.setAttribute('value', val);
        }
        else {
            this.removeAttribute('value');
        }
    }
    get size() {
        return parseInt(this.getAttribute('size') || '');
    }
    set size(val) {
        if (val) {
            this.setAttribute('size', String(val));
        }
        else {
            this.removeAttribute('size');
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['value', 'size'];
    }
    /**
     * attribute 
     *
     * o oldValue
     * nnewValue
     */
    attributeChangedCallback(attr, o, n) {
        switch (attr) {
            case 'value':
            case 'size':
                this._updateCanvas();
                break;
        }
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        if (!this.size) {
            this.size = this.clientWidth || 200;
        }
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
    }
    /**
     *  qrcode 
     */
    _updateCanvas() {
        if (!this.value) {
            return;
        }
        qrcode.toCanvas(this.$canvas, this.value, {
            errorCorrectionLevel: 'H',
            maskPattern: 2,
            margin: 1,
            width: this.size,
        });
    }
}
