'use strict';
const fs = require('fs');
const Chroma = require('chroma-js');
const { domUtils } = require('../../../utils/utils');
import { OperationBase } from '../base';
let customStyle = '';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
import HTML from './index.html';
import STYLE from './index.less';
const instanceArray = [];
// 
let panelHandle;
export class Color extends OperationBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$colorWrap = this.shadowRoot.querySelector('.color-wrap');
        this.$color = this.shadowRoot.querySelector('.color');
        this.$alpha = this.shadowRoot.querySelector('.alpha-line');
        this.$alpha.$root = this.$color.$root = this;
    }
    /**
     * 
     * @param {filePath} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        const val = this.getAttribute('value');
        return domUtils.getValidColor(val);
    }
    set value(val) {
        const value = domUtils.getValidColor(val);
        this.setAttribute('value', JSON.stringify(value));
    }
    get invalid() {
        return this.hasAttribute('invalid');
    }
    set invalid(val) {
        if (val) {
            this.setAttribute('invalid', '');
        }
        else {
            this.removeAttribute('invalid');
        }
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'value',
            'path',
            'invalid',
            'focused',
        ];
    }
    /**
     * attribute 
     * @param attr 
     * @param newVal 
     * @param oldVal 
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'value':
                if (oldVal === newVal) {
                    break;
                }
                this._onValueChanged(newVal);
                break;
            case 'invalid':
                this._updateColor([0, 0, 0, 0]);
                break;
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        // 
        this.addEventListener('click', this._onClick);
        const tabindex = this.getAttribute('tabindex') || '0';
        this.$colorWrap.setAttribute('tabindex', this.disabled ? '-1' : tabindex);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        this.removeEventListener('click', this._onClick);
        const index = instanceArray.indexOf(this);
        // 
        instanceArray.splice(index, 1);
    }
    // ////////////////////
    // 
    /**
     *  value 
     */
    _onValueChanged(n) {
        this.invalid = false;
        const value = domUtils.getValidColor(n);
        this._updateColor(value);
    }
    /**
     *  disabled 
     */
    _onDisabledChanged(val) {
        this.$colorWrap.setAttribute('tabindex', val ? '-1' : this.getAttribute('tabindex') || '0');
    }
    /**
     * 
     */
    _updateColor(value) {
        let rgbValue;
        value = domUtils.getValidColor(value);
        if (value.length > 3) {
            this.$alpha.style.width = 100 * value[3] / 255 + '%';
            rgbValue = Array.from(value).splice(0, 3);
        }
        else {
            rgbValue = value;
        }
        this.$color.style.backgroundColor = Chroma(rgbValue).css();
    }
    /**
     * color 
     */
    _onClick(event) {
        // 
        if (event.target.disabled || event.target.readonly) {
            return;
        }
        const $elem = this;
        // 
        if (panelHandle) {
            panelHandle(event);
        }
        // 
        $elem.dispatch('open');
    }
    /**
     * 
     */
    static registerPanelHandle(fn) {
        if (typeof fn !== 'function') {
            throw new Error('panelHandle is not a function');
        }
        panelHandle = fn;
    }
}
