'use strict';
const fs = require('fs');
import { EditBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
/**
 *   Attribute
 *
 *   disabled: 
 *   readonly: 
 *   invalid: 
 *
 *   value: 
 *   checked: 
 */
export class Checkbox extends EditBase {
    // //////////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$checkbox = this.shadowRoot.querySelector('#checkbox');
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        return this.checked;
    }
    set value(val) {
        this.checked = val;
    }
    get checked() {
        return this.hasAttribute('checked');
    }
    set checked(bool) {
        if (bool) {
            this.setAttribute('checked', '');
        }
        else {
            this.removeAttribute('checked');
        }
    }
    /**
     * 
     */
    _onConnected() {
        //  tabindex
        const tabindex = this.getAttribute('tabindex') || '0';
        this.$checkbox.setAttribute('tabindex', this.disabled ? '-1' : tabindex);
        // 
        instanceArray.push(this);
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
        // 
        this.addEventListener('click', this._onClick);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.removeEventListener('click', this._onClick);
    }
    // ////////////////////
    // 
    /**
     * 
     * 
     */
    _onClick() {
        // disabled  readonly 
        if (this.disabled || this.readonly) {
            return;
        }
        if (this.invalid) {
            this.invalid = false;
        }
        this.value = !this.value;
        this.dispatch('change');
        this.dispatch('confirm');
    }
    /**
     *  value 
     * @param val
     */
    _onValueChanged(val) {
        if (!val || val === '' || val === 'false') {
            this.removeAttribute('checked');
        }
        else {
            this.setAttribute('checked', val);
        }
    }
    /**
     *  disabled 
     */
    _onDisabledChanged(val) {
        this.$checkbox.setAttribute('tabindex', val ? '-1' : this.getAttribute('tabindex') || '0');
    }
    /**
     * 
     *  pressed 
     * @param {Event} event
     */
    _onKeyDown(event) {
        if (this.disabled || this.readonly) {
            return;
        }
        //  checkbox 
        if (this.focused && (event.keyCode === 13 || event.keyCode === 32) && !this.hasAttribute('pressed')) {
            this.setAttribute('pressed', '');
        }
    }
    /**
     * 
     *  pressed 
     * 
     * @param {Event} event
     */
    _onKeyUp(event) {
        if (this.disabled || this.readonly) {
            return;
        }
        //  checked 
        switch (event.keyCode) {
            case 32:
            case 13:
                this.checked = !this.checked;
                this.removeAttribute('pressed');
                this.dispatch('change');
                this.dispatch('confirm');
                break;
        }
    }
}
