'use strict';
const { ipcRenderer } = require('electron');
import { relative, join, isAbsolute, basename } from 'path';
export class FileUrlManager {
    getAllFileProtocol() {
        return Object.keys(FileUrlManager.urlMap).map((protocol) => {
            return {
                protocol,
                label: FileUrlManager.urlMap[protocol].label,
                path: FileUrlManager.urlMap[protocol].path,
            };
        });
    }
    resolveToRaw(url) {
        const matchInfo = url.match(/^([a-zA-z]*):\/\/(.*)$/);
        if (matchInfo) {
            const relPath = matchInfo[2].replace(/\\/g, '/');
            const info = this.getProtocalInfo(matchInfo[1]);
            if (info) {
                return join(info.path, relPath);
            }
        }
        return url;
    }
    resolveToUrl(raw, protocol) {
        if (!raw || !isAbsolute(raw) || !protocol) {
            return '';
        }
        const info = this.getProtocalInfo(protocol);
        if (!info) {
            return '';
        }
        return info.protocol + '://' + relative(info.path, raw).replace(/\\/g, '/');
    }
    getProtocalInfo(protocol) {
        if (!FileUrlManager.urlMap[protocol]) {
            return;
        }
        return {
            protocol,
            ...FileUrlManager.urlMap[protocol],
        };
    }
}
FileUrlManager.urlMap = {};
FileUrlManager.hasInit = false;
export const fileUrlManager = new FileUrlManager();
ipcRenderer.send('ui-file-query-protocols');
ipcRenderer.on('main-query-protocols', (event, arg) => {
    FileUrlManager.urlMap = arg || {};
    FileUrlManager.hasInit = true;
});
export class FileUrl {
    constructor($root) {
        this._relPath = '';
        this._$root = $root;
    }
    /**
     * 
     */
    get invalid() {
        if (!this.value) {
            return false;
        }
        if (this._protocolInfo && (isAbsolute(this.relPath) || this.relPath.includes('../'))) {
            return true;
        }
        if (!this._protocolInfo && !isAbsolute(this.raw)) {
            return true;
        }
        return false;
    }
    get protocolInfo() {
        return this._protocolInfo;
    }
    set relPath(val) {
        val = val ?? '';
        if (this._protocolInfo) {
            this._relPath = val.replace(/\\/g, '/');
        }
        else {
            this._relPath = val;
        }
    }
    get relPath() {
        return this._relPath;
    }
    get protocol() {
        return this._protocolInfo?.protocol || '';
    }
    set protocol(val) {
        // , raw 
        if (!val) {
            this._protocolInfo = undefined;
            return;
        }
        const info = fileUrlManager.getProtocalInfo(val);
        // 
        this._protocolInfo = info;
    }
    get raw() {
        if (!this._protocolInfo || !this._protocolInfo.path) {
            return this.relPath;
        }
        return join(this._protocolInfo.path, this.relPath || '');
    }
    get url() {
        if (!this._protocolInfo) {
            return this.relPath;
        }
        return this._protocolInfo.protocol + '://' + this.relPath;
    }
    set value(val) {
        if (!val || val.length === 0) {
            return;
        }
        // TODO:  multi
        if (val instanceof Array) {
            const value = val[0];
            val = value.replace(basename(value), '');
        }
        // 
        const matchInfo = val.match(/^([a-zA-z]*):\/\/(.*)$/);
        if (matchInfo) {
            this.relPath = matchInfo[2].replace(/\\/g, '/');
            const setProtocol = () => {
                const info = fileUrlManager.getProtocalInfo(matchInfo[1]);
                this._protocolInfo = info;
            };
            if (!fileUrlManager.getProtocalInfo(matchInfo[1]) && !FileUrlManager.hasInit) {
                // HACK 
                this._timer = setTimeout(() => {
                    if (this._protocolInfo) {
                        return;
                    }
                    setProtocol();
                    // @ts-ignore
                    this._$root._urlChange();
                }, 300);
                // @ts-ignore HACK 
                this._protocolInfo = {
                    protocol: matchInfo[1],
                };
            }
            else {
                setProtocol();
            }
        }
        else {
            this.relPath = val;
        }
    }
    get value() {
        if (this._protocolInfo) {
            return this.relPath;
        }
        return this.raw;
    }
    toString() {
        if (this._protocolInfo) {
            return this.url;
        }
        return this.raw;
    }
}
//  protocols 
ipcRenderer.on('main-protocols-change', (event, urlMap) => {
    FileUrlManager.urlMap = urlMap || {};
});
