import { CurveBase, CurveWrapMode, RealInterpMode, TangentWeightMode, WrapMode, } from './curve-base';
import { createMatrix } from './utils';
export class Hermite extends CurveBase {
    constructor(canvas, grid, config) {
        super(canvas, grid, config);
        this.hermiteArgs = [];
        this.config = {
            strokeStyle: 'red',
            lineWidth: 1,
            strokeStyleAxu: 'rgba(255, 0, 0, 0.3)',
            showKey: true,
            preWrapMode: CurveWrapMode.CLAMP,
            postWrapMode: CurveWrapMode.CLAMP,
            wrapMode: WrapMode.CLAMP,
            sample: 1,
        };
        // 
        this.keyframes = [];
        this.config.sample = config.sample;
        this.curveCache = new Path2D();
    }
    /**
     *  hermite 
     * @param point1  x  y 
     * @param k1 
     * @param point2  x  y 
     * @param k2 
     * @returns 
     */
    static calcArgs(point1, k1, point2, k2) {
        // 
        if (k1 === Number.POSITIVE_INFINITY || k2 === Number.POSITIVE_INFINITY) {
            return {
                a: 0,
                b: 0,
                c: 0,
                d: point1.y,
                md5: Hermite.calcHermitArgsMd5(point1, k1, point2, k2),
            };
        }
        const x1 = point1.x;
        const y1 = point1.y;
        const x2 = point2.x;
        const y2 = point2.y;
        let i;
        let j;
        let m;
        let n;
        let r;
        let o;
        let s;
        // 
        const e = [1, x1, x1 * x1, x1 * x1 * x1, 1, x2, x2 * x2, x2 * x2 * x2, 0, 1, 2 * x1, 3 * x1 * x1, 0, 1, 2 * x2, 3 * x2 * x2];
        const f = (e) => {
            // 
            return (e[0] * e[4] * e[8] + e[1] * e[5] * e[6] + e[2] * e[3] * e[7] - e[2] * e[4] * e[6] - e[1] * e[3] * e[8] - e[0] * e[5] * e[7]);
        };
        // 
        for (i = s = 0; i < 4; s += f(o) * e[i] * (i % 2 ? -1 : 1), i++) {
            if (e[i]) {
                for (j = 4, o = []; j < e.length; j++) {
                    if (j % 4 !== i) {
                        o.push(e[j]);
                    }
                }
            }
        }
        // 
        for (r = [], i = 0; i < 4; i++) {
            for (j = 0; j < 4; r[i + j * 4] = (((i + j) % 2 ? -1 : 1) * f(o)) / s, j++) {
                for (o = [], m = 0; m < 3; m++) {
                    for (n = 0; n < 3; n++) {
                        o.push(e[((i + m + 1) % 4) * 4 + ((j + n + 1) % 4)]);
                    }
                }
            }
        }
        // 
        const d = y1 * r[0] + y2 * r[1] + k1 * r[2] + k2 * r[3];
        const c = y1 * r[4] + y2 * r[5] + k1 * r[6] + k2 * r[7];
        const b = y1 * r[8] + y2 * r[9] + k1 * r[10] + k2 * r[11];
        const a = y1 * r[12] + y2 * r[13] + k1 * r[14] + k2 * r[15];
        // 
        return {
            a,
            b,
            c,
            d,
            md5: Hermite.calcHermitArgsMd5(point1, k1, point2, k2),
        };
    }
    /**
     * 
     * @param args 
     * @param t 
     */
    static getRenderFunc(args) {
        const { a, b, c, d } = args;
        return (t) => {
            return a * t * t * t + b * t * t + c * t + d;
        };
    }
    /**
     * 
     * @param keyframes
     * @param canvas
     * @param negative 
     */
    static quickPaint(keyframes, canvas, negative) {
        if (keyframes.length === 0 || !canvas) {
            console.error('paint hermit need keyframes and canvas!');
            return false;
        }
        const w = canvas.width;
        const h = negative ? canvas.height / 2 : canvas.height;
        const ctx = canvas.getContext('2d');
        const multi = h / w;
        function pointToCanvas(point) {
            return {
                x: point.x * w,
                y: h - point.y * h,
            };
        }
        // 
        const data = keyframes.map((item) => {
            // canvas 
            const outTangent = typeof item.outTangent === 'number' ? item.outTangent * multi : Infinity;
            const inTangent = typeof item.inTangent === 'number' ? item.inTangent * multi : Infinity;
            return {
                ...item,
                canvas: pointToCanvas(item.point),
                outTangent,
                inTangent,
            };
        });
        ctx.clearRect(0, 0, w, canvas.height);
        // 
        if (negative) {
            ctx.save();
            ctx.strokeStyle = '#d9dde8';
            ctx.lineWidth = 0.5;
            ctx.beginPath();
            ctx.moveTo(0, h);
            ctx.lineTo(w, h);
            ctx.stroke();
            ctx.closePath();
            ctx.restore();
        }
        ctx.beginPath();
        ctx.strokeStyle = 'red';
        ctx.moveTo(data[0].canvas.x, data[0].canvas.y);
        // TODO 
        // 
        for (let i = 0; i < data.length - 1; i++) {
            const next = data[i + 1];
            const now = data[i];
            switch (now.interpMode) {
                case RealInterpMode.CONSTANT:
                    {
                        ctx.lineTo(next.canvas.x, now.canvas.y);
                        ctx.lineTo(next.canvas.x, next.canvas.y);
                    }
                    break;
                case RealInterpMode.LINEAR:
                case RealInterpMode.CUBIC:
                    {
                        if (now.interpMode === RealInterpMode.LINEAR &&
                            (next.interpMode === RealInterpMode.CONSTANT || next.inTangent === Number.NEGATIVE_INFINITY)) {
                            ctx.lineTo(next.canvas.x, now.canvas.y);
                            ctx.lineTo(next.canvas.x, next.canvas.y);
                            break;
                        }
                        if ((now.interpMode === RealInterpMode.LINEAR && next.interpMode === RealInterpMode.LINEAR) ||
                            (now.outTangent === 0 && next.inTangent === 0 && now.canvas.y === next.canvas.y)) {
                            ctx.moveTo(now.canvas.x, now.canvas.y);
                            ctx.lineTo(next.canvas.x, next.canvas.y);
                            break;
                        }
                        patchCtrlPoint(now, 1, undefined, next);
                        patchCtrlPoint(next, 1, now);
                        const p1 = pointToCanvas(now.outCtrlPoint);
                        const p2 = pointToCanvas(next.inCtrlPoint);
                        const nextCanvas = pointToCanvas(next.point);
                        ctx.bezierCurveTo(p1.x, p1.y, p2.x, p2.y, nextCanvas.x, nextCanvas.y);
                    }
                    break;
            }
            // 
            if (now.outTangent === Number.POSITIVE_INFINITY || next.inTangent === Number.POSITIVE_INFINITY) {
                ctx.lineTo(next.point.x, h - next.point.y);
                ctx.stroke();
            }
            ctx.stroke();
        }
        ctx.closePath();
        return true;
    }
    static calcHermitArgsMd5(p1, k1, p2, k2) {
        return JSON.stringify({ p1, k1, p2, k2 });
    }
    /**
     * 
     * @param x
     * @param y
     */
    translate(x, y) { }
    isPointInCurve(point, range = 3) {
        if (!this.curveCache || !this.keyframes.length) {
            return false;
        }
        this.ctx.save();
        this.ctx.lineWidth = range;
        const res = this.ctx.isPointInStroke(this.curveCache, point.x, point.y);
        this.ctx.restore();
        return res;
    }
    findPointInKeys(point, range = 3) {
        if (!this.keyframes.length) {
            return -1;
        }
        return this.keyframes.findIndex((keyInfos) => {
            const { x, y } = keyInfos.canvas;
            return Math.abs(point.x - x) < range && Math.abs(point.y - y) < range;
        });
    }
    findCurveClipInKeys(point) {
        if (!this.curveCache || !this.keyframes.length) {
            return null;
        }
        for (let i = 0; i < this.keyframes.length; i++) {
            const startKey = this.keyframes[i];
            const nextKey = this.keyframes[i + 1];
            if (!nextKey) {
                break;
            }
            if (point.x > startKey.canvas.x && point.x < nextKey.canvas.x) {
                return {
                    start: i,
                    end: i + 1,
                    curveName: '',
                };
            }
        }
        return null;
    }
    /**
     * 
     * @param keyframes
     */
    paint(keyframes, config) {
        config && Object.assign(this.config, config);
        this.clear();
        this.preCurveCache = null;
        this.postCurveCache = null;
        this.curveCache = undefined;
        // 
        this.hermiteArgs = [];
        if (!keyframes || !keyframes.length) {
            this.keyframes = [];
            return;
        }
        //  JSON 
        try {
            this.keyframes = keyframes.map((item, i) => {
                const res = this.transformKey(item);
                this.patchCtrlPoint(res, keyframes[i - 1], keyframes[i + 1]);
                return res;
            });
            //  tangent  linear 
            this.checkCurveTangent();
        }
        catch (error) {
            console.error(error);
            return;
        }
        this.curveCache = this.paintRect();
        this.paintInWrapMode();
    }
    transformKey(key) {
        return {
            ...key,
            broken: key.broken ?? key.inTangent !== key.outTangent,
            canvas: this.grid.valueToPixel(key.point),
        };
    }
    /**
     * 
     */
    rePaint() {
        this.clear();
        this.curveCache = this.paintRect();
        this.paintInWrapMode();
    }
    paintWithCache(ctx) {
        this.curveCache && ctx.stroke(this.curveCache);
    }
    /**
     * 
     */
    paintKeyFrame(point) {
        this.ctx.save();
        this.ctx.fillStyle = 'gold';
        this.ctx.beginPath();
        this.ctx.arc(point.x, point.y, 1, 0, 2 * Math.PI);
        this.ctx.closePath();
        this.ctx.stroke();
        this.ctx.fill();
        this.ctx.restore();
    }
    /**
     * 
     * @param start  index
     * @param end  index
     */
    paintRect(options) {
        if (!this.keyframes.length) {
            return null;
        }
        const end = options && options.end || this.keyframes.length - 1;
        const start = options && options.start || 0;
        //  curveCache 
        // const end = options?.end || this.keyframes.length - 1;
        // const start = options?.start || 0;
        const curveCache = new Path2D();
        // 
        for (let i = start; i < end; i++) {
            const next = this.keyframes[i + 1];
            const item = this.keyframes[i];
            if (!item) {
                continue;
            }
            const canvas = item.canvas;
            if (i === start) {
                curveCache.moveTo(canvas.x, canvas.y);
            }
            // 
            if (typeof item.interpMode !== 'number') {
                item.interpMode = RealInterpMode.LINEAR;
            }
            // 
            switch (item.interpMode) {
                case RealInterpMode.CONSTANT:
                    {
                        curveCache.moveTo(canvas.x, canvas.y);
                        curveCache.lineTo(next.canvas.x, canvas.y);
                        curveCache.lineTo(next.canvas.x, next.canvas.y);
                    }
                    break;
                case RealInterpMode.LINEAR:
                    {
                        curveCache.moveTo(canvas.x, canvas.y);
                        curveCache.lineTo(next.canvas.x, next.canvas.y);
                    }
                    break;
                case RealInterpMode.CUBIC:
                    {
                        // if (next.interpMode === RealInterpMode.CONSTANT || item.interpMode === RealInterpMode.LINEAR && next.inTangent === Number.NEGATIVE_INFINITY) {
                        //     curveCache.lineTo(next.canvas.x, item.canvas.y);
                        //     curveCache.lineTo(next.canvas.x, next.canvas.y);
                        //     break;
                        // }
                        // if (item.interpMode === RealInterpMode.LINEAR && next.interpMode === RealInterpMode.LINEAR
                        //     || item.outTangent === 0 && next.inTangent === 0 && item.point.y === next.point.y) {
                        //     curveCache.lineTo(next.canvas.x, next.canvas.y);
                        //     break;
                        // }
                        if (!item.outCtrlPoint) {
                            this.patchCtrlPoint(item, undefined, next);
                        }
                        if (!next.inCtrlPoint) {
                            this.patchCtrlPoint(next, item);
                        }
                        const p1 = this.grid.valueToPixel(item.outCtrlPoint);
                        const p2 = this.grid.valueToPixel(next.inCtrlPoint);
                        const nextCanvas = this.grid.valueToPixel(next.point);
                        curveCache.bezierCurveTo(p1.x, p1.y, p2.x, p2.y, nextCanvas.x, nextCanvas.y);
                    }
                    break;
            }
            // 
            // if (item.outTangent === Number.POSITIVE_INFINITY || next.inTangent === Number.POSITIVE_INFINITY) {
            //     const point = this.grid.valueToPixel(next.point);
            //     this.curveCache.lineTo(point.x, point.y);
            // }
            this.paintKeyFrame(canvas);
        }
        const ctx = options && options.ctx || this.ctx;
        this.ctx.strokeStyle = this.config.strokeStyle;
        ctx.stroke(curveCache);
        return curveCache;
    }
    /**
     * 
     */
    paintInWrapMode() {
        if (!this.keyframes.length) {
            return;
        }
        this.ctx.save();
        this.ctx.strokeStyle = this.config.strokeStyleAxu;
        if (this.keyframes.length === 1) {
            this.curveCache = null;
            // 
            const curveCache = new Path2D();
            curveCache.moveTo(0, this.keyframes[0].canvas.y);
            curveCache.lineTo(this.canvas.width, this.keyframes[0].canvas.y);
            this.ctx.stroke(curveCache);
            this.ctx.restore();
            return;
        }
        let end = this.config.duration ? this.grid.valueToPixelX(this.config.duration) : this.canvas.width;
        const endKeyX = this.keyframes[this.keyframes.length - 1].canvas.x;
        end = Math.max(endKeyX, end);
        // 
        const endPoint = this.paintPostWrapMode(end);
        const startPoint = this.paintPreWrapMode();
        this.paintMainCurveWrapMode(startPoint, endPoint);
        this.ctx.restore();
    }
    paintPreWrapMode() {
        const startPoint = this.grid.valueToPixel(this.keyframes[0].point);
        if (typeof this.config.preWrapMode !== 'number') {
            this.preCurveCache = null;
            return startPoint;
        }
        const endPoint = this.grid.valueToPixel(this.keyframes[this.keyframes.length - 1].point);
        this.ctx.clearRect(0, 0, startPoint.x, this.canvas.height);
        const resPoint = { x: this.grid.valueToPixelX(0), y: startPoint.y };
        // 
        if (startPoint.x === resPoint.x) {
            this.preCurveCache = null;
            return resPoint;
        }
        const w = endPoint.x - startPoint.x;
        if (w === 0) {
            console.debug('The key frame distance is 0');
            return resPoint;
        }
        this.ctx.clearRect(0, 0, startPoint.x + 1, this.canvas.height);
        const loop = Math.ceil(startPoint.x / w);
        const curvePath = new Path2D();
        // 
        curvePath.moveTo(resPoint.x, resPoint.y);
        switch (this.config.preWrapMode) {
            case CurveWrapMode.CLAMP: {
                curvePath.lineTo(startPoint.x, startPoint.y);
                break;
            }
            case CurveWrapMode.LOOP: {
                const m = createMatrix();
                for (let i = 0; i < loop; i++) {
                    curvePath.moveTo(startPoint.x - w * i, startPoint.y);
                    m.translateSelf(-w, 0);
                    curvePath.lineTo(startPoint.x - w * i, endPoint.y);
                    curvePath.addPath(this.curveCache, m);
                }
                const x = this.grid.pixelToValueX(startPoint.x - loop * w);
                resPoint.y = this.calcValueFromX(x);
                break;
            }
            case CurveWrapMode.PING_PONG: {
                const flipCache = new Path2D();
                const m = createMatrix();
                flipCache.addPath(this.curveCache, m.scaleSelf(-1, 1));
                let matrix = createMatrix();
                for (let i = 0; i <= loop; i++) {
                    matrix = matrix.translateSelf(-w, 0);
                    if (i % 2 === 0) {
                        curvePath.addPath(flipCache, createMatrix().translateSelf(startPoint.x * 2 - w * i));
                    }
                    else {
                        curvePath.addPath(this.curveCache, matrix);
                    }
                }
                let x = this.grid.pixelToValueX(startPoint.x - loop * w);
                if (loop % 2 !== 0) {
                    // 
                    x = endPoint.x - startPoint.x - x;
                }
                resPoint.y = this.calcValueFromX(x);
                break;
            }
        }
        this.preCurveCache = curvePath;
        //  cache  preCurveCache 
        if (startPoint.x < 0) {
            return resPoint;
        }
        // this.ctx.save();
        this.ctx.strokeStyle = this.config.strokeStyleAxu;
        this.ctx.stroke(curvePath);
        // this.ctx.restore();
        return resPoint;
    }
    paintPostWrapMode(end) {
        const endPoint = this.grid.valueToPixel(this.keyframes[this.keyframes.length - 1].point);
        if (typeof this.config.postWrapMode !== 'number') {
            this.postCurveCache = null;
            return endPoint;
        }
        end = end || this.canvas.width;
        this.ctx.clearRect(endPoint.x, 0, end - endPoint.x + 1, this.canvas.height);
        if (end === endPoint.x || end < endPoint.x) {
            return endPoint;
        }
        const startPoint = this.grid.valueToPixel(this.keyframes[0].point);
        const w = endPoint.x - startPoint.x;
        const minLoop = Math.ceil((end - endPoint.x) / w);
        const curvePath = new Path2D();
        curvePath.moveTo(endPoint.x, endPoint.y);
        const resPoint = { x: end, y: endPoint.y };
        switch (this.config.postWrapMode) {
            case CurveWrapMode.CLAMP: {
                curvePath.lineTo(resPoint.x, resPoint.y);
                break;
            }
            case CurveWrapMode.LOOP: {
                const m = createMatrix();
                for (let i = 0; i <= minLoop; i++) {
                    m.translateSelf(w, 0);
                    curvePath.lineTo(endPoint.x + w * i, startPoint.y);
                    curvePath.addPath(this.curveCache, m);
                }
                const x = this.grid.pixelToValueX(end - endPoint.x - w * (minLoop - 1));
                resPoint.y = this.calcValueFromX(x);
                break;
            }
            case CurveWrapMode.PING_PONG: {
                const flipCache = new Path2D();
                const m = createMatrix();
                flipCache.addPath(this.curveCache, m.scaleSelf(-1, 1));
                let matrix = createMatrix();
                for (let i = 0; i <= minLoop; i++) {
                    matrix = matrix.translateSelf(w, 0);
                    if (i % 2 === 0) {
                        curvePath.addPath(flipCache, createMatrix().translateSelf(endPoint.x * 2 + w * i));
                    }
                    else {
                        curvePath.addPath(this.curveCache, matrix);
                    }
                }
                let x = this.grid.pixelToValueX(end - endPoint.x - w * (minLoop - 1));
                if (minLoop % 2 === 0) {
                    // 
                    x = endPoint.x - startPoint.x - x;
                }
                resPoint.y = this.calcValueFromX(x);
                break;
            }
        }
        this.postCurveCache = curvePath;
        this.ctx.strokeStyle = this.config.strokeStyleAxu;
        this.ctx.stroke(curvePath);
        return resPoint;
    }
    checkCurveTangent() {
        //  curve 
        this.keyframes.forEach((key, i) => {
            if (key.interpMode === RealInterpMode.LINEAR) {
                const last = this.keyframes[i - 1];
                const next = this.keyframes[i + 1];
                last && (key.inTangent = (key.point.y - last.point.y) / (key.point.x - last.point.x));
                next && (key.outTangent = (next.point.y - key.point.y) / (next.point.x - key.point.x));
                this.patchCtrlPoint(key, last, next);
            }
        });
    }
    /**
     *  ctrl 
     */
    checkAllKeyCtrl() {
        this.keyframes.forEach((key, i) => {
            const last = this.keyframes[i - 1];
            const next = this.keyframes[i + 1];
            if (key.interpMode === RealInterpMode.LINEAR) {
                last && (key.inTangent = (key.point.y - last.point.y) / (key.point.x - last.point.x));
                next && (key.outTangent = (next.point.y - key.point.y) / (next.point.x - key.point.x));
            }
            this.patchCtrlPoint(key, last, next);
        });
    }
    /**
     *  duration 
     * @param startPoint
     * @param endPoint
     * @returns
     */
    paintMainCurveWrapMode(startPoint, endPoint) {
        if (typeof this.config.wrapMode !== 'number') {
            return;
        }
        const mainCurve = new Path2D();
        this.preCurveCache && mainCurve.addPath(this.preCurveCache);
        mainCurve.addPath(this.curveCache);
        this.postCurveCache && mainCurve.addPath(this.postCurveCache);
        const w = endPoint.x - startPoint.x;
        const loop = Math.ceil((this.canvas.width - endPoint.x) / w);
        if (loop === 0) {
            return;
        }
        const curvePath = new Path2D();
        curvePath.moveTo(endPoint.x, endPoint.y);
        switch (this.config.wrapMode) {
            case WrapMode.CLAMP: {
                curvePath.lineTo(this.canvas.width, endPoint.y);
                break;
            }
            case WrapMode.LOOP: {
                const m = createMatrix();
                for (let i = 0; i <= loop; i++) {
                    curvePath.moveTo(endPoint.x + w * i, endPoint.y);
                    m.translateSelf(w, 0);
                    curvePath.lineTo(endPoint.x + w * i, startPoint.y);
                    // curvePath.addPath(this.preCurveCache, m);
                    curvePath.addPath(mainCurve, m);
                    // curvePath.addPath(this.postCurveCache, m);
                }
                break;
            }
            case WrapMode.PING_PONG: {
                const flipCache = new Path2D();
                const m = createMatrix();
                flipCache.addPath(mainCurve, m.scaleSelf(-1, 1));
                let matrix = createMatrix();
                for (let i = 0; i <= loop; i++) {
                    matrix = matrix.translateSelf(w, 0);
                    if (i % 2 === 0) {
                        curvePath.addPath(flipCache, createMatrix().translateSelf(endPoint.x * 2 + w * i));
                    }
                    else {
                        curvePath.addPath(mainCurve, matrix);
                    }
                }
                break;
            }
        }
        // this.ctx.save();
        this.ctx.strokeStyle = this.config.strokeStyleAxu;
        this.ctx.stroke(curvePath);
        // this.ctx.restore();
    }
    /**
     *  Y  len (canvas )
     * @param len
     */
    moveAllY(len) {
        if (len === 0) {
            return false;
        }
        const newKeys = [];
        const outRange = this.keyframes.find((key) => {
            const canvas = {
                x: key.canvas.x,
                y: key.canvas.y + len,
            };
            const newPoint = this.grid.patchPointInRange(canvas);
            if (newPoint) {
                newKeys.push({
                    canvas: this.grid.valueToPixel(newPoint),
                    point: newPoint,
                });
            }
            else {
                newKeys.push({
                    canvas,
                    point: this.grid.pixelToValue(canvas),
                });
            }
            return newPoint;
        });
        if (outRange) {
            console.debug('move keys outRange');
            return false;
        }
        this.keyframes.forEach((key, i) => {
            Object.assign(key, newKeys[i]);
        });
        // 
        this.keyframes.sort((a, b) => a.point.x - b.point.x);
        this.checkAllKeyCtrl();
        // const curvePath = new Path2D();
        // let m = createMatrix();
        // m = m.translateSelf(0, len);
        // curvePath.addPath(this.curveCache, m);
        // this.curveCache = curvePath;
        // this.ctx.stroke(this.curveCache);
        // this.paintInWrapMode();
        return true;
    }
    /**
     * 
     * @param keys
     * @param delta  canvas 
     */
    moveKeys(keys, delta) {
        // 
        const outRange = keys.find((key) => {
            const canvas = {
                x: key.canvas.x + delta.x,
                y: key.canvas.y + delta.y,
            };
            const newPoint = this.grid.patchPointInRange(canvas);
            if (newPoint) {
                key.canvas = this.grid.valueToPixel(newPoint);
                key.point = newPoint;
            }
            else {
                key.canvas = canvas;
                key.point = this.grid.pixelToValue(canvas);
            }
            return newPoint;
        });
        if (outRange) {
            console.debug('move keys outRange');
        }
        // 
        this.keyframes.sort((a, b) => a.point.x - b.point.x);
        this.checkAllKeyCtrl();
        return true;
    }
    scaleKeys(keys, scaleOpt) {
        const { changeValue, base, scale } = scaleOpt;
        keys.forEach((item) => {
            const distance = item.canvas[changeValue] - base;
            item.canvas[changeValue] = base + distance * scale;
            item.point = this.grid.pixelToValue(item.canvas);
        });
        this.checkAllKeyCtrl();
    }
    /**
     * 
     * @param keys
     * @param spacing
     */
    spacingKeys(keys, spacing) {
        for (let i = 1; i < keys.length; i++) {
            const key = keys[i];
            key.point.x = keys[i - 1].point.x + spacing;
            key.canvas = this.grid.valueToPixel(key.point);
        }
        // 
        this.keyframes.sort((a, b) => a.point.x - b.point.x);
        this.checkAllKeyCtrl();
        return true;
    }
    /**
     * ()
     * @param x  x canvas
     * @param Y  y canvas
     * @param index 
     * @returns index 
     */
    moveKey(x, y, index) {
        let newPoint = this.grid.patchPointInRange({ x, y });
        let canvas = { x, y };
        if (newPoint) {
            console.debug('move key outRange');
            canvas = this.grid.valueToPixel(newPoint);
        }
        else {
            newPoint = this.grid.pixelToValue(canvas);
        }
        return this.editKey(index, newPoint, canvas);
    }
    /**
     * 
     * @param index
     * @param newPoint
     * @param canvas
     * @returns
     */
    editKey(index, newPoint, canvas) {
        const now = require('lodash').cloneDeep(this.keyframes[index]);
        if (!now) {
            return -1;
        }
        let next = this.keyframes[index + 1];
        let last = this.keyframes[index - 1];
        // /
        if ((last && newPoint.x === last.point.x) || (next && newPoint.x === next.point.x)) {
            return -1;
        }
        now.point = newPoint;
        now.canvas = canvas || this.grid.valueToPixel(newPoint);
        // 
        if (next && now.point.x > next.point.x) {
            this.keyframes.splice(index, 1);
            this.keyframes.splice(index + 1, 0, now);
            index++;
            last = next;
            next = this.keyframes[index + 1];
            next && (next.outTangent = next.inTangent);
            return index;
        }
        else if (last && now.point.x < last.point.x) {
            // 
            this.keyframes.splice(index, 1);
            this.keyframes.splice(index - 1, 0, now);
            index--;
            next = last;
            last = this.keyframes[index - 1];
            last && (last.inTangent = last.outTangent);
            return index;
        }
        this.keyframes[index] = now;
        this.checkCurveTangent();
        this.patchCtrlPoint(now, last, next);
        return index;
    }
    /**
     * 
     * @param start 
     * @param end 
     * @param data 
     */
    applyBezierToCurveClip(start, end, data) {
        const startKey = this.keyframes[start];
        const endKey = this.keyframes[end];
        if (!startKey || !endKey) {
            return false;
        }
        const width = endKey.point.x - startKey.point.x;
        const height = Math.abs(endKey.point.y - startKey.point.y);
        // 
        startKey.interpMode = RealInterpMode.CUBIC;
        startKey.broken = true;
        this.ensureTangentMode(startKey, TangentWeightMode.RIGHT);
        endKey.interpMode = RealInterpMode.CUBIC;
        endKey.broken = true;
        this.ensureTangentMode(endKey, TangentWeightMode.LEFT);
        // y 
        if (endKey.point.y < startKey.point.y) {
            data = JSON.parse(JSON.stringify(data));
            data[1] = -data[1];
            data[3] = -data[3];
        }
        // 
        startKey.outCtrlPoint = {
            x: startKey.point.x + width * data[0],
            y: startKey.point.y + height * data[1],
        };
        endKey.inCtrlPoint = {
            x: startKey.point.x + width * data[2],
            y: startKey.point.y + height * data[3],
        };
        // 
        const outTangent = (startKey.outCtrlPoint.y - startKey.point.y) / ((startKey.outCtrlPoint.x - startKey.point.x) / this.config.sample);
        const inTangent = (endKey.inCtrlPoint.y - endKey.point.y) / ((endKey.inCtrlPoint.x - endKey.point.x) / this.config.sample);
        this.updateTangentWeight(start, startKey.outCtrlPoint, outTangent, 'outTangent');
        this.updateTangentWeight(end, endKey.inCtrlPoint, inTangent, 'inTangent');
        return true;
    }
    /**
     * 
     * @param index 
     * @param tan 
     * @param type 
     * @returns 
     */
    updateTan(index, tan, type) {
        // ()
        const now = this.keyframes[index];
        now[type] = tan;
        let updateType = type;
        if (!now.broken) {
            updateType = 'both';
        }
        if (updateType !== 'inTangent' && typeof now.outTangent === 'number') {
            now.outTangent = tan;
        }
        if (updateType !== 'outTangent' && typeof now.inTangent === 'number') {
            now.inTangent = tan;
        }
        this.patchCtrlPoint(now, this.keyframes[index - 1], this.keyframes[index + 1]);
    }
    updateTangentWeight(index, newPoint, tan, type) {
        const now = this.keyframes[index];
        this.updateTan(index, tan, type);
        const angle = Math.atan(tan);
        if (type === 'inTangent') {
            now.inCtrlPoint = newPoint;
            now.inTangentWeight = (now.point.x - newPoint.x) / this.config.sample / Math.cos(angle);
        }
        else {
            now.outCtrlPoint = newPoint;
            now.outTangentWeight = (newPoint.x - now.point.x) / this.config.sample / Math.cos(angle);
        }
        this.patchCtrlPoint(now, this.keyframes[index - 1], this.keyframes[index + 1]);
        // if (now.tangentWeightMode !== TangentWeightMode.NONE) {
        //     return true;
        // }
        // if (type === 'inTangent') {
        //     this.keyframes[index + 1] && this.patchCtrlPoint(now, this.keyframes[index - 1], this.keyframes[index + 1]);
        // } else {
        //     this.keyframes[index - 1] && this.patchCtrlPoint(now, this.keyframes[index - 1]);
        // }
        return true;
    }
    changeInterpMode(index, interpMode) {
        const keyframe = this.keyframes[index];
        // 
        if (keyframe.interpMode === interpMode) {
            return;
        }
        // if (interpMode === RealInterpMode.CUBIC && keyframe.interpMode !== RealInterpMode.CUBIC) {
        //     const newMethod = interpMode;
        //     interpMode = keyframe.interpMode;
        //     keyframe.interpMode = newMethod;
        //     //  cubic 
        // } else {
        //     keyframe.interpMode = interpMode;
        // }
        keyframe.interpMode = interpMode;
        switch (interpMode) {
            case RealInterpMode.CONSTANT:
                keyframe.inTangent = 0;
                keyframe.outTangent = 0;
                break;
            case RealInterpMode.LINEAR:
                {
                    const last = this.keyframes[index - 1];
                    const next = this.keyframes[index + 1];
                    // 
                    last && (keyframe.inTangent = (keyframe.point.y - last.point.y) / (keyframe.point.x - last.point.x));
                    next && (keyframe.outTangent = (next.point.y - keyframe.point.y) / (next.point.x - keyframe.point.x));
                    if (!last) {
                        keyframe.inTangent = keyframe.outTangent;
                    }
                    if (!next) {
                        keyframe.outTangent = keyframe.inTangent;
                    }
                }
                break;
            case RealInterpMode.CUBIC:
                keyframe.inTangent && (keyframe.inTangent = 0);
                keyframe.outTangent && (keyframe.outTangent = 0);
                break;
        }
        this.patchCtrlPoint(keyframe, this.keyframes[index - 1], this.keyframes[index + 1]);
    }
    changeTangentWeight(index, tangentWeightMode) {
        const currentKey = this.keyframes[index];
        if (![TangentWeightMode.NONE, TangentWeightMode.RIGHT].includes(tangentWeightMode) &&
            [TangentWeightMode.RIGHT, TangentWeightMode.NONE].includes(currentKey.tangentWeightMode)) {
            const next = this.keyframes[index + 1];
            this.patchTangentWeight(currentKey, undefined, next);
        }
        if (![TangentWeightMode.NONE, TangentWeightMode.LEFT].includes(tangentWeightMode) &&
            [TangentWeightMode.LEFT, TangentWeightMode.NONE].includes(currentKey.tangentWeightMode)) {
            const last = this.keyframes[index - 1];
            this.patchTangentWeight(currentKey, last);
        }
        currentKey.tangentWeightMode = tangentWeightMode;
        //  weight 
        this.patchCtrlPoint(currentKey, this.keyframes[index - 1], this.keyframes[index + 1]);
    }
    /**
     *  tangentWeight
     * @param target
     * @param last
     * @param next
     */
    patchTangentWeight(target, last, next) {
        if (last) {
            const angle = Math.atan(target.outTangent);
            const dx = target.point.x - last.point.x;
            target.inTangentWeight = (1 / 3) * dx / Math.cos(angle) / this.config.sample;
        }
        if (next) {
            const angle = Math.atan(target.outTangent);
            const dx = next.point.x - target.point.x;
            target.outTangentWeight = (1 / 3) * dx / Math.cos(angle) / this.config.sample;
        }
    }
    /**
     * 
     * @param keys
     * @param x (point)
     * @param y
     */
    pasteKeys(keys, x, y) {
        const lastIndex = this.calcKeyIndexFromX(x);
        const startX = keys[0].point.x;
        const startY = keys[0].point.y;
        const keyInfos = keys.map((key, i) => {
            // 
            key.point.x = key.point.x - startX + x;
            if (y) {
                key.point.y = key.point.y - startY + y;
            }
            // 
            const index = this.keyframes.findIndex((item) => this.grid.xFormat(item.point.x) === this.grid.xFormat(key.point.x));
            if (index !== -1) {
                this.keyframes.splice(index, 1);
            }
            return this.transformKey(key);
        });
        if (lastIndex === -1) {
            this.keyframes.push(...keyInfos);
        }
        else {
            this.keyframes.splice(lastIndex, 0, ...keyInfos);
        }
        this.keyframes.sort((a, b) => a.point.x - b.point.x);
        this.checkAllKeyCtrl();
        return keyInfos;
    }
    /**
     * 
     * @param point (point)
     */
    createNewKey(point, keyInfo) {
        const newKey = {
            point,
            canvas: this.grid.valueToPixel(point),
            // TODO 
            interpMode: (keyInfo && keyInfo.interpMode) ?? RealInterpMode.CUBIC,
            tangentWeightMode: (keyInfo && keyInfo.tangentWeightMode) ?? TangentWeightMode.NONE,
            inTangent: (keyInfo && keyInfo.inTangent) ?? 0,
            outTangent: (keyInfo && keyInfo.outTangent) ?? 0,
        };
        const newPoint = this.grid.patchPointInRange(newKey.canvas);
        if (newPoint) {
            console.debug('newKey out of range!', newKey);
            newKey.canvas = this.grid.valueToPixel(newPoint);
            newKey.point = newPoint;
        }
        if (this.keyframes.length === 0) {
            this.keyframes.push(newKey);
            return 0;
        }
        const overwriteIndex = this.keyframes.findIndex((key) => key.point.x === point.x);
        if (overwriteIndex !== -1) {
            this.keyframes[overwriteIndex] = newKey;
            this.patchCtrlPoint(newKey, this.keyframes[overwriteIndex - 1], this.keyframes[overwriteIndex + 1]);
            return overwriteIndex;
        }
        let lastIndex = this.calcKeyIndexFromX(point.x);
        if (!lastIndex && this.keyframes[this.keyframes.length - 1].point.x < point.x) {
            lastIndex = this.keyframes.length - 1;
        }
        const index = typeof lastIndex === 'number' ? lastIndex + 1 : 0;
        // const next = this.keyframes[index];
        // if (typeof lastIndex === 'number') {
        //     const last = this.keyframes[lastIndex];
        //     newKey.inTangent = this.calcSlopeWithPoints(last.point, point);
        //     last.outTangent = last.outTangent! || last.inTangent!;
        // }
        this.keyframes.splice(index, 0, newKey);
        this.patchCtrlPoint(newKey, this.keyframes[index - 1], this.keyframes[index + 1]);
        // if (next) {
        //     // TODO 
        //     newKey.outTangent = newKey.inTangent ?? this.calcSlopeWithPoints(point, next.point);
        //     next.inTangent = next.inTangent! || next.outTangent!;
        // }
        return index;
    }
    /**
     * add keyframe in curves at x
     *  interpMode 
     * @param x (point)
     * @return {null | number} return keyframe index or null
     */
    createKeyInCurveAt(x) {
        const index = this.calcKeyIndexFromX(x);
        if (index === -1) {
            return null;
        }
        const last = this.keyframes[index];
        const next = this.keyframes[index + 1];
        const keyFrame = {
            outTangent: 0,
            inTangent: 0,
            interpMode: last.interpMode,
            tangentWeightMode: TangentWeightMode.NONE,
            outTangentWeight: 1,
            inTangentWeight: 1,
        };
        let newKeyFrame;
        switch (last.interpMode) {
            case RealInterpMode.CONSTANT:
                newKeyFrame = Object.assign(keyFrame, {
                    point: {
                        x,
                        y: last.point.y,
                    },
                    canvas: this.grid.valueToPixel({ x, y: last.point.y }),
                });
                break;
            case RealInterpMode.LINEAR:
                {
                    const point = {
                        x,
                        y: last.point.y + x * last.outTangent,
                    };
                    newKeyFrame = Object.assign(keyFrame, {
                        point,
                        canvas: this.grid.valueToPixel(point),
                    });
                }
                break;
            case RealInterpMode.CUBIC:
                newKeyFrame = this.spliteCurveAt(x, last, next);
                this.keyframes.splice(index + 1, 0, newKeyFrame);
                return index + 1;
        }
        // 
        this.keyframes.splice(index + 1, 0, newKeyFrame);
        this.checkCurveTangent();
        this.patchCtrlPoint(newKeyFrame, this.keyframes[index], this.keyframes[index + 2]);
        return index + 1;
    }
    /**
     *  xE 
     * https://blog.csdn.net/nibiewuxuanze/article/details/48229393
     * @param x
     * @param last
     * @param next
     */
    spliteCurveAt(x, last, next) {
        const dx = next.point.x - last.point.x;
        const e = (x - last.point.x) / dx;
        const F = this.calcAssistPoint(last.point, last.outCtrlPoint, e);
        const G = this.calcAssistPoint(last.outCtrlPoint, next.inCtrlPoint, e);
        const H = this.calcAssistPoint(next.inCtrlPoint, next.point, e);
        const I = this.calcAssistPoint(F, G, e);
        const J = this.calcAssistPoint(G, H, e);
        const E = this.calcAssistPoint(I, J, e);
        const newKeyFrame = {
            inCtrlPoint: I,
            outCtrlPoint: J,
            point: E,
            canvas: this.grid.valueToPixel(E),
            interpMode: RealInterpMode.CUBIC,
            inTangent: this.calcSlopeWithPoints(I, E),
            outTangent: this.calcSlopeWithPoints(E, J),
            tangentWeightMode: TangentWeightMode.BOTH,
            inTangentWeight: 1,
            outTangentWeight: 1,
        };
        last.outCtrlPoint = F;
        next.inCtrlPoint = H;
        this.patchTangentWeight(newKeyFrame, last, next);
        // TODO  weight  tangentWeightMode 
        if ([TangentWeightMode.RIGHT, TangentWeightMode.BOTH].includes(last.tangentWeightMode)) {
            this.patchTangentWeight(last, undefined, newKeyFrame);
        }
        if ([TangentWeightMode.LEFT, TangentWeightMode.BOTH].includes(next.tangentWeightMode)) {
            this.patchTangentWeight(next, newKeyFrame);
        }
        return newKeyFrame;
    }
    /**
     * 
     * @param lastP
     * @param nextP
     * @param e
     * @returns
     */
    calcAssistPoint(lastP, nextP, e) {
        const x = lastP.x * (1 - e) + nextP.x * e;
        const y = lastP.y * (1 - e) + nextP.y * e;
        return { x, y };
    }
    /**
     *  value 
     * @param x (point)
     * @returns
     */
    calcValueFromX(x) {
        const index = this.calcKeyIndexFromX(x);
        if (index === -1) {
            return null;
        }
        const key = this.keyframes[index];
        const next = this.keyframes[index + 1];
        if (!next) {
            return null;
        }
        // 
        const arg = this.getArgs(key.point, key.outTangent, next.point, next.inTangent, index);
        return Hermite.getRenderFunc(arg)(x);
    }
    /**
     * 
     * @param point 
     */
    delKeyFrame(index) {
        // 
        this.keyframes.splice(index, 1);
        this.checkCurveTangent();
        this.patchCtrlPoint(this.keyframes[index - 1], undefined, this.keyframes[index + 1]);
        this.keyframes[index] && this.patchCtrlPoint(this.keyframes[index], this.keyframes[index - 1], undefined);
    }
    /**
     * 
     */
    clear() {
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    }
    ensureTangentMode(target, tangentMode) {
        if (target.tangentWeightMode === tangentMode) {
            return;
        }
        if ([TangentWeightMode.BOTH, TangentWeightMode.NONE].includes(tangentMode)) {
            target.tangentWeightMode = tangentMode;
            return;
        }
        if (target.tangentWeightMode === TangentWeightMode.NONE) {
            target.tangentWeightMode = tangentMode;
            return;
        }
        target.tangentWeightMode = TangentWeightMode.BOTH;
        return;
    }
    getArgs(p1, outTan, p2, inTan, i) {
        const md5 = Hermite.calcHermitArgsMd5(p1, outTan, p2, inTan);
        if (!this.hermiteArgs[i] || this.hermiteArgs[i].md5 !== md5) {
            this.hermiteArgs[i] = Hermite.calcArgs(p1, outTan, p2, inTan);
        }
        return this.hermiteArgs[i];
    }
    /**
     * 
     * @param a
     * @param b
     */
    calcSlopeWithPoints(a, b) {
        if (b.x - a.x === 0) {
            return Infinity;
        }
        return (b.y - a.y) / (b.x - a.x);
    }
    /**
     * 
     * @param x 
     * @param arg 
     */
    calcSlopeInCurveAt(x, arg) {
        // 
        const { a, b, c } = arg;
        return 3 * a * x * x + 2 * b * x + c;
    }
    /**
     * 
     * @param x (point)
     */
    calcKeyIndexFromX(x) {
        let index = -1;
        for (let i = 0; i < this.keyframes.length - 1; i++) {
            const point = this.keyframes[i].point;
            const nextPoint = this.keyframes[i + 1].point;
            if (x > point.x && nextPoint.x > x) {
                index = i;
                break;
            }
        }
        if (index === -1 && this.keyframes.length && x > this.keyframes[this.keyframes.length - 1].point.x) {
            index = this.keyframes.length - 1;
        }
        return index;
    }
    /**
     * 
     * @param target
     * @param last
     * @param next
     * @returns
     */
    patchCtrlPoint(target, last, next) {
        patchCtrlPoint(target, this.config.sample, last, next);
    }
}
function patchCtrlPoint(target, sample, last, next) {
    if (!target) {
        console.debug('patchCtrlPoint no target');
        return;
    }
    if (next) {
        if ([TangentWeightMode.BOTH, TangentWeightMode.RIGHT].includes(target.tangentWeightMode) && target.outTangentWeight) {
            const angle = Math.atan(target.outTangent);
            target.outCtrlPoint = {
                x: target.point.x + target.outTangentWeight * Math.cos(angle) * sample,
                y: target.point.y + target.outTangentWeight * Math.sin(angle),
            };
        }
        else {
            const dt = next.point.x - target.point.x;
            target.outCtrlPoint = {
                x: target.point.x + (1 / 3) * dt,
                y: target.point.y + ((1 / 3) * target.outTangent * dt) / sample,
            };
        }
    }
    if (last) {
        if ([TangentWeightMode.BOTH, TangentWeightMode.LEFT].includes(target.tangentWeightMode) && target.inTangentWeight) {
            const angle = Math.atan(target.inTangent);
            target.inCtrlPoint = {
                x: target.point.x - target.inTangentWeight * Math.cos(angle) * sample,
                y: target.point.y - target.inTangentWeight * Math.sin(angle),
            };
        }
        else {
            const dt = target.point.x - last.point.x;
            target.inCtrlPoint = {
                x: target.point.x - (1 / 3) * dt,
                y: target.point.y - ((1 / 3) * target.inTangent * dt) / sample,
            };
        }
    }
}
