'use strict';
const { ipcRenderer } = require('electron');
let dragInfo = null;
// item 
ipcRenderer.on('ui-drag-area-start', (event, info) => {
    dragInfo = info;
});
// item 
ipcRenderer.on('ui-drag-area-end', () => {
    dragInfo = null;
});
/**
 * 
 */
export class DragArea extends HTMLElement {
    constructor() {
        super(...arguments);
        this._types = [];
    }
    static get currentDragInfo() {
        return dragInfo;
    }
    get additional() {
        return this.hasAttribute('additional');
    }
    set additional(val) {
        clearTimeout(this.additionalTimer);
        if (val) {
            this.setAttribute('additional', '');
        }
        else {
            this.removeAttribute('additional');
        }
    }
    get droppable() {
        return (this.getAttribute('droppable') || '').split(',');
    }
    set droppable(types) {
        if (!Array.isArray(types)) {
            types = [types + ''];
        }
        if (types && types.length > 0) {
            this.setAttribute('droppable', types.join(','));
        }
        else {
            this.removeAttribute('droppable');
        }
    }
    get hoving() {
        return this.hasAttribute('hoving');
    }
    // ////////////////////
    /**
     * 
     */
    connectedCallback() {
        // 
        this.addEventListener('dragover', this._onDragOver);
        this.addEventListener('dragleave', this._onDragLevel);
        this.addEventListener('drop', this._onDrop);
        this.addEventListener('drop', this._onPreDrop, true);
    }
    /**
     * 
     */
    disconnectedCallback() {
        this.removeEventListener('dragover', this._onDragOver);
        this.removeEventListener('dragleave', this._onDragLevel);
        this.removeEventListener('drop', this._onDrop);
        this.removeEventListener('drop', this._onPreDrop, true);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['focused', 'readonly', 'disabled', 'invalid', 'droppable'];
    }
    /**
     * attribute 
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        switch (attr) {
            case 'droppable':
                this._types = newVal ? newVal.split(',').map((str) => str.trim()) : [];
                break;
            default:
                break;
        }
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _onDragOver(event) {
        event.preventDefault(); // NOTE: Must have, otherwise we can not drop
        let type = dragInfo ? dragInfo.type : null;
        if (event.dataTransfer.types.indexOf('Files') !== -1) {
            type = 'file';
        }
        const types = this._types;
        let hoving = types && types.indexOf(type) !== -1;
        if (!hoving && dragInfo && Array.isArray(dragInfo.extends)) {
            hoving = dragInfo.extends.some((type) => {
                return types && types.indexOf(type) !== -1;
            });
        }
        this.additional = false;
        if (dragInfo && dragInfo.additional && Array.isArray(dragInfo.additional)) {
            this.additional = dragInfo.additional.some((item) => {
                return types && types.indexOf(item.type) !== -1;
            });
        }
        if (hoving || this.additional) {
            event.stopPropagation();
            clearTimeout(this.hovingTimer);
            this.setAttribute('hoving', '');
        }
    }
    /**
     * 
     */
    _onDragLevel() {
        clearTimeout(this.hovingTimer);
        this.hovingTimer = setTimeout(() => {
            this.removeAttribute('hoving');
        }, 200);
    }
    /**
     * 
     */
    _onDrop(event) {
        if (!this.hasAttribute('hoving')) {
            return;
        }
        event.stopPropagation();
        event.preventDefault();
    }
    /**
     * 
     */
    _onPreDrop() {
        clearTimeout(this.hovingTimer);
        this.hovingTimer = setTimeout(() => {
            this.removeAttribute('hoving');
        }, 200);
    }
}
