'use strict';
const fs = require('fs');
import { EditBase } from '../base';
const { mathUtils, domUtils } = require('../../../utils/utils');
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
/**
 * ()
 * @param {*} elem
 * @param {boolean} [detach=false] 
 */
const controlMouseEvent = function (elem, detach = false) {
    //  start
    const onDragStart = (event) => {
        if (elem.disabled || elem.readonly) {
            return;
        }
        if (elem.invalid) {
            elem.invalid = false;
        }
        elem.dragging = true;
        elem.resetSize();
        if (event.type === 'touchstart') {
            event.clientX = event.touches[0].clientX;
            event.clientY = event.touches[0].clientY;
        }
        const sliderOffset = elem.boundingClientRect;
        let position = 0;
        if (elem.vertical) {
            const sliderOffsetBottom = sliderOffset.bottom;
            position = ((sliderOffsetBottom - event.clientY) / elem.sliderSize) * 100;
        }
        else {
            const sliderOffsetLeft = sliderOffset.left;
            position = ((event.clientX - sliderOffsetLeft) / elem.sliderSize) * 100;
        }
        elem.focused = true;
        elem._staging = elem.value;
        elem.value = elem.positionToValue(position);
    };
    // 
    const onDragging = (event) => {
        if (elem.dragging) {
            elem.resetSize();
            const sliderOffset = elem.boundingClientRect;
            let position = 0;
            if (event.type === 'touchmove') {
                event.clientX = event.touches[0].clientX;
                event.clientY = event.touches[0].clientY;
            }
            if (elem.vertical) {
                position = ((sliderOffset.bottom - event.clientY) / elem.sliderSize) * 100;
            }
            else {
                position = ((event.clientX - sliderOffset.left) / elem.sliderSize) * 100;
            }
            const value = elem.positionToValue(position);
            elem.value = value;
            elem.dispatch('change');
        }
    };
    // 
    const onDragEnd = () => {
        if (elem.dragging) {
            elem.dragging = false;
            elem.dispatch('change');
            elem._confirm();
            window.removeEventListener('mousemove', onDragging);
            window.removeEventListener('touchmove', onDragging);
            window.removeEventListener('mouseup', onDragEnd);
            window.removeEventListener('touchend', onDragEnd);
            window.removeEventListener('contextmenu', onDragEnd);
        }
    };
    /**
     *  domUtils.controlDragEvent 
     */
    domUtils.controlDragEvent(elem.$track, {
        start: onDragStart,
        drag: onDragging,
        end: onDragEnd,
    }, detach);
};
export class Slider extends EditBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.dragging = false;
        this.sliderSize = 1;
        this._staging = null;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$sliderWrap = this.shadowRoot.querySelector('.wrap');
        this.$numInput = this.shadowRoot.querySelector('.num-input');
        this.$track = this.shadowRoot.querySelector('.track');
        this.$wrapper = this.shadowRoot.querySelector('.wrapper');
        this.$cursor = this.shadowRoot.querySelector('.cursor');
        this.$numInput.$root = this.$cursor.$root = this.$track.$root = this.$wrapper.$root = this;
    }
    /**
     * 
     * @param {filePath} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get boundingClientRect() {
        return this.$track.getBoundingClientRect();
    }
    get currentPosition() {
        if (this.value === '-') {
            return 0;
        }
        const val = Number(this.value);
        return ((val - this.min) / (this.max - this.min)) * 100;
    }
    // TODO: inputvaluetooltip/value
    get vertical() {
        return this.hasAttribute('vertical');
    }
    get value() {
        const val = this.getAttribute('value');
        if (val === '-') {
            return val;
        }
        const value = parseFloat(val || '');
        return isNaN(value) ? 0 : value;
    }
    set value(val) {
        if (val !== '-') {
            if (this.min > this.max) {
                console.error('min should not be greater than max');
                return;
            }
            if (typeof val === 'number' && !isNaN(val)) {
                // 
                val = mathUtils.clamp(val, this.min, this.max);
            }
        }
        this.setAttribute('value', String(val));
    }
    get min() {
        const min = this.getAttribute('min');
        const val = Number(min);
        return isNaN(val) ? 0 : val;
    }
    set min(val) {
        this.setAttribute('min', String(val));
    }
    get max() {
        const max = this.getAttribute('max');
        const val = Number(max);
        //  Number(null)  0
        return isNaN(val) || max === null ? 100 : val;
    }
    set max(val) {
        this.setAttribute('max', String(val));
    }
    get step() {
        const step = this.getAttribute('step');
        const value = Number(step);
        return isNaN(value) || step === null ? 1 : value;
    }
    set step(val) {
        this.setAttribute('step', String(val));
    }
    get preci() {
        const preci = this.getAttribute('preci') || '0';
        //  step 
        return parseInt(preci, 10) || mathUtils.comPreci(this.step);
    }
    set preci(val) {
        if (isNaN(val) || !(val >= 0) || val > 20) {
            console.warn('preci only allows numbers between 0 and 20!');
            // step
            const rang = mathUtils.comPreci(this.step);
            this.setAttribute('preci', rang);
            return;
        }
    }
    get focused() {
        return this.hasAttribute('focused');
    }
    set focused(val) {
        if (val) {
            this.setAttribute('focused', '');
            this.$numInput.focused = true;
        }
        else {
            this.removeAttribute('focused');
            this.$numInput.focused = false;
        }
    }
    get height() {
        return this.getAttribute('height');
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        //  tabindex
        const tabindex = this.getAttribute('tabindex') || '0';
        this.$sliderWrap.setAttribute('tabindex', this.disabled ? '-1' : tabindex);
        // 
        this.dragging = false;
        this.sliderSize = 1;
        this.disabled && this.$numInput.setAttribute('disabled', '');
        this.preci = parseInt(this.getAttribute('preci') || '', 10) || mathUtils.comPreci(this.step);
        this.min = parseFloat(this.getAttribute('min') || '') || 0;
        // 
        this.addEventListener('focus', this._onFocus);
        this.addEventListener('blur', this._onBlur);
        // 
        controlMouseEvent(this);
        this.$numInput.addEventListener('change', this._onInputChange);
        this.$numInput.addEventListener('confirm', this._onInputConfirm);
        this.$numInput.addEventListener('cancel', this._onInputCancel);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        this.updateCursorAndInput();
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        controlMouseEvent(this, true);
        this.$numInput.removeEventListener('change', this._onInputChange);
        this.$numInput.removeEventListener('confirm', this._onInputConfirm);
        this.$numInput.removeEventListener('cancel', this._onInputCancel);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'min',
            'max',
            'height',
            'preci',
            'step',
        ];
    }
    /**
     * @description attribute 
     * @param attr 
     * @param oldVal 
     * @param newVal 
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'preci':
                this.preci = parseInt(newVal, 10);
                this.$numInput.preci = this.preci;
                break;
            case 'step':
                this.$numInput.step = isNaN(newVal) ? 1 : newVal;
                break;
            case 'min':
                const maxVal = this.getAttribute('max');
                this.$numInput.min = isNaN(newVal) ? 0 : newVal;
                this.handleMaxEqMin(newVal, maxVal);
                break;
            case 'max':
                const minVal = this.getAttribute('min');
                this.$numInput.max = isNaN(newVal) ? 100 : newVal;
                this.handleMaxEqMin(newVal, minVal);
                break;
        }
    }
    /**
     * @description  min === max  value 
     * @param currenVal 
     * @param compareVal 
     **/
    handleMaxEqMin(currenVal, compareVal) {
        if (!isNaN(currenVal) && !isNaN(compareVal) && currenVal === compareVal) {
            this.value = Number(currenVal);
        }
    }
    // ////////////////////
    // 
    /**
     *  value 
     */
    _onValueChanged(val) {
        if (isNaN(Number(val))) {
            this.$numInput.value = 0;
            return;
        }
        const value = mathUtils.clamp(val, this.min, this.max);
        // 
        if (value !== parseFloat(val)) {
            return;
        }
        //  preci 
        val = parseFloat(val).toFixed(this.preci);
        this.updateCursorAndInput();
    }
    /**
     *  invalid 
     */
    _onInvalidChanged(val) {
        process.nextTick(() => {
            this.$numInput.invalid = val;
        });
    }
    /**
     *  readonly 
     */
    _onReadonlyChanged(val) {
        if (val) {
            this.$numInput.setAttribute('readonly', '');
        }
        else {
            this.$numInput.removeAttribute('readonly');
        }
    }
    /**
     *  disabled 
     */
    _onDisabledChanged(val) {
        this.$sliderWrap.setAttribute('tabindex', val ? '-1' : this.getAttribute('tabindex') || '0');
        if (val) {
            this.$numInput.setAttribute('disabled', '');
        }
        else {
            this.$numInput.removeAttribute('disabled');
        }
    }
    // ////////////////////
    /**
     *  focused
     */
    _onFocus() {
        if (this.disabled) {
            return;
        }
    }
    /**
     *  focused
     */
    _onBlur() {
        this.focused = false;
    }
    /**
     * num-input  change 
     */
    _onInputChange() {
        const $elem = this;
        if ($elem.$input.invalid) {
            $elem.invalid = false;
        }
        $elem.$root.value = $elem.value;
        $elem.$root.updateCursorAndInput();
        $elem.$root.dispatch('change');
    }
    /**
     * num-input  confirm num-input
     */
    _onInputConfirm() {
        const $elem = this;
        $elem.$root.dispatch('confirm');
    }
    /**
     * num-input  cancel 
     */
    _onInputCancel() {
        const $elem = this;
        $elem.$root.dispatch('cancel');
    }
    /**
     *  num-input 
     */
    _onKeyDown(event) {
        if (this.disabled || this.readonly) {
            return;
        }
        switch (event.keyCode) {
            case 13: // 
                if (this.$numInput._staging === null) {
                    this.$numInput.focus();
                }
                else {
                    this.focus;
                }
                break;
            case 27: // esc
                if (this.dragging) {
                    // 
                    domUtils.acceptEvent(event);
                    this.dragging = false;
                    if (this._staging === this.value) {
                        return;
                    }
                    this.value = this._staging || '';
                    this.dispatch('change');
                    this.dispatch('cancel');
                    this.dispatch('confirm');
                }
                break;
            default:
                break;
        }
    }
    // ////////////////////
    /**
     * 
     */
    positionToValue(position) {
        // 
        const pos = mathUtils.clamp(position, 0, 100);
        const stepLength = 100 / ((this.max - this.min) / this.step);
        const steps = Math.round(pos / stepLength);
        const value = steps * stepLength * (this.max - this.min) * 0.01 + this.min;
        return parseFloat(value.toFixed(this.preci));
    }
    /**
     * 
     */
    updateCursor() {
        const key = this.vertical ? 'bottom' : 'left';
        this.$cursor.style[key] = `${this.currentPosition}%`;
    }
    /**
     * 
     */
    updateCursorAndInput() {
        this.updateCursor();
        this.$numInput.value = this.value;
    }
    /**
     * confirm 
     */
    _confirm() {
        if (this._staging === this.value) {
            return;
        }
        this._staging = String(this.value);
        this.dispatch('confirm');
    }
    /**
     *  sliderSize
     */
    resetSize() {
        this.sliderSize = this.vertical ? this.$track.clientHeight : this.$track.clientWidth;
    }
}
