'use strict';
import { FunctionBase } from '../base';
const { ipcRenderer } = require('electron');
/**
 * 
 */
export class DragItem extends FunctionBase {
    get draging() {
        return this.hasAttribute('draging');
    }
    set draging(bool) {
        if (bool) {
            this.setAttribute('draging', '');
        }
        else {
            this.removeAttribute('draging');
        }
    }
    get type() {
        return this.getAttribute('type') || '';
    }
    set type(type) {
        if (type) {
            this.setAttribute('type', type);
        }
        else {
            this.removeAttribute('type');
        }
    }
    get extends() {
        const str = this.getAttribute('extends');
        if (str) {
            return str.split(',').map(type => type.trim());
        }
        return [];
    }
    set extends(array) {
        if (array && array.length > 0) {
            this.setAttribute('extends', array.join(','));
        }
        else {
            this.removeAttribute('extends');
        }
    }
    get additional() {
        const str = this.getAttribute('additional') || '';
        try {
            return JSON.parse(str);
        }
        catch (error) {
            return [];
        }
    }
    set additional(array) {
        if (array && array.length > 0) {
            this.setAttribute('additional', JSON.stringify(array));
        }
        else {
            this.removeAttribute('additional');
        }
    }
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super(false);
    }
    /**
     * 
     */
    _onConnected() {
        this.setAttribute('draggable', 'true');
        // 
        this.addEventListener('dragstart', this._onDragStart);
        this.addEventListener('dragend', this._onDragEnd);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        this.removeEventListener('dragstart', this._onDragStart);
        this.removeEventListener('dragend', this._onDragEnd);
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _onDragStart(event) {
        this.draging = true;
        const info = {
            type: this.type,
            extends: this.extends,
            /**
             * 
             *  [{type, value}]
             *  fbx  subAsset prefab
             * hierarchy  assets  additional 
             */
            additional: this.additional || null,
        };
        (event.dataTransfer.types || []).forEach((name) => {
            let value = event.dataTransfer.getData(name);
            if (name === 'additional') {
                value = JSON.parse(value);
            }
            info[name] = value;
        });
        //  dragArea
        ipcRenderer.send('ui-drag-item-start', info);
    }
    /**
     * 
     */
    _onDragEnd() {
        this.draging = false;
        ipcRenderer.send('ui-drag-item-end');
    }
}
