'use strict';
const fs = require('fs');
import { DisplayBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
const iconGroups = require('./icon-map.json');
const allIconsMap = {};
if (iconGroups) {
    for (const typeName in iconGroups) {
        Object.assign(allIconsMap, iconGroups[typeName]);
    }
}
/**
 * Attribute
 *
 * valueicon
 * color icon 
 */
export class Icon extends DisplayBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$container = this.shadowRoot.querySelector('.container');
    }
    /**
     * 
     * @param {filePath} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        const customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['value'];
    }
    /**
     * attribute 
     *
     * oldVal
     * newVal
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        switch (attr) {
            case 'value':
                this._onValueChanged(newVal);
                break;
            case 'color':
                this._onValueChanged(newVal);
                break;
            default:
                break;
        }
    }
    /**
     *  icon 
     */
    static get Map() {
        return iconGroups;
    }
    get value() {
        return this.getAttribute('value') || '';
    }
    set value(val) {
        val += '';
        this.setAttribute('value', val);
    }
    get color() {
        return this.getAttribute('color') || '';
    }
    set color(val) {
        if (val) {
            this.setAttribute('color', val);
        }
        else {
            this.removeAttribute('color');
        }
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _onConnected() { }
    /**
     *  value 
     * @param val
     */
    _onValueChanged(val) {
        const defaultValue = this.getAttribute('default') || 'unknow';
        const iconName = allIconsMap[val] ? allIconsMap[val] : allIconsMap[defaultValue];
        this.$container.setAttribute('value', iconName);
        const raw = require(`./svg/${iconName}.svg`);
        this.$container.innerHTML = raw.default;
    }
}
