'use strict';
import { ComplexBase } from "../base";
import { getCurveClass } from "../curve-editor/lib/curve";
const fs = require('fs');
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
// 
let openHandle;
export class Curve extends ComplexBase {
    /**
     * 
     */
    constructor() {
        super();
        this._type = 'hermit';
        this._connect = false;
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}${CUSTOM_STYLE}${HTML}</style>`;
        this.$canvas = this.shadowRoot.querySelector('.canvas');
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get type() {
        return this._type;
    }
    set value(val) {
        this.setAttribute('value', JSON.stringify(val));
    }
    get value() {
        return this._value;
    }
    get config() {
        return this._config;
    }
    /**
     * 
     */
    connectedCallback() {
        super.connectedCallback();
        // 
        instanceArray.push(this);
        if (!this.shadowRoot) {
            return;
        }
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
        this._connect = true;
        this.$canvas.height = this.offsetHeight;
        this.$canvas.width = this.offsetWidth;
        try {
            const configAttrValue = this.getAttribute('config');
            if (configAttrValue) {
                const config = JSON.parse(configAttrValue);
                if (config && !this._config) {
                    this._config = config;
                }
            }
        }
        catch (error) {
            console.log(error);
        }
        this._observer = new IntersectionObserver((entries) => {
            const { isIntersecting } = entries[0];
            if (isIntersecting) {
                // 
                this._observer.unobserve(this);
                const rect = this.getBoundingClientRect();
                this.$canvas.width = rect.width;
                this.$canvas.height = rect.height;
                this.render(this._value);
            }
        }, {
            threshold: 0.1,
        });
        this._observer.observe(this);
        this.render();
        this.addEventListener('click', this._onClick);
    }
    /**
     * 
     */
    disconnectedCallback() {
        super.disconnectedCallback();
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        this.removeEventListener('click', this._onClick);
        this._observer.unobserve(this);
        this._observer = undefined;
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['focused', 'readonly', 'disabled', 'value', 'config'];
    }
    /**
     * Attribute 
     * @param attr
     * @param oldValue
     * @param newValue
     */
    attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            case 'value':
                try {
                    const val = JSON.parse(newValue);
                    this.render(val);
                    this._value = val;
                }
                catch (error) {
                    console.debug(error);
                }
                break;
            case 'config':
                try {
                    const config = JSON.parse(this.getAttribute('config'));
                    if (config) {
                        this._config = config;
                    }
                    this.render(this._value);
                }
                catch (error) {
                }
                break;
        }
    }
    /**
    * 
    * @param val
    */
    render(val = this.value) {
        if (!this._connect || !val || !val.keys) {
            return;
        }
        const Curve = getCurveClass('hermit');
        if (Curve && Curve.quickPaint) {
            Curve.quickPaint(val.keys, this.$canvas, (this.config && this.config.negative) ?? false);
        }
    }
    /**
    * 
    * @param event
    */
    _onClick(event) {
        event.preventDefault();
        // @ts-ignore
        if (this.hasAttribute('disabled') || this.hasAttribute('readonly')) {
            return;
        }
        // 
        if (openHandle) {
            openHandle(event);
        }
    }
    /*
    * 
    * */
    static registerOpenHandle(fu) {
        openHandle = fu;
    }
}
