'use strict';
const fs = require('fs');
const { Menu, MenuItem, getCurrentWindow } = require('@electron/remote');
const Chroma = require('chroma-js');
const { domUtils } = require('../../../utils/utils');
import { ComplexBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
let _setColorStorageHandle = null;
let _getColorStorageHandle = null;
export class ColorPicker extends ComplexBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this._rgb = [];
        this._stagingRGBA = [];
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        // 
        this.$hueHandle = this.shadowRoot.querySelector('.hue-handle');
        this.$colorHandle = this.shadowRoot.querySelector('.color-handle');
        this.$alphaHandle = this.shadowRoot.querySelector('.alpha-handle');
        this.$hueCtrl = this.shadowRoot.querySelector('.hue.ctrl');
        this.$colorCtrl = this.shadowRoot.querySelector('.color.ctrl');
        this.$alphaCtrl = this.shadowRoot.querySelector('.alpha.ctrl');
        this.$sliderR = this.shadowRoot.querySelector('#r-slider');
        this.$sliderG = this.shadowRoot.querySelector('#g-slider');
        this.$sliderB = this.shadowRoot.querySelector('#b-slider');
        this.$sliderA = this.shadowRoot.querySelector('#a-slider');
        this.$newColor = this.shadowRoot.querySelector('#new-color');
        this.$oldColor = this.shadowRoot.querySelector('#old-color');
        this.$hexInput = this.shadowRoot.querySelector('#hex-input');
        this.$btnAdd = this.shadowRoot.querySelector('.add-presets');
        this.$palette = this.shadowRoot.querySelector('.palette');
        this.$presets = this.shadowRoot.querySelector('.presets');
        //  this 
        this.$hueHandle.$root = this.$colorHandle.$root = this.$alphaHandle.$root = this.$btnAdd.$root
            = this.$hueCtrl.$root = this.$colorCtrl.$root = this.$alphaCtrl.$root
                = this.$palette.$root = this.$presets.$root = this;
        this.$sliderR.$root = this.$sliderG.$root = this.$sliderA.$root = this.$sliderB.$root
            = this.$newColor.$root = this.$oldColor.$root = this.$hexInput.$root
                = domUtils.$root = this;
        // 0 - 1
        this._h = 1; // hue  0 - 1
        this._a = 1; //  0 - 1
        this._cl = 0; // color  left  0 - 1
        this._ct = 0; // color  top  0 - 1
        this._rgb = [255, 0, 0];
        this._stagingRGBA = [0, 0, 0, 1];
        //  presets 
        this._updateColorPresets();
    }
    /**
     * 
     * @param {string} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    /**
     * @description  preset 
     **/
    static registerSetColorStorageHandle(handle) {
        if (handle) {
            _setColorStorageHandle = handle;
        }
    }
    /**
     * @description  preset 
     **/
    static registerGetColorStorageHandle(handle) {
        if (handle) {
            _getColorStorageHandle = handle;
        }
    }
    // ////////////////////
    get value() {
        return [
            this._rgb[0],
            this._rgb[1],
            this._rgb[2],
            Math.round(this._a * 255),
        ];
    }
    set value(val) {
        const value = domUtils.getValidColor(val);
        this.setAttribute('value', JSON.stringify(value));
    }
    get readonly() {
        return this.hasAttribute('readonly');
    }
    set readonly(val) {
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    get invalid() {
        return this.hasAttribute('invalid');
    }
    set invalid(val) {
        if (val) {
            this.setAttribute('invalid', '');
        }
        else {
            this.removeAttribute('invalid');
        }
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    get focused() {
        return this.hasAttribute('focused');
    }
    set focused(val) {
        if (val) {
            this.setAttribute('focused', '');
        }
        else {
            this.removeAttribute('focused');
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        // 
        this.addEventListener('focus', this._onFocus);
        this.addEventListener('blur', this._onBlur);
        this.addEventListener('keydown', this._onKeyDown);
        this.$hueHandle.addEventListener('mousedown', this._onHueHandleMousedown);
        this.$hueCtrl.addEventListener('mousedown', this._hueCtrlMousedown);
        this.$colorHandle.addEventListener('mousedown', this._onColorHandleMousedown);
        this.$colorCtrl.addEventListener('mousedown', this._colorCtrlMousedown);
        this.$alphaHandle.addEventListener('mousedown', this._onAlphaHandleMousedown);
        this.$alphaCtrl.addEventListener('mousedown', this._alphaCtrlMousedown);
        this.$sliderR.addEventListener('change', this._onRSliderChange);
        this.$sliderG.addEventListener('change', this._onGSliderChange);
        this.$sliderB.addEventListener('change', this._onBSliderChange);
        this.$sliderA.addEventListener('change', this._onASliderChange);
        this.$hexInput.addEventListener('change', this._onHexInputChange);
        this.$sliderR.addEventListener('confirm', this._onConfirm);
        this.$sliderG.addEventListener('confirm', this._onConfirm);
        this.$sliderB.addEventListener('confirm', this._onConfirm);
        this.$sliderA.addEventListener('confirm', this._onConfirm);
        this.$hexInput.addEventListener('confirm', this._onConfirm);
        this.$btnAdd.addEventListener('click', this._onAddClick);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        // 
        this._updateHueHandle();
        this._updateColorHandle();
        this._updateAlphaHandle();
        this._updateColor();
        this._updateRGB();
        this._updateA();
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        this._staging();
        // 
        this.removeEventListener('focus', this._onFocus);
        this.removeEventListener('blur', this._onBlur);
        this.removeEventListener('keydown', this._onKeyDown);
        this.$hueHandle.removeEventListener('mousedown', this._onHueHandleMousedown);
        this.$hueCtrl.removeEventListener('mousedown', this._hueCtrlMousedown);
        this.$alphaHandle.removeEventListener('mousedown', this._onAlphaHandleMousedown);
        this.$alphaCtrl.removeEventListener('mousedown', this._alphaCtrlMousedown);
        this.$colorHandle.removeEventListener('mousedown', this._onColorHandleMousedown);
        this.$colorCtrl.removeEventListener('mousedown', this._colorCtrlMousedown);
        this.$sliderR.removeEventListener('change', this._onRSliderChange);
        this.$sliderG.removeEventListener('change', this._onGSliderChange);
        this.$sliderB.removeEventListener('change', this._onBSliderChange);
        this.$sliderA.removeEventListener('change', this._onASliderChange);
        this.$hexInput.removeEventListener('change', this._onHexInputChange);
        this.$sliderR.removeEventListener('confirm', this._onConfirm);
        this.$sliderG.removeEventListener('confirm', this._onConfirm);
        this.$sliderB.removeEventListener('confirm', this._onConfirm);
        this.$sliderA.removeEventListener('confirm', this._onConfirm);
        this.$hexInput.removeEventListener('confirm', this._onConfirm);
        this.$btnAdd.removeEventListener('click', this._onAddClick);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            'focused',
            'readonly',
            'disabled',
            'value',
            'disabled-color',
            'disabled-alpha',
            'path',
        ];
    }
    /**
     * @description attribute 
     * @param attr 
     * @param oldVal 
     * @param newVal 
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        super.attributeChangedCallback(attr, oldVal, newVal);
        switch (attr) {
            case 'value':
                if (newVal === oldVal) {
                    break;
                }
                this._rgb = JSON.parse(newVal);
                if (this._rgb[3] === undefined) {
                    this._a = 1;
                    this._rgb[3] = 1;
                }
                else {
                    this._rgb[3] = this._a = this._rgb[3] / 255;
                }
                this._staging();
                this._updateHueAndColor();
                this._updateHexAndColor();
                this._updateHueHandle();
                this._updateColorHandle();
                this._updateAlphaHandle();
                this._updateColor();
                this._updateRGB();
                this._updateA();
                break;
        }
    }
    // ////////////////////
    // 
    /**
     *  mousemove 
     */
    _onHueHandleMousedown(hueHandleEvent) {
        const $elem = this;
        const $root = $elem.$root;
        const stagingY = hueHandleEvent.screenY;
        const stagingTop = Math.round(parseInt($elem.style.top || 0) * $root.$hueCtrl.clientHeight / 100);
        function mousemove(event) {
            const offsetY = event.screenY - stagingY;
            const top = Math.min(Math.max(stagingTop + offsetY, 0), $root.$hueCtrl.clientHeight);
            $root._h = 1 - top / $root.$hueCtrl.clientHeight;
            $root._updateHueHandle();
            $root._updateColor();
            $root._updateRGB();
            $root.dispatch('change');
        }
        function mouseup() {
            document.removeEventListener('mousemove', mousemove);
            document.removeEventListener('mouseup', mouseup);
            $root.dispatch('confirm');
        }
        document.addEventListener('mousemove', mousemove);
        document.addEventListener('mouseup', mouseup);
    }
    /**
     * 
     */
    _hueCtrlMousedown(event) {
        if (event.target !== this) {
            return;
        }
        const $elem = this;
        $elem.$root._h = 1 - event.offsetY / $elem.clientHeight;
        $elem.$root._updateHueHandle();
        $elem.$root._updateColor();
        $elem.$root._updateRGB();
        $elem.$root.dispatch('change');
        $elem.$root._onHueHandleMousedown.call($elem.$root.$hueHandle, event);
    }
    /**
     *  mousemove 
     */
    _onAlphaHandleMousedown(alphaHandleEvent) {
        const $elem = this;
        const $root = $elem.$root;
        const stagingY = alphaHandleEvent.screenY;
        const stagingTop = Math.round(parseInt($elem.style.top || 0) * $root.$alphaCtrl.clientHeight / 100);
        function mousemove(event) {
            const offsetY = event.screenY - stagingY;
            const top = Math.min(Math.max(stagingTop + offsetY, 0), $root.$alphaCtrl.clientHeight);
            $root._a = 1 - top / $root.$alphaCtrl.clientHeight;
            $root._updateAlphaHandle();
            $root._updateColor();
            $root._updateA();
            $root.dispatch('change');
        }
        function mouseup() {
            document.removeEventListener('mousemove', mousemove);
            document.removeEventListener('mouseup', mouseup);
            $root.dispatch('confirm');
        }
        document.addEventListener('mousemove', mousemove);
        document.addEventListener('mouseup', mouseup);
    }
    /**
     * 
     */
    _alphaCtrlMousedown(event) {
        if (event.target !== this) {
            return;
        }
        const $elem = this;
        $elem.$root._a = 1 - event.offsetY / $elem.clientHeight;
        $elem.$root._updateAlphaHandle();
        $elem.$root._updateColor();
        $elem.$root._updateA();
        $elem.$root.dispatch('change');
        $elem.$root._onAlphaHandleMousedown.call($elem.$root.$alphaHandle, event);
    }
    /**
     * 
     */
    _onColorHandleMousedown(colorHandleEvent) {
        const $elem = this;
        const $root = $elem.$root;
        const stagingX = colorHandleEvent.pageX;
        const stagingY = colorHandleEvent.pageY;
        const stagingTop = Math.round(parseInt($elem.style.top || 0) * $root.$colorCtrl.clientHeight / 100);
        const stagingLeft = Math.round(parseInt($elem.style.left || 0) * $root.$colorCtrl.clientWidth / 100);
        function mousemove(event) {
            const offsetX = event.pageX - stagingX;
            const offsetY = event.pageY - stagingY;
            const top = Math.min(Math.max(stagingTop + offsetY, 0), $root.$colorCtrl.clientHeight);
            const left = Math.min(Math.max(stagingLeft + offsetX, 0), $root.$colorCtrl.clientWidth);
            $root._cl = left / $root.$colorCtrl.clientWidth;
            $root._ct = top / $root.$colorCtrl.clientHeight;
            $root._updateColorHandle();
            $root._updateRGB();
            $root.dispatch('change');
        }
        function mouseup() {
            document.removeEventListener('mousemove', mousemove);
            document.removeEventListener('mouseup', mouseup);
            $root.dispatch('confirm');
        }
        document.addEventListener('mousemove', mousemove);
        document.addEventListener('mouseup', mouseup);
    }
    /**
     * 
     */
    _colorCtrlMousedown(event) {
        if (event.target !== this) {
            return;
        }
        const $elem = this;
        $elem.$root._cl = event.offsetX / $elem.clientWidth;
        $elem.$root._ct = event.offsetY / $elem.clientHeight;
        $elem.$root._updateColorHandle();
        $elem.$root._updateRGB();
        $elem.$root.dispatch('change');
        $elem.$root._onColorHandleMousedown.call($elem.$root.$colorHandle, event);
    }
    // ////////////////////
    // slider + input 
    /**
     * R 
     */
    _onRSliderChange(event) {
        const $elem = this;
        $elem.$root._rgb[0] = Math.round(event.target.value);
        $elem.$root._updateHueAndColor();
        $elem.$root.dispatch('change');
    }
    /**
     * G 
     */
    _onGSliderChange(event) {
        const $elem = this;
        $elem.$root._rgb[1] = Math.round(event.target.value);
        $elem.$root._updateHueAndColor();
        $elem.$root.dispatch('change');
    }
    /**
     * B 
     */
    _onBSliderChange(event) {
        const $elem = this;
        $elem.$root._rgb[2] = Math.round(event.target.value);
        $elem.$root._updateHueAndColor();
        $elem.$root.dispatch('change');
    }
    /**
     * A 
     */
    _onASliderChange(event) {
        const $elem = this;
        $elem.$root._a = event.target.value / 255;
        $elem.$root._updateAlphaHandle();
        $elem.$root._updateA();
        $elem.$root.dispatch('change');
    }
    /**
     * slider  input 
     */
    _onConfirm() {
        const $elem = this;
        $elem.$root.dispatch('confirm');
    }
    /**
     * rgb 
     */
    _onHexInputChange(event) {
        let rgb;
        try {
            rgb = Chroma(event.target.value).rgb();
        }
        catch (error) {
            rgb = [255, 255, 255];
        }
        const $elem = this;
        $elem.$root._rgb = [rgb[0], rgb[1], rgb[2]];
        $elem.$root._updateHexAndColor();
        $elem.$root._updateHueHandle();
        $elem.$root._updateColorHandle();
        $elem.$root._updateColor();
        // 
        $elem.$root._updateHueAndColor();
        $elem.$root._updateRGB();
        $elem.$root.dispatch('change');
    }
    // ////////////////////
    /**
     * 
     */
    _onKeyDown(event) {
        switch (event.keyCode) {
            case 27: // esc
                this.value = [
                    this._stagingRGBA[0],
                    this._stagingRGBA[1],
                    this._stagingRGBA[2],
                    Math.round(this._stagingRGBA[3] * 255),
                ];
                this.dispatch('cancel');
                break;
        }
    }
    /**
     *  focused
     */
    _onBlur() {
        if (this.disabled) {
            return;
        }
        this.focused = false;
        const value = domUtils.getValidColor(this.value);
        this.setAttribute('value', JSON.stringify(value));
        this.dispatch('confirm');
        this._staging();
    }
    /**
     *  focused
     */
    _onFocus() {
        if (this.disabled || (this.hasAttribute('disabled-color') && this.hasAttribute('disabled-alpha'))) {
            return;
        }
        this.focused = true;
    }
    // ////////////////////
    // 
    _getStorageColor() {
        let colors;
        if (_getColorStorageHandle) {
            colors = _getColorStorageHandle();
        }
        else {
            const stagingList = localStorage.getItem('color-picker-staging-color') || '[]';
            colors = JSON.parse(stagingList);
        }
        return colors || [];
    }
    _setStorageColor(colors) {
        if (_setColorStorageHandle) {
            _setColorStorageHandle(colors);
        }
        else {
            localStorage.setItem('color-picker-staging-color', JSON.stringify(colors));
        }
    }
    /**
     *  colorPresets 
     */
    async _updateColorPresets() {
        const list = this._getStorageColor();
        let innerHTML = '';
        list?.forEach((color, index) => {
            innerHTML += `<div class="item presets-item" index="${index}" style="background-color: rgba(${color.toString()})"></div>`;
        });
        this.$presets.innerHTML = innerHTML;
        // preset 
        this.$presets.addEventListener('click', this._onPaletteClick);
        this.$presets.addEventListener('mousedown', this._onPaletteContextMenu);
    }
    /**
     *  colorPresets 
     */
    _onAddClick() {
        const $elem = this;
        const list = $elem.$root._getStorageColor();
        const rgb = Chroma($elem.$root.value, 'rgb').rgb();
        rgb[3] = $elem.$root._a;
        list.push(rgb);
        $elem.$root._setStorageColor(list);
        $elem.$root._updateColorPresets();
        // 
        const { $presets } = $elem.$root;
        $presets.scrollLeft = $presets.scrollWidth;
    }
    /**
     *  presets ,
     */
    _onPaletteClick(event) {
        if (!event.target || event.target.className.indexOf('presets-item') === -1) {
            return;
        }
        // $presets
        const $elem = this;
        const color = Chroma(event.target.style.backgroundColor).rgba();
        $elem.$root._rgb = [
            color[0],
            color[1],
            color[2],
        ];
        $elem.$root._a = color[3];
        $elem.$root._updateHexAndColor();
        $elem.$root._updateHueHandle();
        $elem.$root._updateColorHandle();
        $elem.$root._updateColor();
        $elem.$root._updateAlphaHandle();
        $elem.$root._updateA();
        // 
        $elem.$root._updateHueAndColor();
        $elem.$root._updateRGB();
        $elem.$root.dispatch('change');
        $elem.$root.dispatch('confirm');
    }
    /**
     * TODO: web  electron  Electron
     *  presets , 
     */
    _onPaletteContextMenu(event) {
        if (event.button !== 2 || !event.target || event.target.className.indexOf('presets-item') === -1) {
            return;
        }
        event.stopPropagation();
        const $elem = this;
        const $target = event.target;
        const list = $elem.$root._getStorageColor();
        const index = Number($target.getAttribute('index')); // 
        // 
        const menu = new Menu();
        menu.append(new MenuItem({
            label: 'Replace',
            click: () => {
                const value = Chroma($elem.$root.value, 'rgb').rgb();
                value[3] = $elem.$root._a;
                list.splice(index, 1, value);
                $elem.$root._setStorageColor(list);
                $elem.$root._updateColorPresets();
            },
        }));
        menu.append(new MenuItem({
            label: 'Delete',
            click: () => {
                // 
                list.splice(index, 1);
                // 
                $elem.$root._setStorageColor(list);
                $elem.$root._updateColorPresets();
            },
        }));
        // 
        menu.popup({ window: getCurrentWindow() });
    }
    /**
     *  value
     */
    _staging() {
        this._stagingRGBA = [
            this._rgb[0],
            this._rgb[1],
            this._rgb[2],
            this._a,
        ];
    }
    /**
     * 
     */
    _updateHueHandle() {
        this.$hueHandle.style.top = Math.round((1 - this._h) * 100) + '%';
    }
    /**
     * 
     */
    _updateAlphaHandle() {
        this.$alphaHandle.style.top = Math.round((1 - this._a) * 100) + '%';
    }
    /**
     * 
     */
    _updateColorHandle() {
        this.$colorHandle.style.left = Math.round(this._cl * 100) + '%';
        this.$colorHandle.style.top = Math.round(this._ct * 100) + '%';
    }
    /**
     * 
     */
    _updateColor() {
        const color = Chroma([this._h * 369, 1, 1], 'hsv').rgb();
        this.$colorCtrl.style.backgroundColor = `rgb(${color[0]}, ${color[1]}, ${color[2]})`;
    }
    /**
     *  RGB  slider
     */
    _updateRGB() {
        // 
        const color = Chroma([this._h * 360, this._cl, 1 - this._ct], 'hsv').rgb();
        this._rgb[0] = this.$sliderR.value = color[0];
        this._rgb[1] = this.$sliderG.value = color[1];
        this._rgb[2] = this.$sliderB.value = color[2];
        this._updateHexAndColor();
    }
    /**
     * 
     */
    _updateA() {
        this.$sliderA.value = Math.round(this._a * 255);
        this._updateHexAndColor();
    }
    /**
     * 
     */
    _updateHueAndColor() {
        const color = Chroma(this._rgb, 'rgb');
        const hsv = color.hsv();
        if (isNaN(hsv[0])) {
            hsv[0] = 360;
        }
        this._h = hsv[0] / 360;
        this._cl = hsv[1];
        this._ct = 1 - hsv[2];
        this._updateHueHandle();
        this._updateColor();
        this._updateColorHandle();
        this._updateHexAndColor();
    }
    /**
     * 
     */
    _updateHexAndColor() {
        this.$newColor.style.backgroundColor = `rgba(${this._rgb[0]}, ${this._rgb[1]}, ${this._rgb[2]}, ${this._a})`;
        this.$oldColor.style.backgroundColor = `rgba(${this._stagingRGBA[0]}, ${this._stagingRGBA[1]}, ${this._stagingRGBA[2]}, ${this._stagingRGBA[3]})`;
        this.$hexInput.value = Chroma(this._rgb).hex().toUpperCase();
        this.$alphaCtrl.style.background = `linear-gradient(rgba(${this._rgb[0]}, ${this._rgb[1]}, ${this._rgb[2]}, 1), rgba(0, 0, 0, 0))`;
    }
}
