import LinearTicks from './linear-ticks';
import { clamp } from './utils';
const Chroma = require('chroma-js');
export class Grid {
    constructor(canvas, config) {
        this.multi = 1;
        this.axisMargin = 20; // 
        this.negative = false; // 
        this.lineWidth = 1; // 
        this.showX = false; // , 
        this.showY = false; // , 
        this.xAxisScale = 1.0;
        this.yAxisScale = 1.0;
        // origin: IPoint; //  canvas 
        this.axisXConfig = {
            lods: [5, 2],
            minScale: 1,
            maxScale: 100,
            startOffset: 0,
            showLine: true,
            showLabel: true,
            lineWidth: 0.5,
            lineColor: '#333846',
            precision: 2,
            minStep: 1,
        };
        this.axisYConfig = {
            lods: [5, 2],
            minScale: 1,
            maxScale: 100,
            startOffset: 0,
            showLine: true,
            showLabel: true,
            lineWidth: 0.5,
            lineColor: '#333846',
            precision: 2,
            minStep: 1,
        };
        this.xAxisOffset = 0.0;
        this.xAnchor = 0.5;
        this.yAxisOffset = 0.0;
        this.yAnchor = 0.5;
        this.xAxisMinStep = 50;
        this.yAxisMinStep = 50;
        this.xMaxValue = 0;
        this.xMinValue = 0;
        this.yMaxValue = 0;
        this.yMinValue = 0;
        this.canvas = canvas;
        this.ctx = canvas.getContext('2d');
        this.ctx.strokeStyle = '#333846c';
        this.initConfig(config);
    }
    get height() {
        if (this.negative) {
            return this.canvas.height / 2;
        }
        return this.canvas.height;
    }
    get xRange() {
        return {
            left: 0,
            right: this.canvas.width,
        };
    }
    get anchorInfo() {
        return {
            x: 0 + this.axisMargin - this.lineWidth - 0.5,
            y: 0 + this.axisMargin - this.lineWidth - 0.5,
            w: this.canvas.width - this.axisMargin * 2 + this.lineWidth * 2 + 0.5,
            h: this.canvas.height - this.axisMargin * 2 + this.lineWidth * 2 + 0.5,
        };
    }
    initConfig(config) {
        if (!config.axisXConfig && !config.axisYConfig) {
            throw new Error('');
        }
        this.axisMargin = config.axisMargin || 0;
        this.showX = !!config.axisXConfig;
        this.showY = !!config.axisYConfig;
        this.negative = (config.negative || config.axisYConfig && config.axisYConfig.minValue < 0);
        config.axisXConfig && (Object.assign(this.axisXConfig, config.axisXConfig));
        config.axisYConfig && (Object.assign(this.axisYConfig, config.axisYConfig));
        this._initXAxios();
        this._initYAxios();
    }
    /** ***********  *************/
    resize(w, h) {
        if (!w || !h) {
            const rect = this.canvas.getBoundingClientRect();
            w = w || rect.width;
            h = h || rect.height;
            w = Math.round(w);
            h = Math.round(h);
        }
        // adjust xAxisOffset by anchor x
        // if (this.canvas.width !== 0) {
        //     this.transferX((w - this.canvas.width) * this.xAnchor);
        // }
        if (this.canvas.width === w && this.canvas.height === h) {
            return;
        }
        this.canvas.width = w;
        this.canvas.height = h;
        this.render();
    }
    /**
     * 
     */
    render() {
        this.clear();
        this.updateGrids();
        this.updateLabels();
    }
    transfer(x, y) {
        return {
            x: this.transferX && this.transferX(x) || 0,
            y: this.transferY && this.transferY(y),
        };
    }
    /**
     * 
     */
    updateLabels() {
        this.renderLabelX && this.axisXConfig.showLabel && this.renderLabelX();
        this.renderLabelY && this.axisYConfig.showLabel && this.renderLabelY();
    }
    clear() {
        this.ctx?.clearRect(-10, -10, this.canvas.width + 10, this.canvas.height + 10);
    }
    updateGrids() {
        this.ctx.save();
        this.ctx.rect(this.anchorInfo.x, this.anchorInfo.y, this.anchorInfo.w, this.anchorInfo.h);
        this.ctx.clip();
        const left = this.pixelToValueX(0 + this.axisMargin);
        const right = this.pixelToValueX(this.canvas.width - this.axisMargin * 2);
        this.xTicks.range(left, right, this.canvas.width - this.axisMargin * 2);
        this.renderX && this.renderX();
        const top = this.pixelToValueY(0 + this.axisMargin);
        const bottom = this.pixelToValueY(this.canvas.height - this.axisMargin * 2);
        this.yTicks.range(top, bottom, this.canvas.height - this.axisMargin * 2);
        this.renderY && this.renderY();
        this.ctx.restore();
    }
    valueToPixel(point) {
        return {
            x: (this.valueToPixelX && this.valueToPixelX(point.x)) ?? point.x,
            y: (this.valueToPixelY && this.valueToPixelY(point.y)) ?? point.y,
        };
    }
    pixelToValue(point) {
        return {
            x: (this.pixelToValueX && this.pixelToValueX(point.x)) ?? point.x,
            y: (this.pixelToValueY && this.pixelToValueY(point.y)) ?? point.y,
        };
    }
    formatValue(point) {
        return {
            x: this.xFormat && this.xFormat(point.x),
            y: this.yFormat && this.yFormat(point.y),
        };
    }
    valueToFormat(point) {
        return {
            x: (this.xDefaultFormat && this.xDefaultFormat(point.x)) ?? point.x,
            y: (this.yDefaultFormat && this.yDefaultFormat(point.y)) ?? point.y,
        };
    }
    /**
     * /
     * @param pixelPoint
     * @returns
     */
    patchPointInRange(pixelPoint) {
        const pointValue = this.pixelToValue(pixelPoint);
        let dirty = false;
        if (this.axisXConfig && (this.axisXConfig.maxValue < pointValue.x || pointValue.x < this.axisXConfig.minValue)) {
            const { maxValue, minValue } = this.axisXConfig;
            pointValue.x = (pointValue.x > maxValue) ? maxValue : minValue;
            pixelPoint.x = this.valueToPixelX(pointValue.x * this.xMaxValue);
            dirty = true;
        }
        if (this.axisYConfig && (this.axisYConfig.maxValue < pointValue.y || pointValue.y < this.axisYConfig.minValue)) {
            const { maxValue, minValue } = this.axisYConfig;
            pointValue.y = (pointValue.y > maxValue) ? maxValue : minValue;
            pixelPoint.y = this.valueToPixelY(pointValue.y * this.yMaxValue);
            dirty = true;
        }
        return dirty ? pointValue : null;
    }
    /**
     *
     * @param x value
     * @returns
     */
    checkXInRange(x) {
        if (this.axisXConfig && (this.axisXConfig.realMaxValue < x || x < this.axisXConfig.realMaxValue)) {
            return this.xAxisScale;
        }
        this.xAxisOffset = 0;
        const pixel = this.canvas.width - this.axisMargin * 2;
        return pixel / this.axisXConfig.realMaxValue;
    }
    /**
     *
     * @param y value
     * @returns
     */
    checkYInRange(y) {
        if (this.axisYConfig && (this.axisYConfig.realMaxValue < y || y < this.axisYConfig.minValue)) {
            return this.yAxisScale;
        }
        let scale = 1;
        if (this.negative) {
            this.yAxisOffset = this.axisMargin;
            scale = (this.height - this.axisMargin) / this.axisYConfig.realMaxValue;
        }
        else {
            this.yAxisOffset = 0;
            scale = (this.canvas.height - this.axisMargin * 2) / this.axisYConfig.realMaxValue;
        }
        return scale;
    }
    setDisplayRange(xRange, yRange) {
        const [minX, maxX] = xRange;
        const [minY, maxY] = yRange;
        this.xAxisScale = (this.canvas.width - this.axisMargin * 2) / (maxX - minX);
        this.xAxisOffset = -minX * this.xAxisScale;
        this.yAxisScale = (this.height - this.axisMargin) / (maxY - minY);
        this.yAxisOffset = maxY * this.yAxisScale - this.height;
        this.yAxisOffset = 0;
    }
    /**
     * 
     */
    resetScale() {
        this.xAxisScale = this.checkXInRange(this.axisXConfig.realMaxValue);
        this.yAxisScale = this.checkYInRange(this.axisYConfig.realMaxValue);
    }
    /**
     * 
     */
    _initXAxios() {
        const { lods, startOffset, showLine, lineColor, $container, format, maxValue, precision, showLabel, minStep } = this.axisXConfig;
        const realMaxValue = maxValue === Infinity ? this.canvas.width : (maxValue || this.canvas.width);
        this.axisXConfig.realMaxValue = realMaxValue;
        this.xAxisOffset = startOffset ?? 0;
        // 
        this.xTicks = new LinearTicks();
        this.xTicks.initTicks(lods, 1 / Math.pow(10, precision || 2), realMaxValue).spacing(10, 100);
        this.xAxisScale = clamp(this.xAxisScale, this.xTicks.minValueScale, this.xTicks.maxValueScale);
        this.xAxisMinStep = 40;
        this.pixelToValueX = (x) => {
            x = x - this.axisMargin;
            return ((x - this.xAxisOffset) / this.xAxisScale);
        };
        this.valueToPixelX = (x) => {
            return (x * this.xAxisScale + this.xAxisOffset + this.axisMargin);
        };
        const left = this.pixelToValueX(0 + this.axisMargin);
        const right = this.pixelToValueX(this.canvas.width - this.axisMargin * 2);
        this.xTicks.range(left, right, this.canvas.width - this.axisMargin * 2);
        this.xMaxValue = this.xTicks.maxValue;
        this.transferX = (deltaPixelX) => {
            const { startOffset } = this.axisXConfig;
            // 
            if (this.xAxisOffset === startOffset && deltaPixelX > 0) {
                return 0;
            }
            let newOffset = this.xAxisOffset + deltaPixelX;
            if (newOffset > startOffset) {
                newOffset = startOffset;
            }
            // 
            deltaPixelX = newOffset - this.xAxisOffset;
            this.xAxisOffset = newOffset;
            return deltaPixelX;
        };
        if (maxValue) {
            const scale = this.checkXInRange(this.axisXConfig.maxValue);
            this.xTicks.minValueScale = scale;
            this.xAxisScale = scale;
        }
        this.xAxisScaleAt = (pixelX, scale) => {
            // const oldScaleX = this.xAxisScale;
            const oldValueX = this.pixelToValueX(pixelX);
            // 
            // const scalePositive = scale > this.xAxisScale;
            this.xAxisScale = clamp(scale, this.xTicks.minValueScale, this.xTicks.maxValueScale);
            const deltaX = pixelX - this.valueToPixelX(oldValueX);
            this.transferX(deltaX);
            // 
            // if (!scalePositive) {
            //     const max = this.pixelToValueX(this.canvas.width);
            //     if (max > this.xMaxValue) {
            //         this.transferX!(-deltaX);
            //         this.xAxisScale = oldScaleX;
            //     }
            // } else {
            //     const min = this.pixelToValueX(0);
            //     if (min < this.xMinValue) {
            //         this.transferX!(-deltaX);
            //         this.xAxisScale = oldScaleX;
            //     }
            // }
            return this.xAxisScale;
        };
        this.xDefaultFormat = (value) => {
            return parseFloat((value).toFixed(precision));
        };
        if (format) {
            this.xFormat = format;
        }
        else {
            this.xFormat = this.xDefaultFormat;
        }
        //  x  label
        if ($container && showLabel) {
            this.renderLabelX = () => {
                const labelLevel = this.xTicks.levelForStep(this.xAxisMinStep);
                const ticks = this.xTicks.ticksAtLevel(labelLevel, false);
                let axisString = '';
                const renderLabel = (num) => {
                    const x = Math.floor(this.valueToPixelX(num)) - 5;
                    const text = this.xFormat(num);
                    axisString += `<span style="transform: translateX(${Math.floor(x)}px);">${text}</span>`;
                };
                ticks.forEach(renderLabel);
                this.axisXConfig.$container.innerHTML = axisString;
            };
        }
        if (showLine) {
            const color = Chroma(lineColor).rgb();
            this.renderX = () => {
                let ticks;
                let ratio;
                let screenX;
                const ctx = this.ctx;
                const renderLine = (val) => {
                    ctx.beginPath();
                    screenX = this.valueToPixelX(val);
                    //  0.5 
                    ctx.moveTo(Math.floor(screenX) + 0.5, 0);
                    ctx.lineTo(Math.floor(screenX) + 0.5, this.canvas.height);
                    ctx.stroke();
                };
                for (let i = this.xTicks.minTickLevel; i <= this.xTicks.maxTickLevel; ++i) {
                    ratio = this.xTicks.tickRatios[i];
                    if (ratio > 0) {
                        ctx.strokeStyle = `rgba(${color.toString()}, ${ratio * 3})`;
                        ticks = this.xTicks.ticksAtLevel(i, true);
                        for (const j of ticks) {
                            renderLine(j);
                        }
                    }
                }
                // 
                // if (this.xTicks!.maxValue >= this.xMaxValue) {
                //     ctx.save();
                //     ctx.strokeStyle = 'red';
                //     ctx.setLineDash([2,3])
                //     renderLine(this.xMaxValue);
                //     ctx.restore();
                // }
            };
        }
    }
    /**
     * 
     */
    _initYAxios() {
        const { lods, minScale, maxScale, lineColor, startOffset, showLine, $container, format, maxValue, precision, showLabel, minStep } = this.axisYConfig;
        const realMaxValue = maxValue === Infinity ? this.canvas.height : (maxValue || this.canvas.height);
        this.axisYConfig.realMaxValue = realMaxValue;
        this.yAxisOffset = startOffset || 0;
        // 
        // TODO  40  4 
        this.yAxisMinStep = 30;
        this.yTicks = new LinearTicks();
        this.yTicks.initTicks(lods, 0.01, realMaxValue).spacing(10, 100);
        this.yAxisScale = clamp(this.yAxisScale, this.yTicks.minValueScale, this.yTicks.maxValueScale);
        this.pixelToValueY = (y) => {
            return ((this.height - y - this.axisMargin + this.yAxisOffset) / this.yAxisScale);
        };
        this.valueToPixelY = (y) => {
            return (-y * this.yAxisScale + this.height + this.yAxisOffset - this.axisMargin);
        };
        const top = this.pixelToValueY(0 + this.axisMargin);
        const bottom = this.pixelToValueY(this.canvas.height - this.axisMargin * 2);
        this.yTicks.range(top, bottom, this.canvas.height - this.axisMargin * 2);
        this.yMaxValue = this.yTicks.maxValue;
        this.yDefaultFormat = (value) => {
            return parseFloat((value).toFixed(precision));
        };
        if (format) {
            this.yFormat = format;
        }
        else {
            this.yFormat = this.yDefaultFormat;
        }
        if (maxValue) {
            const scale = this.checkYInRange(this.axisYConfig.maxValue);
            this.yTicks.minValueScale = scale;
            this.yAxisScale = scale;
        }
        this.transferY = (deltaPixelY) => {
            if (!this.showY) {
                return 0;
            }
            const newValue = this.yAxisOffset + deltaPixelY;
            if (!this.negative && newValue < 0) {
                return 0;
            }
            return this.yAxisOffset = newValue;
        };
        if ($container && showLabel) {
            this.renderLabelY = () => {
                const labelLevel = this.yTicks.levelForStep(this.yAxisMinStep);
                const ticks = this.yTicks.ticksAtLevel(labelLevel, false);
                let axisString = '';
                ticks.forEach((num) => {
                    const y = Math.floor(this.valueToPixelY(num)) - 10;
                    const text = this.yFormat(num);
                    axisString += `<span style="transform: translateY(${Math.floor(y)}px);">${text}</span>`;
                });
                this.axisYConfig.$container.innerHTML = axisString;
            };
        }
        if (showLine) {
            const color = Chroma(lineColor).rgb();
            this.renderY = () => {
                let ticks;
                let ratio;
                let screenY;
                const ctx = this.ctx;
                const renderLine = (val) => {
                    ctx.beginPath();
                    screenY = this.valueToPixelY(val);
                    //  0.5 
                    ctx.moveTo(0, Math.floor(screenY) + 0.5);
                    ctx.lineTo(this.canvas.width, Math.floor(screenY) + 0.5);
                    ctx.stroke();
                };
                for (let i = this.yTicks.minTickLevel; i <= this.yTicks.maxTickLevel; ++i) {
                    ratio = this.yTicks.tickRatios[i];
                    if (ratio > 0) {
                        ctx.strokeStyle = `rgba(${color.toString()}, ${ratio * 3})`;
                        ticks = this.yTicks.ticksAtLevel(i, true);
                        for (const j of ticks) {
                            renderLine(j);
                        }
                    }
                }
                // 
                // if (this.yTicks!.maxValue >= this.yMaxValue) {
                //     ctx.save();
                //     ctx.strokeStyle = 'red';
                //     ctx.setLineDash([2,3])
                //     renderLine(this.yMaxValue);
                //     ctx.restore();
                // }
            };
        }
        this.yAxisScaleAt = function (pixelY, scale) {
            const oldValueX = this.pixelToValueY(pixelY);
            this.yAxisScale = clamp(scale, this.yTicks.minValueScale, this.yTicks.maxValueScale);
            const newScreenY = this.valueToPixelY(oldValueX);
            this.transferY(pixelY - newScreenY);
            return this.yAxisScale;
        };
    }
}
