'use strict';
import { OperationBase } from '../base';
const fs = require('fs');
const Chroma = require('chroma-js');
let customStyle = '';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
import HTML from './index.html';
import STYLE from './index.less';
const instanceArray = [];
// 
let panelHandle;
export class Gradient extends OperationBase {
    // ////////////////////
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$colorWrap = this.shadowRoot.querySelector('.color-wrap');
        this.$color = this.shadowRoot.querySelector('.color');
        this.$colorWrap.$root = this.$color.$root = this;
    }
    /**
     * 
     * @param {filePath} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        //  css
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        const value = this.getAttribute('value');
        if (!value) {
            return value;
        }
        try {
            return JSON.parse(value);
        }
        catch (error) {
            console.warn(error);
            return null;
        }
    }
    set value(val) {
        if (!val) {
            this.removeAttribute('value');
            return;
        }
        if (typeof val !== 'object') {
            val = { alpha: [], color: [] };
        }
        try {
            this.setAttribute('value', JSON.stringify(val));
            if (this.invalid) {
                this.invalid = false;
            }
        }
        catch (error) {
            console.warn(error);
            this.removeAttribute('value');
            this.invalid = true;
        }
    }
    get invalid() {
        return this.hasAttribute('invalid');
    }
    set invalid(val) {
        if (val) {
            this.setAttribute('invalid', '');
        }
        else {
            this.removeAttribute('invalid');
        }
    }
    get readonly() {
        return this.hasAttribute('readonly');
    }
    set readonly(val) {
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        // 
        if (this.shadowRoot) {
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        //  attribute
        this.readonly = this.hasAttribute('readonly');
        this.disabled = this.hasAttribute('disabled');
        // 
        this.addEventListener('click', this._onClick);
        const tabindex = this.getAttribute('tabindex') || '0';
        this.$colorWrap.setAttribute('tabindex', this.disabled ? '-1' : tabindex);
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        this.removeEventListener('click', this._onClick);
        const index = instanceArray.indexOf(this);
        // 
        instanceArray.splice(index, 1);
        this.dispatch('confirm');
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return [
            ...super.observedAttributes,
            'value',
            'invalid',
            'path',
        ];
    }
    /**
     * attribute 
     *
     * o oldValue
     * nnewValue
     */
    attributeChangedCallback(attr, o, n) {
        super.attributeChangedCallback(attr, o, n);
        switch (attr) {
            case 'value':
                if (o === n) {
                    break;
                }
                this._updateColor();
                break;
            case 'invalid':
                if (o === n) {
                    break;
                }
                this._updateColor();
                break;
        }
    }
    // ////////////////////
    // 
    /**
     * 
     */
    _updateColor() {
        // 
        const value = this.invalid ? { color: [], alpha: [] } : this.value || { color: [], alpha: [] };
        // 
        const alphas = value.alpha || [{ value: 1, progress: 0 }];
        const colors = value.color || [{ value: '#ffffff', progress: 0 }];
        //  color  alpha 
        const map = (this._map = {
            0: { r: 255, g: 255, b: 255, a: 255 },
            1: { r: 255, g: 255, b: 255, a: 255 }, // 
        });
        // 
        alphas.forEach((item, index) => {
            const color = (map[item.progress] = map[item.progress] || {});
            color.a = (item.value * 255) | 0;
            if (index === alphas.length - 1) {
                map[1].a = (item.value * 255) | 0;
            }
            if (index === 0) {
                map[0].a = (item.value * 255) | 0;
            }
        });
        // 
        colors.forEach((item, index) => {
            const rgb = Chroma(item.value).rgb();
            const color = (map[item.progress] = map[item.progress] || {});
            color.r = rgb[0];
            color.g = rgb[1];
            color.b = rgb[2];
            if (index === colors.length - 1) {
                map[1].r = rgb[0];
                map[1].g = rgb[1];
                map[1].b = rgb[2];
            }
            if (index === 0) {
                map[0].r = rgb[0];
                map[0].g = rgb[1];
                map[0].b = rgb[2];
            }
        });
        // 
        const keys = Object.keys(map).sort();
        //  style 
        function fill(index, name) {
            const key = keys[index];
            let _i;
            _i = index;
            let prev;
            do {
                _i -= 1;
                prev = map[keys[_i]];
            } while (prev[name] === undefined);
            _i = index;
            let next;
            do {
                _i += 1;
                next = map[keys[_i]];
            } while (next[name] === undefined);
            const progress = (key - keys[index - 1]) / (keys[index + 1] - keys[index - 1]);
            return (next[name] - prev[name]) * progress + prev[name];
        }
        //  style
        let hybridStyle = 'linear-gradient(to right';
        keys.forEach((key, index) => {
            const color = map[key];
            if (color.r === undefined) {
                color.r = Math.round(fill(index, 'r'));
            }
            if (color.g === undefined) {
                color.g = Math.round(fill(index, 'g'));
            }
            if (color.b === undefined) {
                color.b = Math.round(fill(index, 'b'));
            }
            if (color.a === undefined) {
                color.a = Math.round(fill(index, 'a'));
            }
            color.progress = key;
        });
        keys.forEach((key, index) => {
            const color = map[key];
            hybridStyle += `, rgba(${color.r}, ${color.g}, ${color.b}, ${color.a / 255}) ${(key * 100) | 0}%`;
            if (this.fixed && keys[index + 1]) {
                const nextColor = map[keys[index + 1]];
                hybridStyle += `, rgb(${nextColor.r}, ${nextColor.g}, ${nextColor.b}, ${nextColor.a / 255}) ${((key * 100) | 0) - 0.01}%`;
            }
            color.progress = key;
        });
        hybridStyle += ')';
        this.$color.style.background = hybridStyle;
    }
    /**
     * wrap 
     */
    _onClick(event) {
        // 
        if (event.target.disabled || event.target.readonly) {
            return;
        }
        const $elem = this;
        // 
        if (panelHandle) {
            panelHandle(event);
        }
        // 
        $elem.dispatch('open');
    }
    /**
     * 
     */
    static registerPanelHandle(fn) {
        if (typeof fn !== 'function') {
            throw new Error('panelHandle is not a function');
        }
        panelHandle = fn;
    }
}
