// TODO  API
export function createMatrix() {
    return new DOMMatrix();
}
export function smoothScale(delta, scale) {
    scale = Math.pow(2, delta * 0.002) * scale;
    return scale;
}
/**
 * 
 * @param val
 * @param num
 */
export function toFixed(val, num) {
    return parseFloat((Math.round(val * Math.pow(10, num)) / Math.pow(10, num)).toFixed(num));
}
/**
 * @function clamp01
 * @param {number} val
 * @returns {number}
 *
 * Clamps a value between 0 and 1.
 */
export function clamp01(val) {
    return val < 0 ? 0 : val > 1 ? 1 : val;
}
/**
 * 
 * Take the value of the given boundary range
 * @param {number} val
 * @param {number} min
 * @param {number} max
 */
export function clamp(val, min, max) {
    return Math.min.call(null, Math.max.call(null, val, min), max);
}
export function createPoint(x, y, base) {
    base = base || { x: 0, y: 0 };
    return {
        x: base.x + x,
        y: base.y + y,
    };
}
export function getEnumList(enumData) {
    if (!enumData || typeof enumData !== 'object') {
        return [];
    }
    const list = [];
    for (const name in enumData) {
        const v = enumData[name];
        if (Number.isInteger(v)) {
            list.push({ name, value: v });
        }
    }
    return list;
}
/**
 *  event  ctrl  command
 * @param event
 */
export function checkCtrlOrCommand(event) {
    if (!event) {
        return false;
    }
    if (process.platform === 'win32') {
        return event.ctrlKey;
    }
    return event.metaKey;
}
