'use strict';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
import HTML from './index.html';
const customStyle = '';
// 
const GRID_MARGIN = 8;
export class Scaleplate extends HTMLElement {
    /**
     * 
     */
    constructor() {
        super();
        this._time = 0;
        this.hasInit = false;
        this.attachShadow({ mode: 'open' });
        if (!this.shadowRoot) {
            return;
        }
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        // 
        this.$grid = this.shadowRoot.querySelector('ui-grid');
        this.$container = this.shadowRoot.querySelector('.container');
        this.$label = this.shadowRoot.querySelector('.label');
        this.$pointer = this.shadowRoot.querySelector('.pointer');
        // @ts-ignore
        this.$container.$root = this.$pointer.$root = this.$grid.$root = this;
        const that = this;
        this.$grid.addEventListener('change', (event) => {
            that.dispatch('transform');
            this.$pointer.style.transform = `translateX(${this.$grid?.valueToPixelX(this._time) - GRID_MARGIN}px)`;
        });
        this.$container.addEventListener('mousedown', this._onGridMouseDown);
        // @ts-ignore
        this.resizeObserver = new window.ResizeObserver(() => {
            this.setConfig();
        });
    }
    ////////////////////////////
    get value() {
        return this._time || 0;
    }
    set value(val) {
        this.setAttribute('value', String(val));
    }
    get disabled() {
        return this.getAttribute('disabled') !== null;
    }
    set disabled(val) {
        val = !!val;
        if (val) {
            this.setAttribute('disabled', '');
            this.setAttribute('tabindex', '-1');
        }
        else {
            this.removeAttribute('disabled');
            this.setAttribute('tabindex', '0');
        }
    }
    get readonly() {
        return this.getAttribute('readonly') !== null;
    }
    set readonly(val) {
        val = !!val;
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    /**
     * 
     */
    connectedCallback() {
        if (!this.shadowRoot) {
            return;
        }
        // 
        const $style = this.shadowRoot.querySelector('#custom-style');
        if ($style) {
            $style.innerHTML = customStyle;
        }
        this.setConfig(this.getAttribute('config'));
        this.hasInit = true;
        const value = this.getAttribute('value');
        this._updateValue(value);
        this.resizeObserver.observe(this.$container);
    }
    /**
     * 
     */
    disconnectedCallback() {
        this.resizeObserver.unobserve(this.$container);
    }
    // get set focused
    // get set disabled
    /**
     * TODO config
     *  attribute 
     */
    static get observedAttributes() {
        return ['focused', 'readonly', 'disabled', 'value'];
    }
    /**
     * Attribute 
     * @param attr
     * @param oldValue
     * @param newValue
     */
    attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            case 'value':
                if (!this.hasInit) {
                    break;
                }
                this._updateValue(newValue);
                break;
            case 'config':
                if (!this.hasInit) {
                    break;
                }
                this.setConfig(newValue);
                break;
        }
    }
    // ////////////////////
    // 
    _onGridMouseDown(event) {
        if (event.button === 0) {
            // @ts-ignore
            const $root = this.$root;
            // 
            $root.$pointer.style.transform = `translateX(${event.offsetX - GRID_MARGIN}px)`;
            // @ts-ignore
            this.$root._time = this.$root.$grid.pixelToValueX(event.offsetX);
            event.preventDefault();
            event.stopPropagation();
            $root.dispatch('change');
            $root.dispatch('confirm');
            return;
        }
    }
    _updateValue(newValue) {
        const val = Number(newValue);
        this.showTimeInViewRange(val);
        this._time = typeof val === 'number' ? val : 0;
        this.$pointer.style.transform = `translateX(${this.valueToPixel(this._time)}px)`;
    }
    /**
     * 
     * @param eventName 
     */
    dispatch(eventName) {
        const event = document.createEvent('HTMLEvents');
        event.initEvent(eventName, true, true);
        this.dispatchEvent(event);
    }
    legalConfig(config) {
        try {
            if (typeof config === 'string') {
                this._config = JSON.parse(config) || {};
            }
            else if (config) {
                this._config = config;
            }
            else if (!this._config) {
                this._config = {};
            }
        }
        catch (err) {
            console.error(`Error: the config of ui-scale-plate element is illegal\n${err}`);
            this._config = {};
        }
    }
    setConfig(config) {
        this.legalConfig(config);
        this.$grid?.setConfig({
            showY: false,
            showX: true,
            axisXConfig: {
                minScale: this._config.minStep || 1,
                maxScale: this._config.max || 100,
                maxValue: this._config.max || 100,
                $container: this.$label,
                preci: this._config.preci || 0,
            },
            axisMargin: 0,
            lineColor: '#ccc',
            precision: 0,
        });
        this.$grid?.initGrid();
    }
    /**
     * 
     * @param val
     * @returns
     */
    valueToPixel(val) {
        return this.$grid?.valueToPixelX(val) - GRID_MARGIN;
    }
    /**
     * 
     * @param x
     * @returns
     */
    pixelToValue(x) {
        return this.$grid?.pixelToValueX(x + GRID_MARGIN);
    }
    /**
     * 
     * 
     * 
     * @param value
     */
    showTimeInViewRange(value = this._time) {
        const { start, end } = this.$grid?.getValueRang();
        if (value < start) {
            this.jumpTimeInHeader(value);
        }
        else if (value > end) {
            this.jumpTimeInFooter(value);
        }
    }
    /**
     * 
     * @param time
     */
    jumpTimeInHeader(time) {
        const startPixel = this.$grid?.valueToPixelX(time);
        return this.$grid?.move(0 - startPixel, 0);
    }
    /**
     * 
     * @param time
     */
    jumpTimeInFooter(time) {
        const endPixel = this.$grid?.valueToPixelX(time);
        return this.$grid?.move(this.$grid?.$ctrlCanvas?.width - endPixel, 0);
    }
}
