export default ":host {\n  position: relative;\n  display: flex;\n  height: 50px;\n  min-height: 50px;\n  box-sizing: border-box;\n  padding: 0 8px;\n  overflow: hidden;\n}\n:host .container {\n  width: 100%;\n  height: 100%;\n  position: relative;\n  padding-top: 30px;\n  box-sizing: border-box;\n}\n:host .label {\n  position: absolute;\n  top: 0;\n}\n:host .label span {\n  position: absolute;\n  white-space: nowrap;\n}\n:host ui-grid {\n  width: 100%;\n  height: 100%;\n  box-sizing: border-box;\n  border: 1px solid #cccccc;\n  border-top: none;\n}\n:host .pointer {\n  position: absolute;\n  top: 0;\n  left: 8px;\n  height: 100%;\n  box-sizing: border-box;\n  padding: 24px 0 1px 0;\n  cursor: ew-resize;\n  transform: translateX(-8px);\n  z-index: 1;\n}\n:host .pointer > * {\n  pointer-events: none;\n}\n:host .pointer ui-icon[value='play'] {\n  transform: rotate(90deg);\n  color: var(--color-danger-contrast);\n  position: absolute;\n  top: 20px;\n  left: 2px;\n  width: 9px;\n  height: 9px;\n  font-size: 9px;\n}\n:host .pointer span {\n  display: inline-block;\n  width: 1px;\n  background-color: var(--color-danger-fill);\n  height: 100%;\n}\n";