'use strict';
const fs = require('fs');
import { LayoutBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
import { domUtils, translate } from '../../../utils/utils';
const prefix = 'i18n:';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
// 
const cacheExpand = {};
/**
 * config
 * expand
 * focused
 * no-border
 * whole
 */
export class Section extends LayoutBase {
    /**
     * 
     */
    constructor() {
        super();
        this._noFocusFlag = false;
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$header = this.shadowRoot.querySelector('#header');
        this.$headerSlot = this.shadowRoot.querySelector('slot[name="header"]');
        this.$content = this.shadowRoot.querySelector('.content');
        this.$contentSlot = this.shadowRoot.querySelector('.content > slot');
        this.$scrollHeight = this.shadowRoot.querySelector('.scroll-height');
        this.$header.$root = this.$content.$root = this.$contentSlot.$root = this;
        this._useCacheExpand(this, this.getAttribute('cache-expand') || '');
        // @ts-ignore
        this.resizeObserver = new window.ResizeObserver(() => {
            this._recoverScroll(this);
        });
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get expand() {
        return this.hasAttribute('expand');
    }
    set expand(val) {
        if (val) {
            this.setAttribute('expand', '');
        }
        else {
            this.removeAttribute('expand');
        }
        //  expand 
        this.dispatch('expand');
        // 
        const key = this.getAttribute('cache-expand');
        if (key !== null) {
            cacheExpand[key] = val;
        }
    }
    get header() {
        return this.getAttribute('header') || '';
    }
    set header(val) {
        if (val) {
            this.setAttribute('header', val);
        }
        else {
            this.removeAttribute('header');
        }
    }
    get focused() {
        return this.hasAttribute('focused');
    }
    set focused(val) {
        if (val) {
            this.setAttribute('focused', '');
        }
        else {
            this.removeAttribute('focused');
        }
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    // ////////////////////
    _useCacheExpand($root, key) {
        if (!key || cacheExpand[key] === undefined) {
            return;
        }
        const val = cacheExpand[key];
        if (val) {
            $root.setAttribute('expand', '');
        }
        else {
            $root.removeAttribute('expand');
        }
    }
    /**
     * 
     */
    _onConnected() {
        instanceArray.push(this);
        if (this.shadowRoot) {
            // 
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        // content 
        this.expand = this.hasAttribute('expand');
        //  tabindex  foucs/blur
        const tabindex = this.getAttribute('tabindex') || '0';
        this.$header.setAttribute('tabindex', this.disabled ? '-1' : tabindex);
        // 
        this.addEventListener('focus', this._onFocus);
        this.addEventListener('blur', this._onBlur);
        this.addEventListener('mousedown', this._onMouseDown);
        // 
        this.$header.addEventListener('click', this._onHeaderClick);
        // 
        this.$content.addEventListener('scroll', this._onContentScroll);
        // 
        this.resizeObserver.observe(this.$content);
    }
    /**
     * 
     */
    _onDisconnected() {
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.removeEventListener('focus', this._onFocus);
        this.removeEventListener('blur', this._onBlur);
        this.removeEventListener('mousedown', this._onMouseDown);
        // 
        this.$header.removeEventListener('click', this._onHeaderClick);
        // 
        this.$content.removeEventListener('scroll', this._onContentScroll);
        // 
        this.resizeObserver.unobserve(this.$content);
    }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['focused', 'readonly', 'disabled', 'header', 'expand', 'scroll', 'cache-expand'];
    }
    /**
     * attribute 
     *
     * o oldValue
     * nnewValue
     */
    attributeChangedCallback(attr, o, n) {
        switch (attr) {
            case 'header':
                this._updateHeader(n);
                break;
            case 'readonly':
                this._updateUIAttr('readonly', n !== null, this);
                break;
            case 'disabled':
                this.$header.setAttribute('tabindex', n !== null ? '-1' : this.getAttribute('tabindex') || '0');
                this._updateUIAttr('disabled', n !== null, this);
                break;
            case 'scroll':
                this._recoverScroll(this);
                break;
            case 'cache-expand':
                this._useCacheExpand(this, n);
                break;
        }
    }
    // ////////////////////
    /**
     * 
     */
    _onContentScroll() {
        // @ts-ignore
        const $root = this.$root;
        window.clearTimeout($root._recoverScrollHeightTimeId);
        $root._recoverScrollHeightTimeId = window.setTimeout(() => {
            $root.dispatch('uiscroll');
        }, 300);
    }
    /**
     * 
     */
    _recoverScroll($root) {
        window.clearTimeout($root._recoverScrollHeightTimeId);
        window.clearTimeout($root._clearScrollHeightTimeId);
        $root._recoverScrollHeightTimeId = window.setTimeout(() => {
            if (!$root.hasAttribute('scroll')) {
                return;
            }
            const uuidScroll = JSON.parse($root.getAttribute('scroll'));
            if (uuidScroll && uuidScroll.scrollHeight !== undefined) {
                //  scrollTo 
                $root.$scrollHeight.style['height'] = `${uuidScroll.scrollHeight}px`;
                $root.$content.scrollTo(uuidScroll.scrollLeft, uuidScroll.scrollTop);
                /**
                 *  scrollHeight
                 * scrollTo  _onContentScroll 
                 *  _onContentScroll 
                 */
                $root._clearScrollHeightTimeId = setTimeout(() => {
                    $root.$scrollHeight.style['height'] = '0px';
                    $root.removeAttribute('scroll');
                }, 500);
            }
        }, 100);
    }
    /**
     * 
     */
    _updateUIAttr(attrName, value, parentNode) {
        if (!parentNode.childNodes) {
            return;
        }
        for (const item of parentNode.children) {
            if (!domUtils.isUIComponent(item)) {
                continue;
            }
            item[attrName] = !!value;
        }
    }
    /**
     * focus
     */
    _onFocus() {
        if (this._noFocusFlag) {
            return;
        }
        this.focused = true;
    }
    /**
     * blur 
     */
    _onBlur() {
        this.focused = false;
    }
    /**
     * header 
     */
    _onHeaderClick(event) {
        const $elem = this;
        const excludes = ['ui-num-input', 'input', 'ui-select', 'select', 'ui-checkbox', 'checkbox'];
        const target = event.target;
        const tag = target.tagName.toLowerCase();
        if (excludes.includes(tag)) {
            return;
        }
        $elem.$root.expand = !$elem.$root.expand;
    }
    /**
     * 
     */
    _onMouseDown(event) {
        if (this.disabled) {
            return;
        }
        // focusui-prop
        if (event.target !== this && !this.$header.contains(event.target) && event.target.slot !== 'header') {
            this._noFocusFlag = true;
            return;
        }
        this._noFocusFlag = false;
    }
    /**
     *  header 
     */
    _updateHeader(val) {
        this.$headerSlot.innerHTML = translate(val);
    }
}
