export default ":host {\n  user-select: none;\n  outline: none;\n  display: inline-block;\n  box-sizing: border-box;\n  font-size: inherit;\n  line-height: inherit;\n  font-family: inherit;\n  border-radius: calc(var(--size-normal-radius) * 1px);\n  width: 100%;\n}\n:host .wrap-p {\n  display: flex;\n  align-items: center;\n  justify-content: flex-start;\n  color: var(--color-focus-contrast);\n  overflow: hidden;\n  position: relative;\n  line-height: inherit;\n}\n:host > span {\n  visibility: hidden;\n  display: block;\n  margin: -1px;\n}\n:host .wrap {\n  position: relative;\n  width: 100%;\n  height: 4px;\n  background-color: var(--color-normal-fill-weakest);\n  border-radius: 4px;\n}\n:host .bar {\n  position: absolute;\n  top: 50%;\n  left: 0;\n  background-color: var(--color-focus-fill);\n  text-align: right;\n  transition: width 0.3s ease, background-color 0.3s ease;\n  display: flex;\n  height: 4px;\n  width: 0;\n  transform: translate(0, -50%);\n  border-radius: 4px;\n}\n:host .percent {\n  display: none;\n  margin-left: 8px;\n  white-space: nowrap;\n}\n:host([percent]) .percent {\n  display: inline-block;\n}\n:host([success]) {\n  color: var(--color-success-contrast);\n}\n:host([success]) .bar {\n  background-color: var(--color-success-fill) !important;\n}\n:host([failure]) {\n  color: var(--color-danger-contrast);\n}\n:host([failure]) .bar {\n  background-color: var(--color-danger-fill) !important;\n}\n";