/**
 *  point  canvas 
 */
import { EventEmitter } from 'events';
import { clamp } from '../../../../utils/math';
import { checkIsJSONString, interimTranslate } from '../../../../utils/utils';
import { CurveWrapMode, RealInterpMode, TangentWeightMode, WrapMode, } from './curve-base';
import { Grid } from './grid';
import { Hermite } from './hermite';
import { checkCtrlOrCommand, createPoint, getEnumList, smoothScale } from './utils';
const Chroma = require('chroma-js');
const lodash = require('lodash');
const PIXEL_RANGE = 20; // 
//  PIXEL_RANGE  PIXEL_RANGE  2  2 rgba
const UINT8_COLOR_DEFAULT = Array(4 * PIXEL_RANGE * PIXEL_RANGE)
    .fill(0)
    .toString();
const DEFAULT_CTRL_COLOR = 'rgba(255, 0, 0, 0.1)'; // 
const AUX_LINE_COLOR = 'rgba(255, 0, 0, 0.3)'; // 
const CLICK_RANGE = 5; // /
const ICON_U16 = {
    settings: '&#xe736',
};
const ICON_FONT_SIZE = 10;
const ICON_FONT_NAME = 'editor-icon';
const clipboardHandle = {};
const clipboard = {
    write: async (type = 'curve-editor.keys', data) => {
        try {
            if (clipboardHandle?.write) {
                clipboardHandle?.write(type, data);
            }
            else {
                await navigator.clipboard.writeText(JSON.stringify({ type, data }));
            }
        }
        catch (e) {
            console.error(e);
        }
    },
    read: async (type = 'curve-editor.keys') => {
        let fallBackData = null;
        if (clipboardHandle?.read) {
            fallBackData = clipboardHandle?.read(type);
        }
        else {
            const navigatorClipboardData = await navigator.clipboard.readText();
            // @ts-ignore
            if (navigatorClipboardData && checkIsJSONString(navigatorClipboardData)) {
                const parseNavigatorClipboardData = JSON.parse(navigatorClipboardData);
                if (type === parseNavigatorClipboardData?.type) {
                    fallBackData = parseNavigatorClipboardData.data;
                }
            }
            else {
                fallBackData = navigatorClipboardData;
            }
        }
        return fallBackData;
    },
};
const I18N_HEADER = 'ui-kit.curve_editor';
const HANDLE_SIZE = 40;
/**
 *  <ui-prop>
        <span slot="label">inTangent: </span>
        <ui-num-input slot="content"></ui-num-input>
    </ui-prop>
    <ui-prop>
        <span slot="label">outTangent: </span>
        <ui-num-input slot="content"></ui-num-input>
    </ui-prop>
 *
 */
export class CurveCtrl extends EventEmitter {
    constructor(canvas, config) {
        super();
        this.readonly = false;
        this.editCurveInfo = {};
        this.config = {
            handlerSize: HANDLE_SIZE,
            r: Math.floor(Math.sqrt(12)),
            strokeStyle: DEFAULT_CTRL_COLOR,
            showPreWrapMode: true,
            showPostWrapMode: true,
            spacingFrame: 5,
            wrapMode: WrapMode.CLAMP,
            sample: 1,
        };
        // 
        this.colors = {
            // 
            handler: 'gold',
            light: 'rgba(255, 190, 1, 0.3)',
            defaultLine: 'white',
            selectBoxLine: 'rgba(255, 255, 255, 0.3)',
            // 
            // selectBox * 0.2
            selectBoxFill: 'rgba(255, 255, 255, 0.1)',
        };
        this._rawCurveInfo = {};
        this.curvePools = []; // 
        // 
        this.curveKeyQueue = [];
        this.currentCurveKey = ''; //  key
        this.ctrlKeys = [];
        this.changeType = ''; // 
        this.ctrlState = {
            showAuxin: false,
            showCtrlHandle: false,
            lightCurve: false,
            lightCurveClip: false,
        };
        this.currentKeyIndex = null; // 
        this.activeBoxInfo = null; // 
        this.selectedInfo = null;
        this.mouseDownPoint = null; // 
        this._rawKeyFrames = [];
        this.editKeyInfo = null;
        this.eventHandlers = null; // 
        //   document mouseup 
        this.isMouseDownFromCurrent = false;
        this.translation = interimTranslate;
        this.canvas = canvas;
        this.ctx = canvas.getContext('2d');
        this.ctx.strokeStyle = this.config.strokeStyle;
        const gridCanvas = canvas.cloneNode();
        gridCanvas.className = 'grid';
        const canvasContainer = document.createElement('div');
        const hermiteCanvas = canvas.cloneNode();
        hermiteCanvas.className = 'curve';
        canvasContainer.append(hermiteCanvas);
        canvas.parentNode.insertBefore(canvasContainer, canvas);
        canvas.parentNode.insertBefore(gridCanvas, canvasContainer);
        this.canvasContainer = canvasContainer;
        this.config.handlerSize = config.handlerSize || HANDLE_SIZE;
        this.config.showPostWrapMode = config.showPostWrapMode ?? true;
        this.config.showPreWrapMode = config.showPreWrapMode ?? true;
        this.config.spacingFrame = config.spacingFrame || 4;
        this.config.sample = config.sample || 1;
        this.grid = new Grid(gridCanvas, config.gridConfig || {});
        this.curve = new Hermite(hermiteCanvas, this.grid, {
            sample: this.config.sample,
        });
        this.curvePools.push(this.curve);
        //  scale  dom 
        const main = document.createElement('div');
        main.className = 'scale-ctrl';
        const scaleCtrl = {
            main,
            top: document.createElement('div'),
            bottom: document.createElement('div'),
            right: document.createElement('div'),
            left: document.createElement('div'),
        };
        ['top', 'bottom', 'right', 'left'].forEach((name) => {
            scaleCtrl[name].setAttribute('name', name);
            scaleCtrl.main.append(scaleCtrl[name]);
        });
        // @ts-ignore
        this.scaleCtrl = scaleCtrl;
        this.canvas.after(this.scaleCtrl.main);
        const $editorBox = document.createElement('div');
        this.canvas.after($editorBox);
        $editorBox.className = 'mask';
        $editorBox.style.display = 'none';
        $editorBox.innerHTML = editorBoxTemplate(config.xAxisName, config.yAxisName);
        this.editBox = {
            main: $editorBox,
            x: $editorBox.firstElementChild.firstElementChild.lastElementChild,
            y: $editorBox.firstElementChild.lastElementChild.lastElementChild,
            box: $editorBox.firstElementChild,
        };
        this.editBox.x.setAttribute('min', String(this.grid.axisXConfig.minValue || 0));
        this.editBox.x.setAttribute('max', String(this.grid.axisXConfig.maxValue || 0));
        this.editBox.y.setAttribute('min', String(this.grid.axisYConfig.minValue || 0));
        this.editBox.y.setAttribute('max', String(this.grid.axisYConfig.maxValue || 0));
        // TODO  resetConfig 
        this.emitChange = lodash.throttle(() => {
            this.emit('change');
        }, 60);
        this.emitConfirm = lodash.throttle(() => {
            this.emit('confirm');
        }, 60);
        this.registerHandler();
    }
    /**
     * 
     * @param beginCanvas
     * @param endPoint
     * @param ctx
     */
    static drawLine(beginCanvas, endPoint, ctx) {
        ctx.beginPath();
        ctx.moveTo(beginCanvas.x, beginCanvas.y);
        ctx.lineTo(endPoint.x, endPoint.y);
        ctx.closePath();
        ctx.stroke();
    }
    /**
     * 
     * @param point
     * @param radius
     * @param ctx
     */
    static drawArc(point, radius, ctx) {
        ctx.beginPath();
        ctx.moveTo(point.x, point.y);
        ctx.arc(point.x, point.y, radius, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.stroke();
        ctx.fill();
    }
    get currentSelectKeys() {
        if (!this.selectedInfo || this.selectedInfo.type !== 'keys') {
            return null;
        }
        return this.selectedInfo.info[0].keys;
    }
    resize(w, h) {
        this.canvas.width = w;
        this.canvas.height = h;
        this.grid.canvas.width = w;
        this.grid.canvas.height = h;
        this.curvePools.forEach((curve) => {
            curve.canvas.width = w;
            curve.canvas.height = h;
            //  canvas 
            curve.keyframes.forEach((item) => {
                item.canvas = this.grid.valueToPixel(item.point);
            });
        });
    }
    resetConfig(config) {
        this.config.handlerSize = config.handlerSize || HANDLE_SIZE;
        this.config.showPostWrapMode = config.showPostWrapMode ?? true;
        this.config.showPreWrapMode = config.showPreWrapMode ?? true;
        this.config.spacingFrame = config.spacingFrame || 4;
        this.canvasContainer.innerHTML = '';
        this.curvePools = [];
        this.config.sample = config.sample || 1;
        this.grid = new Grid(this.grid.canvas, config.gridConfig || {});
        this.editBox.x.setAttribute('min', String(this.grid.axisXConfig.minValue || 0));
        this.editBox.x.setAttribute('max', String(this.grid.axisXConfig.maxValue || 0));
        this.editBox.y.setAttribute('min', String(this.grid.axisYConfig.minValue || 0));
        this.editBox.y.setAttribute('max', String(this.grid.axisYConfig.maxValue || 0));
        this.curve = new Hermite(this.curve.canvas, this.grid, {
            sample: this.config.sample,
        });
        this.curvePools = [this.curve];
    }
    updateSample(sample) {
        this.config.sample = sample;
        if (!this.curvePools.length) {
            return;
        }
        this.curvePools.forEach((curve) => {
            curve.config.sample = sample;
            curve.paint(curve.keyframes);
        });
    }
    /**
     *  grid 
     * @param func
     * @param type
     */
    setGridFormat(func, type) {
        // @ts-ignore
        this.grid[`${type}Format`] = func;
    }
    /**
     * 
     */
    getCurrentCurveInfo() {
        const res = {};
        Object.keys(this.editCurveInfo).forEach((key) => {
            const info = this.editCurveInfo[key];
            res[key] = {
                keys: info.curve.keyframes,
                preWrapMode: info.curve.config.preWrapMode,
                postWrapMode: info.curve.config.postWrapMode,
                // color 
                color: info.curve.config.strokeStyle,
            };
        });
        return {
            curveInfos: res,
            wrapMode: this.config.wrapMode,
        };
    }
    /**
     * 
     * @param keyframes
     */
    paint(value) {
        this.clearAll();
        this.grid.render();
        // TODO 
        if (!value) {
            this.clearData();
            return;
        }
        this._rawCurveInfo = value.curveInfos;
        if (!Object.keys(value.curveInfos).length) {
            return;
        }
        this.initCurveInfo(value.curveInfos, value.wrapMode, value.duration);
        // 
        this.changeEditCurve(this.curveKeyQueue.includes(this.currentCurveKey) ? this.currentCurveKey : this.curveKeyQueue[0]);
        this.checkActiveInfo();
        this.paintCtrlPoint();
    }
    clearData() {
        this.editKeyInfo = null;
        this.currentCurveKey = '';
        this._rawKeyFrames = [];
        this.ctrlKeys = [];
        this.curvePools.forEach((curve => curve.paint([])));
        this.editCurveInfo = {};
    }
    initCurveInfo(curveInfos, wrapMode, duration) {
        this.editCurveInfo = {};
        this.curveKeyQueue = Object.keys(curveInfos);
        if (this.curvePools.length) {
            this.curvePools.forEach((curve) => {
                curve.canvas.style.display = 'none';
            });
        }
        this.curveKeyQueue.forEach((key, i) => {
            if (!this.curvePools[i]) {
                this.curvePools[i] = this.createNewCurve();
            }
            const curveInfo = curveInfos[key];
            const curve = this.curvePools[i];
            if (!curve.canvas.parentNode) {
                this.canvasContainer.append(curve.canvas);
            }
            curve.canvas.setAttribute('name', key);
            curve.canvas.style.display = 'block';
            curve.config.wrapMode = wrapMode;
            curve.config.duration = duration;
            curve.config.preWrapMode = curveInfo.preWrapMode;
            curve.config.postWrapMode = curveInfo.postWrapMode;
            curve.config.strokeStyle = curveInfo.color || 'red';
            curve.config.strokeStyleAxu = `rgba(${Chroma(curveInfo.color || 'red').rgb().toString()}, 0.3`;
            curve.clear();
            curve.paint(curveInfo.keys);
            const ctrlKeys = this.transToCtrlKey(curve.keyframes, this._rawCurveInfo[key].keys);
            this.editCurveInfo[key] = {
                curve,
                ctrlKeys,
                key,
            };
        });
    }
    createNewCurve() {
        const hermiteCanvas = this.curvePools[0].canvas.cloneNode();
        this.canvasContainer.append(hermiteCanvas);
        return new Hermite(hermiteCanvas, this.grid, this.curvePools[0].config);
    }
    checkActiveInfo() {
        if (!this.selectedInfo) {
            return;
        }
        if (this.selectedInfo.type === 'keys') {
            const newSelectCurveKeys = [];
            const selectedKeyInfos = this.selectedInfo.info;
            selectedKeyInfos.forEach((curveInfo) => {
                if (!this.editCurveInfo[curveInfo.key]) {
                    return;
                }
                const newSelectedKeys = [];
                const ctrlKeys = this.editCurveInfo[curveInfo.key].ctrlKeys;
                for (const info of curveInfo.keys) {
                    const key = ctrlKeys.find((item) => this.grid.xFormat(item.key.point.x) === this.grid.xFormat(info.key.point.x));
                    key && newSelectedKeys.push(key);
                }
                if (newSelectedKeys.length) {
                    newSelectCurveKeys.push({
                        key: curveInfo.key,
                        keys: newSelectedKeys,
                    });
                }
            });
            this.selectedInfo = newSelectCurveKeys.length ? {
                type: 'keys',
                info: newSelectCurveKeys,
            } : null;
        }
        if (this.selectedInfo && this.selectedInfo.type === 'curve') {
            // 
            this.lightCurve();
        }
        // 
        if (this.selectedInfo && this.selectedInfo.type === 'clip') {
            this.lightCurveClip();
        }
        // this.activeBoxInfo = null;
    }
    /**
     *  keyFrames  ctrlKeys 
     */
    rePaint() {
        this.clear();
        this.grid.render();
        if (!Object.keys(this.editCurveInfo).length) {
            return;
        }
        this.curveKeyQueue.forEach((curveName) => {
            const curveInfo = this.editCurveInfo[curveName];
            curveInfo.curve.keyframes.forEach((item) => {
                item.canvas = this.grid.valueToPixel(item.point);
            });
            if (!curveInfo.curve.keyframes.length) {
                return;
            }
            curveInfo.curve.rePaint();
            curveInfo.ctrlKeys.forEach((ctrlKey, index) => {
                const key = curveInfo.curve.keyframes[index];
                Object.assign(ctrlKey, this.calcCtrl(key, key));
            });
        });
        this.checkActiveInfo();
        this.paintCtrlPoint();
    }
    /**
     * 
     * @param type
     * @param value
     */
    updateCurveWrapMode(type, value) {
        this.curve.config[type] = value;
        // TODO 
        if (type === 'preWrapMode') {
            this.curve.paintPreWrapMode();
        }
        else {
            this.curve.paintPostWrapMode();
        }
    }
    // *****************  ************************************* //
    moveTimeLine(deltaX, deltaY) {
        this.grid.transferX && this.grid.transferX(deltaX);
        this.grid.transferY && this.grid.transferY(deltaY);
        return {
            x: (this.grid.transferX && this.grid.transferX(deltaX)) || 0,
            y: (this.grid.transferY && this.grid.transferY(deltaY)) || 0,
        };
    }
    moveTimeLineX(delta) {
        return this.grid.transferX(delta);
    }
    moveTimeLineY(delta) {
        return this.grid.transferY(delta);
    }
    scaleTimeLine(x, y, scale) {
        return this.scaleTimeLineY(y, scale) && this.scaleTimeLineX(x, scale); // 
    }
    scaleTimeLineY(y, scale) {
        const { yAxisScale } = this.grid;
        this.grid.yAxisScaleAt(y, smoothScale(scale, this.grid.yAxisScale));
        return yAxisScale !== this.grid.yAxisScale;
    }
    scaleTimeLineX(x, scale) {
        const { xAxisScale } = this.grid;
        this.grid.xAxisScaleAt(x, smoothScale(scale, this.grid.xAxisScale)); // 
        return xAxisScale !== this.grid.xAxisScale;
    }
    /**
     * 
     * @param position 
     */
    createNewKey(position) {
        const point = this.grid.pixelToValue(position);
        const index = this.curve.createNewKey(point);
        if (index === null) {
            console.warn('', point);
            return;
        }
        const raw = lodash.cloneDeep(this.curve.keyframes[index]);
        this.ctrlKeys.splice(index, 0, this.calcCtrl(this.curve.keyframes[index], raw));
        this.selectedInfo = {
            type: 'keys',
            info: [{
                    keys: [this.ctrlKeys[index]],
                    key: this.currentCurveKey,
                }],
        };
        this.currentKeyIndex = index;
        this.refreshRender();
        this.emitOperate('create-keys', this.selectedInfo.info);
        this.emitChange();
        this.emitConfirm();
    }
    /**
     *  x  y )
     * @param x (point)
     */
    createKeyInCurveAt(x) {
        // 
        const index = this.curve.createKeyInCurveAt(x);
        if (index === null) {
            console.warn('', x);
            return;
        }
        const raw = lodash.cloneDeep(this.curve.keyframes[index]);
        this.ctrlKeys.splice(index, 0, this.calcCtrl(this.curve.keyframes[index], raw));
        const info = this.ctrlKeys[index];
        this.emitOperate('create-keys', [{
                key: this.currentCurveKey,
                keys: [info],
            }]);
        this.paintCtrlPoint();
        this.drawCtrl();
        this.emitChange();
        this.emitConfirm();
    }
    // ********************  ***************************************** //
    /**
     * 
     * @param key
     */
    transToCtrlKey(keys, rawKeyFrames) {
        return keys.map((item, i) => {
            return this.calcCtrl(item, rawKeyFrames[i]);
        });
    }
    /**
     * 
     * @param index
     * @param type
     * @returns ICtrlKeyframe
     */
    calcCtrl(key, raw, type) {
        let outCanvas;
        let inCanvas;
        if (key.interpMode === RealInterpMode.CUBIC) {
            if (!key.broken) {
                key.inTangent = key.inTangent ?? key.outTangent ?? 0;
                key.outTangent = key.outTangent ?? key.inTangent ?? 0;
            }
            inCanvas = this.calcCtrlPoint(key, key.inTangent || 0, 'inTangent');
            outCanvas = this.calcCtrlPoint(key, key.outTangent, 'outTangent');
        }
        return {
            outCanvas,
            inCanvas,
            key,
            raw: lodash.cloneDeep(raw),
        };
    }
    /**
     * 
     */
    calcCtrlPoint(key, k, type) {
        if (([TangentWeightMode.LEFT, TangentWeightMode.BOTH].includes(key.tangentWeightMode) &&
            type === 'inTangent' &&
            key.inCtrlPoint) ||
            ([TangentWeightMode.RIGHT, TangentWeightMode.BOTH].includes(key.tangentWeightMode) && type === 'outTangent' && key.outCtrlPoint)) {
            return {
                type,
                ...(type === 'inTangent' ? this.grid.valueToPixel(key.inCtrlPoint) : this.grid.valueToPixel(key.outCtrlPoint)),
            };
        }
        if (typeof k !== 'number') {
            throw new Error(`k should be number.`);
        }
        const point = JSON.parse(JSON.stringify(key.canvas));
        k *= (this.grid.yAxisScale / this.grid.xAxisScale) / this.config.sample;
        point.y = this.canvas.height - point.y;
        const { handlerSize } = this.config;
        let x = 0;
        let y = 0;
        // //  canvas k  canvas 
        // if (k === Number.POSITIVE_INFINITY) {
        //     //  x 
        //     x = point.x;
        //     if (type === 'inTangent') {
        //         y = point.y + handlerSize;
        //     } else {
        //         y = point.y - handlerSize;
        //     }
        // } else if (k === 0) {
        //     if (type === 'inTangent') {
        //         x = point.x - handlerSize;
        //     } else {
        //         x = point.x + handlerSize;
        //     }
        //     y = point.y;
        // } else {
        // 
        x = Math.sqrt((handlerSize * handlerSize) / (1 + k * k));
        if (type === 'inTangent') {
            x = point.x - x;
        }
        else {
            x = point.x + x;
        }
        y = point.y - k * (point.x - x);
        // }
        y = this.canvas.height - y;
        return { x, y, type };
    }
    /**
     * 
     */
    delSelectedKeys() {
        if (!this.selectedInfo || this.selectedInfo.type !== 'keys') {
            return;
        }
        for (const info of this.selectedInfo.info) {
            const curveInfo = this.editCurveInfo[info.key];
            info.keys.forEach((keyInfo) => {
                const index = curveInfo.ctrlKeys.findIndex((item) => item === keyInfo);
                curveInfo.curve.delKeyFrame(index);
                curveInfo.ctrlKeys.splice(index, 1);
            });
        }
        this.emitOperate('remove-keys', this.selectedInfo.info);
        this.emitChange();
        this.emitConfirm();
        this.selectedInfo = null;
        this.currentKeyIndex = null;
        this.clear();
        this.refreshRender();
        this.paintCtrlPoint();
    }
    /**
     * 
     * @param endX
     * @param endY
     * @returns
     */
    moveSelectedCurveClip(endX, endY) {
        if (!this.selectedInfo || this.selectedInfo.type !== 'clip' || !this.mouseDownPoint) {
            return false;
        }
        const { offsetX: startX, offsetY: startY, startInfo } = this.mouseDownPoint;
        const { start, end, curveName } = this.selectedInfo.info[0];
        const selectedKeys = [this.ctrlKeys[start], this.ctrlKeys[end]];
        const hasMoveX = selectedKeys[0].key.canvas.x - startInfo.startKey.x;
        const hasMoveY = selectedKeys[0].key.canvas.y - startInfo.startKey.y;
        const moveX = endX - startX - hasMoveX;
        const moveY = endY - startY - hasMoveY;
        const curve = this.editCurveInfo[curveName].curve;
        curve.moveKeys(selectedKeys.map((item) => item.key), {
            x: moveX,
            y: moveY,
        });
        this.mouseDownPoint.keys = [{
                key: curveName,
                keys: selectedKeys,
            }];
        this.refreshRender();
        return true;
    }
    applyBezierToSelectedCurveClip(bezierDataArr) {
        if (!this.selectedInfo || this.selectedInfo.type !== 'clip') {
            return;
        }
        const { start, end, curveName } = this.selectedInfo.info[0];
        const res = this.curve.applyBezierToCurveClip(start, end, bezierDataArr);
        if (res) {
            this.clear();
            this.refreshRender();
            this.emitChange();
            this.emitOperate('apply-bezier', [{
                    key: curveName,
                    keys: [this.ctrlKeys[start], this.ctrlKeys[end]],
                }]);
        }
    }
    /**
     *  0
     * @returns
     */
    flatSelectedKeys() {
        if (!this.selectedInfo || this.selectedInfo.type !== 'keys') {
            return;
        }
        for (const info of this.selectedInfo.info) {
            const { curve, ctrlKeys } = this.editCurveInfo[info.key];
            info.keys.forEach((keyFrame) => {
                const index = ctrlKeys.findIndex((item) => item === keyFrame);
                curve.updateTan(index, 0, 'inTangent');
                curve.updateTan(index, 0, 'outTangent');
                curve.keyframes[index].broken = !!curve.keyframes[index].broken;
                curve.changeInterpMode(index, RealInterpMode.CUBIC);
                ctrlKeys[index] = Object.assign(ctrlKeys[index], this.calcCtrl(ctrlKeys[index].key, this._rawCurveInfo[info.key].keys[index]));
            });
        }
        this.emitOperate('change-interp-mode', this.selectedInfo.info);
        this.emitChange();
        this.emitConfirm();
        this.refreshRender();
    }
    /**
     * 
     * @returns
     */
    spacingSelectedKeys() {
        if (!this.selectedInfo || this.selectedInfo.type !== 'keys') {
            return;
        }
        this.selectedInfo.info.forEach((info) => {
            const curve = this.editCurveInfo[info.key].curve;
            curve.spacingKeys(info.keys.map((item) => item.key), this.config.spacingFrame);
        });
        this.emitOperate('move-keys', this.selectedInfo.info);
        this.emitChange();
        this.emitConfirm();
        this.refreshRender();
    }
    /**
     * 
     * @param index 
     */
    delKeyFrame(index) {
        this.curve.delKeyFrame(index);
        this.ctrlKeys.splice(index, 1);
        this.clear();
        this.refreshRender();
        this.paintCtrlPoint();
    }
    /**
     * 
     */
    refreshRender() {
        this.clear();
        this.curveKeyQueue.forEach((curveName) => {
            // 
            this.editCurveInfo[curveName] && this.editCurveInfo[curveName].curve.rePaint();
        });
        this.resetCtrl('reset');
        this.updateEditBoxPosition();
        // if (this.currentKeyIndex && this.selectedInfo.info) {
        //     // 
        //     // this.drawCtrl();
        // } else {
        //     // 
        //     this.paintSelectedKeys();
        // }
    }
    clear() {
        const { width, height } = this.canvas;
        this.ctx.clearRect(0, 0, width, height);
        this.ctrlState.lightCurve = false;
    }
    clearAll() {
        this.clear();
        this.curvePools.forEach((curve) => curve.clear());
        this.grid.clear();
    }
    /**
     * 
     * @param offsetX  x 
     * @param offsetY
     * @param type
     */
    moveHandleCtrl(offsetX, offsetY, type) {
        if (!this.ctrlKeys[this.currentKeyIndex]) {
            return;
        }
        const ctrlKey = this.ctrlKeys[this.currentKeyIndex];
        // 
        if (type === 'outTangent' && offsetX <= ctrlKey.key.canvas.x ||
            type === 'inTangent' && offsetX >= ctrlKey.key.canvas.x ||
            offsetY === ctrlKey.key.canvas.y) {
            return;
        }
        if (!ctrlKey.key.tangentWeightMode ||
            // @ts-ignore
            ctrlKey.key.tangentWeightMode === TangentWeightMode.NONE ||
            ctrlKey.key.tangentWeightMode === TangentWeightMode.RIGHT && type === 'inTangent' ||
            ctrlKey.key.tangentWeightMode === TangentWeightMode.LEFT && type === 'outTangent') {
            this.updateTan(offsetX, offsetY, type);
            return;
        }
        // weight 
        const lastKey = this.ctrlKeys[this.currentKeyIndex - 1];
        const minX = lastKey && lastKey.key.canvas.x || this.grid.valueToPixelX(0);
        const nextKey = this.ctrlKeys[this.currentKeyIndex + 1];
        const maxX = nextKey && nextKey.key.canvas.x || Infinity;
        // if (offsetX < minX && movementX < 0 || offsetX > maxX && movementX > 0) {
        //     return;
        // }
        const point = this.grid.pixelToValue({
            x: offsetX,
            y: offsetY,
        });
        const k = (point.y - ctrlKey.key.point.y) / ((point.x - ctrlKey.key.point.x) / this.config.sample);
        const res = this.curve.updateTangentWeight(this.currentKeyIndex, point, k, type);
        if (!res) {
            return;
        }
        if (type === 'inTangent') {
            ctrlKey.inCanvas = {
                x: offsetX,
                y: offsetY,
            };
        }
        else {
            ctrlKey.outCanvas = {
                x: offsetX,
                y: offsetY,
            };
        }
        Object.assign(ctrlKey, this.calcCtrl(ctrlKey.key, this._rawKeyFrames[this.currentKeyIndex]));
        this.emitChange();
        this.refreshRender();
    }
    updateKeyBrokenState(broken) {
        const ctrlKey = this.currentSelectKeys[0];
        ctrlKey.key.broken = broken;
        if (!broken && ctrlKey.key.inTangent !== ctrlKey.key.outTangent) {
            ctrlKey.key.outTangent = ctrlKey.key.inTangent;
            this.editCurveInfo[this.currentCurveKey].curve.updateTan(this.currentKeyIndex, ctrlKey.key.outTangent, 'inTangent');
            Object.assign(ctrlKey, this.calcCtrl(ctrlKey.key, ctrlKey.raw));
            this.refreshRender();
            this.drawCtrl();
        }
        // this.emitOperate('change-broken-state', this.selectedInfo.info as ICurveKeyInfos[]);
        // this.emitChange();
        // this.emitConfirm();
    }
    /**
     * 
     * @param offsetX  x 
     * @param offsetY
     */
    updateTan(offsetX, offsetY, type) {
        const index = this.currentKeyIndex;
        const { key: { canvas, point }, raw, } = this.ctrlKeys[this.currentKeyIndex];
        // 
        if (type === 'outTangent' && offsetX <= canvas.x ||
            type === 'inTangent' && offsetX >= canvas.x ||
            offsetY === canvas.y) {
            return;
        }
        const newPoint = this.grid.pixelToValue({
            x: offsetX,
            y: offsetY,
        });
        if (newPoint.x === point.x) {
            return;
        }
        let k = (newPoint.y - point.y) / ((newPoint.x - point.x) / this.config.sample);
        if (type === 'outTangent') {
            k = (point.y - newPoint.y) / ((point.x - newPoint.x) / this.config.sample);
        }
        if (k === Infinity) {
            return;
        }
        this.curve.updateTan(index, k, type);
        this.emitChange();
        Object.assign(this.ctrlKeys[index], this.calcCtrl(this.curve.keyframes[index], raw));
        this.refreshRender();
    }
    /**
     * 
     * @param endX
     * @param endY
     */
    moveSelectedKeys(endX, endY) {
        if (!this.selectedInfo || this.selectedInfo.type !== 'keys' || !this.mouseDownPoint) {
            return false;
        }
        const { offsetX: startX, offsetY: startY, startInfo } = this.mouseDownPoint;
        const hasMoveX = this.currentSelectKeys[0].key.canvas.x - startInfo.startKey.x;
        const hasMoveY = this.currentSelectKeys[0].key.canvas.y - startInfo.startKey.y;
        const moveX = endX - startX - hasMoveX;
        const moveY = endY - startY - hasMoveY;
        this.selectedInfo.info.forEach((info) => {
            const curve = this.editCurveInfo[info.key].curve;
            curve.moveKeys(info.keys.map((item) => item.key), {
                x: moveX,
                y: moveY,
            });
        });
        this.mouseDownPoint.keys = this.selectedInfo.info;
        this.updateActiveBox();
        this.refreshRender();
        return true;
    }
    /**
     * 
     * @param x  canvas  x
     * @param y  canvas  y
     * @param i
     */
    moveKey(x, y, i) {
        //  canvas  y 
        const index = this.curve.moveKey(x, y, i);
        if (index === -1) {
            return false;
        }
        this.changeType = 'move-keys';
        const newCtrl = Object.assign(this.ctrlKeys[i], this.calcCtrl(this.curve.keyframes[index], this.ctrlKeys[i].raw || this._rawKeyFrames[i]));
        this.ctrlKeys.splice(i, 1);
        this.ctrlKeys.splice(index, 0, newCtrl);
        this.selectedInfo.info = [{
                keys: [this.ctrlKeys[index]],
                key: this.currentCurveKey,
            }];
        this.mouseDownPoint.keys = this.selectedInfo.info;
        this.currentKeyIndex = index;
        this.refreshRender();
        return true;
    }
    // ****************************  ************************* //
    /**
     * canvas 
     */
    registerHandler() {
        if (this.eventHandlers) {
            return;
        }
        this.eventHandlers = {
            mousemove: this.onMouseMove.bind(this),
            mouseup: this.onMouseUp.bind(this),
            mousedown: this.onMouseDown.bind(this),
            wheel: this.onWheel.bind(this),
            scaleMouseDown: this.onScaleMouseDown.bind(this),
            editKeyConfirm: this.onEditKeyConfirm.bind(this),
        };
        this.canvas.addEventListener('mousemove', this.eventHandlers.mousemove);
        document.addEventListener('mouseup', this.eventHandlers.mouseup);
        this.canvas.addEventListener('mousedown', this.eventHandlers.mousedown);
        this.canvas.addEventListener('wheel', this.eventHandlers.wheel);
        this.scaleCtrl.main.addEventListener('mousedown', this.eventHandlers.scaleMouseDown);
        this.editBox.box.addEventListener('confirm', this.eventHandlers.editKeyConfirm);
    }
    unregisterHandler() {
        if (!this.eventHandlers) {
            return;
        }
        this.canvas.removeEventListener('mousemove', this.eventHandlers.mousemove);
        document.removeEventListener('mouseup', this.eventHandlers.mouseup);
        this.canvas.removeEventListener('mousedown', this.eventHandlers.mousedown);
        this.canvas.removeEventListener('wheel', this.eventHandlers.wheel);
        this.scaleCtrl.main.removeEventListener('mousedown', this.eventHandlers.scaleMouseDown);
        this.scaleCtrl.main.removeEventListener('mousemove', this.eventHandlers.scaleMouseMove);
        this.editBox.box.removeEventListener('confirm', this.eventHandlers.editKeyConfirm);
        this.eventHandlers = null;
    }
    onScaleMouseDown(event) {
        // @ts-ignore
        const target = event.target;
        event.stopPropagation();
        event.preventDefault();
        const name = target.getAttribute('name') + 'Scale';
        const { offsetX, offsetY } = event;
        this.mouseDownPoint = {
            offsetX,
            offsetY,
            button: event.button === 0 ? 'left' : 'right',
            dbClick: !!(this.lastMousedown && Date.now() - this.lastMousedown < 300),
            type: name,
            startInfo: this.activeBoxInfo && this.activeBoxInfo.box,
        };
    }
    onScaleMouseMove(event) {
        if (!this.mouseDownPoint || !this.mouseDownPoint.startInfo || !this.activeBoxInfo || !this.activeBoxInfo.box || !this.selectedInfo) {
            return;
        }
        const { w: startW, h: startH, x, y } = this.mouseDownPoint.startInfo;
        const { offsetX, offsetY } = event;
        const scaleOpt = {
            scale: 1,
            changeValue: 'x',
            base: 0,
        };
        const type = this.mouseDownPoint.type;
        switch (this.mouseDownPoint.type) {
            case 'topScale':
                this.activeBoxInfo.box.y = offsetY;
                this.activeBoxInfo.box.h += y - offsetY;
                scaleOpt.base = y + startH;
                scaleOpt.scale = this.activeBoxInfo.box.h / startH;
                break;
            case 'bottomScale':
                this.activeBoxInfo.box.h = offsetY - y;
                scaleOpt.scale = this.activeBoxInfo.box.h / startH;
                scaleOpt.base = y;
                break;
            case 'rightScale':
                this.activeBoxInfo.box.w = offsetX - x;
                scaleOpt.scale = this.activeBoxInfo.box.w / startW;
                scaleOpt.base = x;
                break;
            case 'leftScale':
                this.activeBoxInfo.box.x = offsetX;
                this.activeBoxInfo.box.w += x - offsetX;
                scaleOpt.scale = this.activeBoxInfo.box.w / startW;
                scaleOpt.base = x + startW;
                break;
        }
        if (scaleOpt.scale === 1 || scaleOpt.scale === 0) {
            return;
        }
        if (['topScale', 'bottomScale'].includes(type)) {
            scaleOpt.changeValue = 'y';
        }
        this.selectedInfo.info.forEach((info) => {
            const curve = this.editCurveInfo[info.key].curve;
            curve.scaleKeys(info.keys.map((item) => item.key), scaleOpt);
            info.keys.forEach((info) => {
                const key = info.key;
                info.inCanvas = this.calcCtrlPoint(key, key.inTangent || key.outTangent || 0, 'inTangent');
                info.outCanvas = this.calcCtrlPoint(key, key.outTangent || key.inTangent || 0, 'outTangent');
            });
        });
        this.changeType = 'scale-keys';
        this.mouseDownPoint.keys = this.selectedInfo.info;
        this.paintScaleCtrl();
        this.refreshRender();
    }
    onEditKeyConfirm(event) {
        if (!this.editKeyInfo) {
            return;
        }
        const name = event.target.getAttribute('name');
        // @ts-ignore
        this.editKeyInfo[name] = event.target.value;
    }
    applyEditKeyInfo() {
        const rawPoint = this.ctrlKeys[this.currentKeyIndex].key.point;
        if (rawPoint.x === this.editKeyInfo.x && rawPoint.y === this.editKeyInfo.y) {
            return false;
        }
        const index = this.curve.editKey(this.currentKeyIndex, this.editKeyInfo);
        if (index === -1) {
            return false;
        }
        const newCtrl = Object.assign(this.ctrlKeys[this.currentKeyIndex], this.calcCtrl(this.curve.keyframes[index], this.ctrlKeys[this.currentKeyIndex].raw || this._rawCurveInfo[this.currentCurveKey].keys[this.currentKeyIndex]));
        this.ctrlKeys.splice(this.currentKeyIndex, 1);
        this.ctrlKeys.splice(index, 0, newCtrl);
        this.selectedInfo.info = [{
                key: this.currentCurveKey,
                keys: [this.ctrlKeys[index]],
            }];
        this.currentKeyIndex = index;
        this.refreshRender();
        return true;
    }
    onWheel(event) {
        event.stopPropagation();
        event.preventDefault();
        let changed = true;
        const isMacOS = process.platform === 'darwin';
        const cltrKey = isMacOS ? event.metaKey : event.ctrlKey;
        // Mac  shift  deltaX 
        const delta = event.shiftKey && isMacOS ? event.deltaX : event.deltaY;
        //  shift + Ctrl 
        if (event.shiftKey && cltrKey) {
            changed = this.scaleTimeLineX(event.offsetX, -delta);
            //  alt + Ctrl 
        }
        else if (event.altKey && cltrKey) {
            changed = this.scaleTimeLineY(event.offsetY, -delta);
        }
        else if (event.shiftKey) {
            //  shift 
            changed = !!this.moveTimeLineX(delta);
        }
        else if (event.altKey) {
            //  alt 
            changed = !!this.moveTimeLineY(delta);
        }
        else {
            changed = this.scaleTimeLine(event.offsetX, event.offsetY, -event.deltaY);
        }
        // TODO 
        if (changed) {
            this.rePaint();
            this.emit('transform');
        }
    }
    onMouseDown(event) {
        event.stopPropagation();
        this.isMouseDownFromCurrent = true;
        this.canvas.setAttribute('focused', '');
        const { r } = this.config;
        const { offsetX, offsetY } = event;
        this.mouseDownPoint = {
            offsetX,
            offsetY,
            button: event.button === 0 ? 'left' : 'right',
            dbClick: !!(this.lastMousedown && (Date.now() - this.lastMousedown < 300)),
            type: 'empty',
        };
        this.lastMousedown = Date.now();
        // 
        if (this.selectedInfo && this.selectedInfo.type === 'keys' && this.activeBoxInfo && this.activeBoxInfo.box) {
            const { x, y, w, h } = this.activeBoxInfo.box;
            let inSelect = false;
            if (!w || !h) {
                // 
                for (const info of this.selectedInfo.info) {
                    const curveInfo = this.editCurveInfo[info.key];
                    // 
                    const currentKeyIndex = curveInfo.curve.findPointInKeys({
                        x: offsetX,
                        y: offsetY,
                    }, this.config.r * 2);
                    if (currentKeyIndex !== -1) {
                        inSelect = true;
                        break;
                    }
                }
            }
            else {
                if (offsetX > x && offsetX < x + w && offsetY > y && offsetY < y + h) {
                    inSelect = true;
                }
            }
            if (inSelect) {
                this.mouseDownPoint.type = 'activeBox';
                this.mouseDownPoint.keys = this.selectedInfo.info;
                // this.mouseDownPoint.dbClick && this.emitOperate('db-select', this.selectedInfo.info);
                // startInfo 
                this.mouseDownPoint.startInfo = {
                    startKey: JSON.parse(JSON.stringify(this.currentSelectKeys[0].key.canvas)),
                };
                return;
            }
        }
        // 
        if (this.selectedInfo && this.selectedInfo.type === 'keys' && this.ctrlState.showCtrlHandle && this.ctrlKeys[this.currentKeyIndex]) {
            const ctrlPoints = this.ctrlKeys[this.currentKeyIndex];
            for (const key of ['outCanvas', 'inCanvas']) {
                // @ts-ignore
                const info = ctrlPoints[key];
                if (!info) {
                    continue;
                }
                const { x, y } = info;
                // 
                if (Math.abs(offsetX - x) < CLICK_RANGE + r && Math.abs(offsetY - y) < CLICK_RANGE + r) {
                    this.mouseDownPoint.keys = this.selectedInfo.info;
                    this.mouseDownPoint.type = key === 'outCanvas' ? 'outTangent' : 'inTangent';
                    return;
                }
            }
        }
        // TODO  setting 
        // 
        const selectCurveNames = [];
        const selectKey = [-1, -1];
        for (let i = 0; i < this.curveKeyQueue.length; i++) {
            const curveInfo = this.editCurveInfo[this.curveKeyQueue[i]];
            // 
            let isInCurve = curveInfo.curve.isPointInCurve({
                x: offsetX,
                y: offsetY,
            }, this.config.r * 2);
            // 
            const currentKeyIndex = curveInfo.curve.findPointInKeys({
                x: offsetX,
                y: offsetY,
            }, this.config.r * 2);
            isInCurve = isInCurve || currentKeyIndex !== -1;
            if (isInCurve) {
                selectCurveNames.push(curveInfo.key);
            }
            else {
                continue;
            }
            selectKey[0] = selectCurveNames.length - 1;
            selectKey[1] = currentKeyIndex;
            break;
        }
        if (selectCurveNames.length) {
            // 
            let selectedKeyInfos = this.selectedInfo && this.selectedInfo.type === 'keys' ? this.selectedInfo.info : null;
            this.mouseDownPoint.type = 'curve';
            const curveName = selectCurveNames[selectKey[0]];
            let clickSelectedCurve = false;
            if (curveName === this.currentCurveKey) {
                clickSelectedCurve = true;
            }
            // 
            this.changeEditCurve(curveName);
            const currentCurve = this.editCurveInfo[curveName];
            if (selectKey[1] > -1) {
                const info = currentCurve.ctrlKeys[selectKey[1]];
                const enterCtrl = checkCtrlOrCommand(event);
                let multiKeys = false;
                if (enterCtrl && selectedKeyInfos) {
                    // 
                    const curveInfo = selectedKeyInfos.find((item) => item.key === curveName);
                    if (curveInfo) {
                        const currentIndex = curveInfo.keys.findIndex((keyInfo) => keyInfo.key.point.x === info.key.point.x);
                        multiKeys = true;
                        // 
                        if (currentIndex !== -1) {
                            curveInfo.keys.splice(currentIndex, 1);
                        }
                        else {
                            curveInfo.keys.push(info);
                        }
                    }
                    else {
                        selectedKeyInfos.push({
                            key: curveName,
                            keys: [info],
                        });
                    }
                }
                else {
                    selectedKeyInfos = [{
                            key: curveName,
                            keys: [info],
                        }];
                }
                this.mouseDownPoint.type = 'key';
                this.selectedInfo = {
                    type: 'keys',
                    info: selectedKeyInfos,
                };
                this.mouseDownPoint.keys = selectedKeyInfos;
                this.currentKeyIndex = multiKeys ? null : selectKey[1];
                if (event.button === 2) {
                    return;
                }
                this.canvas.style.cursor = 'move';
                this.emitOperate(this.mouseDownPoint.dbClick ? 'db-select' : 'select', this.selectedInfo.info);
                this.refreshRender();
                return;
            }
            else {
                // 
                this.mouseDownPoint.startInfo = {
                    startKey: JSON.parse(JSON.stringify(this.curve.keyframes[0])),
                };
                if (this.mouseDownPoint.dbClick) {
                    // 
                    if (clickSelectedCurve && this.selectedInfo.type === 'curve') {
                        this.selectedInfo = null;
                        return;
                    }
                    this.selectedInfo = {
                        type: 'curve',
                        info: [selectCurveNames[0]],
                    };
                    if (event.button !== 2) {
                        this.resetCtrl('reset');
                        this.canvas.style.cursor = 'ns-resize';
                    }
                    this.emitOperate('select-curve', [{
                            key: this.currentCurveKey,
                            keys: [this.ctrlKeys[0]],
                        }]);
                    return;
                }
                // 
                if (clickSelectedCurve && this.selectedInfo && this.selectedInfo.type === 'curve') {
                    return;
                }
                const clipInfo = currentCurve.curve.findCurveClipInKeys({
                    x: offsetX,
                    y: offsetY,
                });
                if (clipInfo) {
                    clipInfo.curveName = curveName;
                    this.mouseDownPoint.type = 'clip';
                    // 
                    this.selectedInfo = {
                        type: 'clip',
                        info: [clipInfo],
                    };
                    this.emitOperate('select-curve-clip', [{
                            key: this.currentCurveKey,
                            keys: [this.ctrlKeys[clipInfo.start], this.ctrlKeys[clipInfo.end]],
                        }]);
                    if (event.button !== 2) {
                        this.resetCtrl('reset');
                        this.canvas.style.cursor = 'ns-resize';
                    }
                }
            }
        }
        this.currentKeyIndex = null;
    }
    selectKeys(keyInfos) {
        if (!keyInfos) {
            this.selectedInfo = null;
            this.refreshRender();
            return;
        }
        const selectedKeyInfos = [];
        keyInfos.forEach((keyInfo) => {
            if (!this.editCurveInfo[keyInfo.key]) {
                return;
            }
            const keys = this.editCurveInfo[keyInfo.key].ctrlKeys.filter((ctrlKey) => keyInfo.frames.includes(ctrlKey.key.point.x));
            if (!keys.length) {
                return;
            }
            selectedKeyInfos.push({
                key: keyInfo.key,
                keys: keys,
            });
        });
        this.selectedInfo = {
            type: 'keys',
            info: selectedKeyInfos,
        };
        this.refreshRender();
    }
    changeEditCurve(key) {
        if (!this.editCurveInfo[key]) {
            return false;
        }
        const info = this.editCurveInfo[key];
        info.curve.canvas.style.zIndex = '1';
        this.curve = info.curve;
        this.ctrlKeys = info.ctrlKeys;
        this._rawKeyFrames = this._rawCurveInfo[key].keys;
        if (this.currentCurveKey === key) {
            return true;
        }
        if (this.currentCurveKey && this.editCurveInfo[this.currentCurveKey]) {
            this.editCurveInfo[this.currentCurveKey].curve.canvas.style.zIndex = '0';
        }
        this.currentCurveKey = key;
        // 
        this.selectedInfo = null;
        this.activeBoxInfo = null;
        this.currentKeyIndex = null;
        this.ctrlState = {
            showAuxin: false,
            showCtrlHandle: false,
            lightCurve: false,
            lightCurveClip: false,
        };
        this.changeType = '';
        return true;
    }
    /**
     *  ()
     * @returns
     */
    async getCopyKeys(type) {
        let keys = [];
        const data = await clipboard.read(type);
        if (Array.isArray(data) && data.length > 0) {
            keys = data;
        }
        return keys;
    }
    /**
     * 
     * @param offsetX
     * @param offsetY
     */
    async pasteCopyKeys(offsetX, offsetY) {
        const curveKeysInfos = await this.getCopyKeys();
        if (!curveKeysInfos.length) {
            return;
        }
        const keyInfos = [];
        curveKeysInfos.forEach((info) => {
            info.keys.forEach((info) => {
                info.key.canvas = this.grid.valueToPixel(info.key.point);
                keyInfos.push(info);
            });
        });
        if (!keyInfos.length) {
            return;
        }
        let moveX = 0;
        let moveY = 0;
        if (keyInfos.length > 1) {
            const box = this.calcActiveBox(keyInfos);
            // 
            moveX = offsetX - box.x;
            offsetY && (moveY = offsetY - box.y);
        }
        const selectedKeyInfos = [];
        let x = this.grid.pixelToValueX(offsetX);
        if (this.grid.xFormat) {
            x = Number(this.grid.xFormat(x));
        }
        if (this.selectedInfo
            && this.selectedInfo.type === 'keys'
            && this.selectedInfo.info.length === 1
            && this.selectedInfo.info[0].keys.length === 1
            && curveKeysInfos.length === 1
            && curveKeysInfos[0].keys.length === 1) {
            // 
            const newKey = JSON.parse(JSON.stringify(curveKeysInfos[0].keys[0].key));
            newKey.point.x = x;
            const index = this.curve.createNewKey(newKey.point, newKey);
            if (index === null) {
                console.warn('Invalid key info:', newKey);
                return;
            }
            const raw = lodash.cloneDeep(this.selectedInfo.info[0].keys[0].key);
            this.ctrlKeys.splice(index, 0, this.calcCtrl(this.curve.keyframes[index], raw));
            this.selectedInfo = {
                type: 'keys',
                info: [{
                        keys: [this.ctrlKeys[index]],
                        key: this.currentCurveKey,
                    }],
            };
            this.currentKeyIndex = index;
            this.refreshRender();
            this.emitOperate('create-keys', this.selectedInfo.info);
            this.emitChange();
            this.emitConfirm();
            return;
        }
        function pasteCurveInfo(curveKeysInfo, editCurveInfo) {
            const startCanvas = JSON.parse(JSON.stringify(curveKeysInfo.keys[0].key.canvas));
            moveX = moveX || offsetX - startCanvas.x;
            offsetY && (moveY = moveY || offsetY - startCanvas.y);
            startCanvas.x += moveX;
            startCanvas.y += moveY;
            const startPoint = this.grid.pixelToValue(startCanvas);
            // TODO  canvas 
            const newKeys = editCurveInfo.curve.pasteKeys(curveKeysInfo.keys.map((keyInfo) => keyInfo.key), startPoint.x, startPoint.y);
            newKeys.forEach((key, i) => {
                const indexInCtrlKeys = editCurveInfo.ctrlKeys.findIndex((ctrlKey) => this.grid.xFormat(ctrlKey.key.point.x) === this.grid.xFormat(key.point.x));
                const newCtrlKey = this.calcCtrl(key, curveKeysInfo.keys[i].key);
                selectedKeyInfos.push({
                    keys: [newCtrlKey],
                    key: editCurveInfo.key,
                });
                // 
                if (indexInCtrlKeys !== -1) {
                    editCurveInfo.ctrlKeys.splice(indexInCtrlKeys, 1, newCtrlKey);
                    return;
                }
                //  curve 
                const indexInCurveKeys = editCurveInfo.curve.keyframes.findIndex((keyInfo) => keyInfo.point.x === key.point.x);
                editCurveInfo.ctrlKeys.splice(indexInCurveKeys, 0, newCtrlKey);
            });
        }
        const editCurveKeys = Object.keys(this.editCurveInfo);
        if (editCurveKeys.length === 1) {
            //  key 
            const curveKeysInfo = curveKeysInfos.find((item) => item.key === editCurveKeys[0]) || curveKeysInfos[0];
            pasteCurveInfo.call(this, curveKeysInfo, this.editCurveInfo[editCurveKeys[0]]);
        }
        else {
            curveKeysInfos.forEach((curveKeysInfo) => {
                const curveInfo = this.editCurveInfo[curveKeysInfo.key];
                if (!curveInfo) {
                    return;
                }
                pasteCurveInfo.call(this, curveKeysInfo, curveInfo);
            });
        }
        this.selectedInfo = selectedKeyInfos.length ? {
            type: 'keys',
            info: selectedKeyInfos,
        } : null;
        this.emitOperate('create-keys', this.selectedInfo.info, x);
        this.emitOperate('select', this.selectedInfo.info);
        this.emitChange();
        this.emitConfirm();
        this.refreshRender();
    }
    /**
     * 
     */
    async copySelectedKeys(type) {
        if (!this.selectedInfo || this.selectedInfo.type !== 'keys') {
            return false;
        }
        await clipboard.write(type, this.selectedInfo.info);
        this.emitOperate('copy', this.selectedInfo.info);
        return true;
    }
    static registerClipboardHandle(clipboard) {
        if (clipboard) {
            if (clipboard.read && typeof clipboard.read === 'function') {
                clipboardHandle.read = clipboard.read;
            }
            else {
                console.error('clipboard.read not a function');
            }
            if (clipboard.write && typeof clipboard.write === 'function') {
                clipboardHandle.write = clipboard.write;
            }
            else {
                console.error('clipboard.write not a function');
            }
        }
        else {
            console.error('clipboard is undefined');
        }
    }
    onMouseMove(event) {
        if (!this.mouseDownPoint || this.readonly) {
            return;
        }
        event.stopPropagation();
        event.preventDefault();
        const { offsetX, offsetY, movementY, movementX, x, y } = event;
        const { offsetX: startX, offsetY: startY, type, button } = this.mouseDownPoint;
        // 
        if (Math.abs(offsetX - startX) <= 1 && Math.abs(offsetY - startY) <= 1) {
            return;
        }
        this.mouseDownPoint.offset = [Math.abs(offsetX - startX), Math.abs(offsetY - startY)];
        if (type) {
            this.mouseDownPoint.movingType = type;
        }
        if (type.endsWith('Scale')) {
            this.onScaleMouseMove(event);
            this.emitChange();
            return;
        }
        if (type === 'activeBox') {
            this.moveSelectedKeys(offsetX, offsetY);
            this.changeType = 'move-keys';
            this.emitChange();
            return;
        }
        // if (type === 'clip') {
        //     this.moveSelectedCurveClip(offsetX, offsetY);
        //     this.changeType = 'move-keys';
        //     this.emitChange();
        //     return;
        // }
        if (this.selectedInfo && this.selectedInfo.type === 'keys' && (type === 'inTangent' || type === 'outTangent')) {
            this.changeType = 'tangent';
            this.moveHandleCtrl(offsetX, offsetY, type);
            this.mouseDownPoint.keys = this.selectedInfo.info;
            this.emitChange();
            return;
        }
        if (type === 'key' && typeof this.currentKeyIndex === 'number') {
            this.moveKey(offsetX, offsetY, this.currentKeyIndex);
            this.changeType = 'move-keys';
            this.emitChange();
            return;
        }
        if (type === 'curve' && this.mouseDownPoint.startInfo) {
            const { canvas: startCanvas } = this.mouseDownPoint.startInfo.startKey;
            const { canvas } = this.curve.keyframes[0];
            const deltaY = (offsetY - startY) + startCanvas.y - canvas.y;
            const flag = this.curve.moveAllY(deltaY);
            if (!flag) {
                return;
            }
            this.mouseDownPoint.keys = [{
                    keys: this.ctrlKeys,
                    key: this.currentCurveKey,
                }];
            this.changeType = 'move-curve';
            this.emitChange();
            this.rePaint();
            return;
        }
        if (['timeline', 'empty'].includes(type) && button === 'right') {
            this.mouseDownPoint.type = 'timeline';
            // @ts-ignore
            event.target.style.cursor = '-webkit-grabbing';
            requestAnimationFrame(() => {
                const res = this.grid.transfer(movementX, movementY);
                if (res.x || res.y) {
                    Object.values(this.editCurveInfo).forEach((curveInfo) => {
                        curveInfo.curve.keyframes.forEach((item) => {
                            item.canvas = this.grid.valueToPixel(item.point);
                        });
                    });
                    this.rePaint();
                }
                this.emit('transform');
            });
            return;
        }
        if (type === 'empty' && button === 'left') {
            const w = Math.abs(offsetX - startX);
            const h = Math.abs(offsetY - startY);
            const x = offsetX < startX ? offsetX : startX;
            const y = offsetY < startY ? offsetY : startY;
            this.selectedInfo = {
                type: 'keys',
                info: [],
            };
            // const allKeys: IPoint[] = [];
            this.curveKeyQueue.forEach((key) => {
                const keys = [];
                const curveInfo = this.editCurveInfo[key];
                for (const info of curveInfo.ctrlKeys) {
                    if (info.key.canvas.x > x && info.key.canvas.x < x + w && info.key.canvas.y > y && info.key.canvas.y < y + h) {
                        // allKeys.push(info.key.canvas);
                        keys.push(info);
                    }
                }
                if (keys.length) {
                    this.selectedInfo.info.push({
                        key,
                        keys,
                    });
                }
            });
            this.activeBoxInfo = {
                selectBox: { x, y, w, h },
            };
            this.mouseDownPoint.movingType = 'select';
            this.clear();
            this.paintAssistBox(this.activeBoxInfo.selectBox);
            this.paintCtrlPoint();
        }
    }
    // TODO  document 
    onMouseUp(event) {
        if (!this.isMouseDownFromCurrent) {
            return;
        }
        this.isMouseDownFromCurrent = false;
        this.canvas.style.cursor = 'default';
        const type = this.mouseDownPoint && this.mouseDownPoint.type;
        const hasNode = event.path.find((item) => item === this.editBox.box);
        if (!hasNode) {
            this.editBox.main.style.display = 'none';
            if (this.selectedInfo && this.editKeyInfo && this.selectedInfo.type === 'keys' && this.selectedInfo.info.length && this.currentKeyIndex) {
                // 
                const res = this.applyEditKeyInfo();
                res && this.emitOperate('move-keys', this.selectedInfo.info);
                this.emitConfirm();
                this.editKeyInfo = null;
            }
        }
        if (this.readonly) {
            this.mouseDownPoint = null;
            return;
        }
        if (this.mouseDownPoint && this.mouseDownPoint.button === 'right' && type !== 'timeline' && !this.mouseDownPoint.offset) {
            this.onPopMenu({
                x: event.pageX,
                y: event.pageY,
            });
        }
        if (this.activeBoxInfo && this.activeBoxInfo.selectBox && this.selectedInfo.type === 'keys') {
            this.activeBoxInfo.selectBox && this.emitOperate('select', this.selectedInfo.info);
            this.activeBoxInfo.selectBox = undefined;
            this.rePaint();
            this.mouseDownPoint = null;
            return;
        }
        if (this.changeType) {
            this.mouseDownPoint.keys && this.emitOperate(this.changeType, this.mouseDownPoint.keys);
            this.emitConfirm();
            this.changeType = '';
        }
        if (type === 'empty') {
            if (!this.mouseDownPoint.movingType || this.mouseDownPoint.movingType !== 'select') {
                this.selectedInfo = null;
            }
            this.mouseDownPoint = null;
            this.emitOperate('unselect');
            this.resetCtrl('reset');
            // 
            return;
        }
        this.mouseDownPoint = null;
    }
    /**
     * 
     */
    async onPopMenu(position) {
        const mouseDownPoint = this.mouseDownPoint;
        const { type, offsetX, offsetY } = mouseDownPoint;
        const menu = [];
        const copyKeys = await this.getCopyKeys();
        let x = this.grid.pixelToValueX(offsetX);
        if (this.grid.xFormat) {
            x = Number(this.grid.xFormat(x));
        }
        const info = this.ctrlKeys[this.currentKeyIndex];
        let targetXValue = String(info ? info.key.point.x : x);
        if (this.currentSelectKeys && this.currentSelectKeys.length > 1) {
            targetXValue = this.currentSelectKeys.map((item) => this.grid.xFormat(item.key.point.x)).toString();
        }
        if (copyKeys.length) {
            let targetX = offsetX;
            let targetY = offsetY;
            // 
            if (mouseDownPoint?.type === 'key' && this.currentKeyIndex) {
                if (mouseDownPoint?.keys) {
                    const { x, y } = mouseDownPoint?.keys[0].keys[0].key.canvas;
                    targetX = x;
                    targetY = y;
                }
            }
            menu.push({
                label: this.translation('ui-kit.curve_editor.paste'),
                // accelerator: 'CmdOrCtrl+V',
                click: () => {
                    this.pasteCopyKeys(targetX, targetY);
                },
            });
        }
        if (['curve'].includes(type)) {
            // menu.push({
            //     label: Editor.I18n.t('ui-kit.curve_editor.createKeyInCurve'),
            //     click: () => {
            //         this.createKeyInCurveAt(x);
            //     },
            // });
        }
        else if (['key', 'activeBox'].includes(type) && this.selectedInfo.info && this.selectedInfo.info.length) {
            menu.push({
                label: this.translation('ui-kit.curve_editor.delete'),
                // accelerator: 'Delete',
                click: () => {
                    this.delSelectedKeys();
                },
            });
            menu.push({
                label: this.translation('ui-kit.curve_editor.copy'),
                // accelerator: 'CmdOrCtrl+C',
                click: () => {
                    this.copySelectedKeys();
                },
            });
            //  dump 
            if (this.currentSelectKeys?.length === 1) {
                menu.push({
                    label: this.translation('ui-kit.curve_editor.edit'),
                    click: () => {
                        if (this.currentSelectKeys) {
                            const keyData = this.currentSelectKeys[0].key;
                            this.editKeyInfo = {
                                x: keyData.point.x,
                                y: keyData.point.y,
                            };
                            this.updateEditBoxPosition();
                            this.editBox.main.style.display = 'block';
                        }
                    },
                });
            }
            // @ts-ignore
            if (this.selectedInfo.info.length === 1 && this.currentSelectKeys[0].key.interpMode === RealInterpMode.CUBIC) {
                menu.push({
                    type: 'separator',
                });
                // @ts-ignore
                menu.push({
                    label: this.translation(`ui-kit.curve_editor.broken`) || 'Broken',
                    type: 'checkbox',
                    checked: this.currentSelectKeys[0].key.broken,
                    click: () => {
                        this.updateKeyBrokenState(!this.currentSelectKeys[0].key.broken);
                    },
                });
            }
            // @ts-ignore
            if (this.currentSelectKeys.length > 1) {
                menu.push({
                    label: this.translation('ui-kit.curve_editor.spacingKey') + ` (${this.config.spacingFrame})`,
                    click: () => {
                        this.spacingSelectedKeys();
                    },
                });
                menu.push({
                    type: 'separator',
                });
            }
            menu.push({
                label: this.translation('ui-kit.curve_editor.flat'),
                click: () => {
                    this.flatSelectedKeys();
                },
            });
            menu.push({
                type: 'separator',
            });
            if (info) {
                menu.push({
                    label: this.translation(`${I18N_HEADER}.realCurve.RealInterpMode.label`) || 'InterpMode',
                    sublabel: this.translation(`${I18N_HEADER}.realCurve.RealInterpMode.tip`) || 'InterpMode',
                    submenu: this.showInterpMethodMenu(info.key.interpMode),
                });
                menu.push({
                    label: this.translation(`${I18N_HEADER}.realCurve.TangentWeightMode.label`) || 'TangentWeightMode',
                    sublabel: this.translation(`${I18N_HEADER}.realCurve.TangentWeightMode.tip`) || 'TangentWeightMode',
                    submenu: this.showTangentWeightMenu(info.key.tangentWeightMode),
                });
            }
        }
        else if (this.curveKeyQueue.length === 1) {
            // 
            menu.push({
                label: this.translation('ui-kit.curve_editor.createKey'),
                click: () => {
                    this.createNewKey({
                        x: offsetX,
                        y: offsetY,
                    });
                },
            });
        }
        menu.push({
            type: 'separator',
        });
        menu.push({
            label: `Keys: ${targetXValue}`,
            enabled: false,
        });
        const menuHandle = require('@electron/remote').Menu.buildFromTemplate(menu);
        menuHandle.popup({
            x: position.x,
            y: position.y,
        });
    }
    showTangentWeightMenu(tangentWeightMode) {
        return CurveCtrl.tangentWeightModeList.map((item) => {
            return {
                label: this.translation(`${I18N_HEADER}.realCurve.TangentWeightMode.${item.name}.label`) || item.name,
                sublabel: this.translation(`${I18N_HEADER}.realCurve.TangentWeightMode.${item.name}.tip`) || item.name,
                type: 'checkbox',
                click: () => {
                    this.changeTangentWeight(item.value);
                },
                checked: tangentWeightMode === item.value,
            };
        });
    }
    showInterpMethodMenu(interpMode) {
        return CurveCtrl.interpModeList.map((item) => {
            return {
                label: this.translation(`${I18N_HEADER}.realCurve.RealInterpMode.${item.name}.label`) || item.name,
                sublabel: this.translation(`${I18N_HEADER}.realCurve.RealInterpMode.${item.name}.tip`) || item.name,
                type: 'checkbox',
                click: () => {
                    this.changeInterpMode(item.value);
                },
                checked: interpMode === item.value,
            };
        });
    }
    changeTangentWeight(tangentWeightMode) {
        if (!this.currentSelectKeys || this.selectedInfo.type !== 'keys') {
            return;
        }
        this.currentSelectKeys.forEach((info) => {
            const index = this.ctrlKeys.findIndex((item) => item === info);
            this.curve.changeTangentWeight(index, tangentWeightMode);
            //  ctrlKey 
            this.ctrlKeys[index] = Object.assign(info, this.calcCtrl(this.ctrlKeys[index].key, this.curve.keyframes[index]));
        });
        this.refreshRender();
        this.emitOperate('change-tangent-weight', this.selectedInfo.info);
        this.emitChange();
        this.emitConfirm();
    }
    resetScale() {
        this.grid.resetScale();
        this.rePaint();
        this.emit('transform');
    }
    setDisplayRange(xRange, yRange) {
        this.grid.setDisplayRange(xRange, yRange);
        this.grid.clear();
        this.rePaint();
        this.emit('transform');
    }
    changeInterpMode(insertMethod) {
        if (this.selectedInfo.type !== 'keys') {
            return;
        }
        this.currentSelectKeys.forEach((info) => {
            const index = this.ctrlKeys.findIndex((item) => item === info);
            this.curve.changeInterpMode(index, insertMethod);
            //  ctrlKey 
            this.ctrlKeys[index] = Object.assign(info, this.calcCtrl(this.ctrlKeys[index].key, this.curve.keyframes[index]));
        });
        this.refreshRender();
        this.emitOperate('change-interp-mode', this.selectedInfo.info);
        this.emitChange();
        this.emitConfirm();
    }
    // 
    showCurveWrapMode(type) {
        const wrapMode = this.curve.config[type];
        return CurveCtrl.extrapModeList.map((item) => {
            return {
                label: this.translation(`${I18N_HEADER}.realCurve.ExtrapMode.${item.name}.label`) || item.name,
                sublabel: this.translation(`${I18N_HEADER}.realCurve.ExtrapMode.${item.name}.tip`) || item.name,
                type: 'checkbox',
                click: () => {
                    this.changeCurveWrapMode(type, item.value);
                },
                checked: wrapMode === item.value,
            };
        });
    }
    changeCurveWrapMode(type, wrapMode) { }
    /**
     * 
     * @param info 
     */
    showPoint(position) {
        this.ctrlState.showCtrlHandle = true;
        this.showKeyValueInfo(position);
    }
    /**
     * 
     */
    paintAssistBox(boxInfo) {
        const { x, y, w, h } = boxInfo;
        this.ctx.save();
        this.ctx.strokeStyle = this.colors.selectBoxLine;
        this.ctx.setLineDash([4, 5]);
        this.ctx.lineWidth = 1;
        this.ctx.strokeRect(x, y, w, h);
        this.ctx.restore();
    }
    /**
     * 
     */
    lightCurve() {
        if (this.ctrlState.lightCurve && !this.selectedInfo || this.selectedInfo.type !== 'curve') {
            return;
        }
        this.ctx.strokeStyle = this.colors.light;
        this.curve.paintWithCache(this.ctx);
        this.ctrlState.lightCurve = true;
    }
    lightCurveClip() {
        if (!this.selectedInfo || this.selectedInfo.type !== 'clip') {
            return;
        }
        this.ctx.strokeStyle = this.colors.light;
        this.curve.paintRect({
            start: this.selectedInfo.info[0].start,
            end: this.selectedInfo.info[0].end,
            ctx: this.ctx,
        });
        this.ctrlState.lightCurveClip = true;
    }
    /**
     * 
     * @param info 
     */
    showCtrl(info, i) {
        // 
        if (typeof this.currentKeyIndex === 'number') {
            this.resetCtrl();
            this.lightCurve();
        }
        this.drawCtrl();
        this.ctx.strokeStyle = this.colors.defaultLine;
        this.ctx.fillStyle = this.curve.config.strokeStyle;
        this.paintSingleCtrlPoint(this.currentSelectKeys[0]);
    }
    /**
     * 
     * @param info 
     * @param ctx 
     */
    drawCtrl() {
        if (!this.ctrlKeys[this.currentKeyIndex]) {
            return;
        }
        const info = this.ctrlKeys[this.currentKeyIndex];
        const ctx = this.ctx;
        this.ctrlState.showCtrlHandle = true;
        const { key: { canvas }, } = info;
        ctx.save();
        this.selectedInfo.info = [{
                key: this.currentCurveKey,
                keys: [info],
            }];
        // ()
        this.paintSelectedKeys();
        this.showKeyCtrlHand(info);
        ctx.restore();
        this.showPoint(canvas);
    }
    /**
     * 
     */
    paintSelectedKeys() {
        if (!this.selectedInfo || this.selectedInfo.type !== 'keys') {
            this.scaleCtrl.main.style.visibility = `hidden`;
            return;
        }
        const keyInfos = this.selectedInfo.info;
        if (!keyInfos || !keyInfos.length) {
            this.scaleCtrl.main.style.visibility = `hidden`;
            return;
        }
        if (this.mouseDownPoint && this.mouseDownPoint.startInfo) {
            this.paintActiveCtrlBox();
            return;
        }
        this.updateActiveBox();
        this.paintActiveCtrlBox();
        keyInfos.forEach((item) => {
            item.keys.forEach((key) => {
                this.ctx.fillStyle = 'gold';
                this.paintSingleCtrlPoint(key);
            });
        });
        if (this.ctrlKeys[this.currentKeyIndex]) {
            this.showPoint(this.ctrlKeys[this.currentKeyIndex].key.canvas);
        }
    }
    updateActiveBox() {
        const keyInfos = [];
        this.selectedInfo.info.forEach((info) => {
            keyInfos.push(...info.keys);
        });
        if (!keyInfos.length) {
            return;
        }
        const box = this.calcActiveBox(keyInfos);
        if (box && keyInfos.length > 1) {
            this.activeBoxInfo = this.activeBoxInfo || {};
            this.activeBoxInfo.box = box;
            this.paintScaleCtrl(keyInfos);
        }
        else if (keyInfos.length === 1) {
            this.activeBoxInfo = null;
            this.ctrlKeys[this.currentKeyIndex] && this.showKeyCtrlHand(this.ctrlKeys[this.currentKeyIndex]);
        }
        else {
            this.activeBoxInfo = null;
        }
    }
    /**
     * 
     * @returns
     */
    calcActiveBox(keyInfos) {
        if (!keyInfos || !keyInfos.length) {
            this.scaleCtrl.main.style.visibility = `hidden`;
            return null;
        }
        const keys = Array.from(keyInfos);
        if (keys.length === 1) {
            const point = keys[0].key.canvas;
            return {
                x: point.x,
                y: point.y,
                w: 0,
                h: 0,
            };
        }
        else {
            const xArr = [];
            const yArr = [];
            for (const keyInfo of keyInfos) {
                xArr.push(keyInfo.key.canvas.x);
                yArr.push(keyInfo.key.canvas.y);
            }
            xArr.sort((a, b) => a - b);
            yArr.sort((a, b) => a - b);
            const x = Math.min(...xArr);
            const y = Math.min(...yArr);
            const res = {
                x: Math.round(x),
                y: Math.round(y),
                w: Math.round(Math.max(...xArr) - x),
                h: Math.round(Math.max(...yArr) - y),
            };
            //  w,h 
            if (res.w <= 0 || res.h <= 0) {
                res.w = 0;
                res.h = 0;
            }
            return res;
        }
    }
    paintScaleCtrl(keyInfos) {
        if (!keyInfos) {
            keyInfos = keyInfos || [];
            this.selectedInfo.info.forEach((info) => {
                keyInfos.push(...info.keys);
            });
        }
        const box = this.activeBoxInfo.box;
        //  0  dom 
        if (keyInfos.length < 1 || !box.w || !box.h) {
            this.scaleCtrl.main.style.visibility = `hidden`;
            return;
        }
        const { r } = this.config;
        this.scaleCtrl.main.style.width = `${box.w + r * 2}px`;
        this.scaleCtrl.main.style.height = `${box.h + r * 2}px`;
        this.scaleCtrl.main.style.top = `${box.y - r}px`;
        this.scaleCtrl.main.style.left = `${box.x - r}px`;
        this.scaleCtrl.main.style.visibility = `visible`;
    }
    updateEditBoxPosition() {
        if (!this.editKeyInfo || !this.currentSelectKeys) {
            return;
        }
        const keyData = this.currentSelectKeys[0].key;
        this.editBox.x.value = keyData.point.x;
        this.editBox.y.value = keyData.point.y;
        const top = clamp(keyData.canvas.y, 10, this.canvas.height - 70);
        const left = clamp(keyData.canvas.x, 10, this.canvas.width - 110);
        this.editBox.box.style.top = top + 'px';
        this.editBox.box.style.left = left + 'px';
    }
    /**
     * 
     */
    paintActiveCtrlBox() {
        if (!this.activeBoxInfo || !this.activeBoxInfo.box) {
            return;
        }
        const ctx = this.ctx;
        ctx.save();
        const { x, y, w, h } = this.activeBoxInfo.box;
        const { r } = this.config;
        // ()
        ctx.fillStyle = this.colors.selectBoxFill;
        ctx.fillRect(x - r, y - r, w + r * 2, h + r * 2);
        ctx.restore();
    }
    /**
     * 
     */
    showKeyCtrlHand(info) {
        const ctx = this.ctx;
        ctx.save();
        const { key: { canvas }, inCanvas, outCanvas, } = info;
        const { r } = this.config;
        ctx.fillStyle = this.colors.handler;
        ctx.strokeStyle = this.colors.defaultLine;
        // 
        // 
        if (inCanvas && info.key.interpMode === RealInterpMode.CUBIC) {
            CurveCtrl.drawLine(canvas, inCanvas, this.ctx);
            CurveCtrl.drawArc(inCanvas, r, this.ctx);
        }
        if (outCanvas && info.key.interpMode === RealInterpMode.CUBIC) {
            CurveCtrl.drawLine(canvas, outCanvas, this.ctx);
            CurveCtrl.drawArc(outCanvas, r, this.ctx);
        }
        ctx.restore();
    }
    /**
     * 
     */
    showKeyValueInfo(point) {
        // 
        const { x, y } = point;
        this.ctx.save();
        // 
        this.ctx.strokeStyle = this.colors.selectBoxLine;
        this.ctx.setLineDash([4, 8]);
        this.ctx.moveTo(x, this.canvas.height - this.grid.axisMargin);
        this.ctx.lineTo(x, y);
        this.ctx.lineTo(this.grid.axisMargin, y);
        this.ctx.stroke();
        // 
        const color = this.curve.config.strokeStyle;
        // this.ctx.fillStyle = this.colors.selectBoxLine;
        this.grid.ctx.clearRect(0, y - 10, 15, 15);
        // this.ctx.fillRect(0, y - 10, 15, 15);
        this.ctx.fillStyle = color;
        this.ctx.fillText(String(this.grid.yFormat(this.grid.pixelToValueY(y))), 0, y);
        this.ctx.fillText(String(this.grid.xFormat(this.grid.pixelToValueX(x))), x - this.grid.axisMargin / 2, this.canvas.height - this.grid.axisMargin / 2);
        this.ctx.restore();
    }
    /**
     * 
     */
    resetCtrl(flag) {
        if (!this.ctrlState.showCtrlHandle && !this.ctrlState.showAuxin && !flag) {
            return;
        }
        this.ctrlState.showAuxin = false;
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        // this.ctx.strokeStyle = DEFAULT_CTRL_COLOR;
        this.clear();
        // this.editCurveInfo[this.currentCurveKey].curve.paintWithCache(this.ctx);
        this.checkActiveInfo();
        this.paintCtrlPoint();
    }
    /**
     * 
     */
    paintCtrlPoint() {
        this.ctx.strokeStyle = this.colors.defaultLine;
        Object.values(this.editCurveInfo).forEach((curveInfo) => {
            this.ctx.fillStyle = curveInfo.curve.config.strokeStyle;
            const ctrlKeys = curveInfo.ctrlKeys;
            for (const info of ctrlKeys) {
                this.paintSingleCtrlPoint(info);
            }
        });
        this.paintSelectedKeys();
        //  settings 
        // this.paintSettingIcon();
        if (this.selectedInfo && this.selectedInfo.type === 'clip') {
            this.lightCurveClip();
        }
    }
    paintSingleCtrlPoint(ctrlKey) {
        const { r } = this.config;
        this.ctx.beginPath();
        const { x, y } = ctrlKey.key.canvas;
        this.ctx.moveTo(x, y + r);
        this.ctx.lineTo(x + r, y);
        this.ctx.lineTo(x, y - r);
        this.ctx.lineTo(x - r, y);
        this.ctx.lineTo(x, y + r);
        this.ctx.closePath();
        this.ctx.stroke();
        this.ctx.fill();
    }
    paintSettingIcon() {
        const ctrlKeys = this.editCurveInfo[this.currentCurveKey].ctrlKeys;
        if (!ctrlKeys || !ctrlKeys[0] || !ctrlKeys[ctrlKeys.length - 1]) {
            return;
        }
        this.settingsIconInfo = {
            pre: createPoint(-6, -6, ctrlKeys[0].key.canvas),
            post: createPoint(6, -6, ctrlKeys[ctrlKeys.length - 1].key.canvas),
        };
        this.ctx.save();
        //  ui-icon 
        this.paintIcon(ICON_U16.settings, this.settingsIconInfo.pre);
        this.paintIcon(ICON_U16.settings, this.settingsIconInfo.post);
        this.ctx.restore();
    }
    /**
     *  iconfont 
     * @param u16
     * @param position
     */
    paintIcon(u16, position) {
        const icon = eval(('("' + u16).replace('&#x', '\\u').replace(';', '') + '")');
        this.ctx.font = `${ICON_FONT_SIZE}px ${ICON_FONT_NAME}`;
        this.ctx.fillText(icon, position.x, position.y);
    }
    /**
     * 
     * @param operate
     * @param select
     * @param arg
     */
    emitOperate(operate, curveKeys, ...arg) {
        const data = [];
        if (curveKeys) {
            JSON.parse(JSON.stringify(curveKeys)).forEach((info) => {
                const keys = info.keys;
                data.push(Object.assign(info, { keys }));
            });
        }
        this.emit('operate', operate, data, ...arg);
    }
}
