'use strict';
import { Tooltip } from '../utils/utils';
export class Base extends HTMLElement {
    /**
     * 
     * @param eventName 
     */
    dispatch(eventName, options) {
        const targetOptions = {
            bubbles: true,
            cancelable: true,
        };
        if (options) {
            Object.assign(targetOptions, options);
        }
        const event = new CustomEvent(eventName, targetOptions);
        this.dispatchEvent(event);
    }
    set path(val) {
        this.setAttribute('path', val);
        this.dispatch('path');
    }
    get path() {
        return this.getAttribute('path') || '';
    }
    /**
     * 
     * @memberof Base
     */
    constructor(shadow) {
        super();
        if (shadow === undefined || shadow === true) {
            this.attachShadow({ mode: 'open' });
        }
    }
    /**
     * 
     */
    connectedCallback() {
        this._onConnected();
        if (this.path) {
            this.dispatch('path');
        }
        Tooltip.bind(this);
    }
    /**
     * 
     */
    disconnectedCallback() {
        this._onDisconnected();
        Tooltip.unbind(this);
    }
    /**
     * Element  HTML 
     */
    _onConnected() { }
    /**
     * Element 
     */
    _onDisconnected() { }
    /**
     *  attribute 
     */
    static get observedAttributes() {
        return ['tooltip'];
    }
    attributeChangedCallback(attr, oldValue, newValue) {
        switch (attr) {
            case 'path':
                this.dispatch('path');
                break;
            default:
                break;
        }
    }
}
