'use strict';
const fs = require('fs');
import { Channel } from './channel';
import { DisplayBase } from '../base';
import HTML from './index.html';
import STYLE from './index.less';
const CUSTOM_STYLE = `<style id="custom-style"></style>`;
const instanceArray = [];
let customStyle = '';
const { DragArea } = require('../../function/drag/drag-area');
// 
let srcTranslator = function (uuid) {
    return uuid;
};
export class Image extends DisplayBase {
    /**
     * 
     */
    constructor() {
        super();
        this.shadowRoot.innerHTML = `<style>${STYLE}</style>${CUSTOM_STYLE}${HTML}`;
        this.$img = this.shadowRoot.querySelector('img');
        this.$area = this.shadowRoot.querySelector('.area ui-drag-area');
        this.$placeholder = this.shadowRoot.querySelector('.placeholder');
        this.$canvas = this.shadowRoot.querySelector('canvas');
        this.$canvas.$root = this.$img.$root = this.$area.$root = this;
    }
    /**
     * 
     * @param {file} src
     */
    static importStyle(src) {
        if (!fs.existsSync(src)) {
            return;
        }
        //  css 
        customStyle = fs.readFileSync(src, 'utf8');
        // 
        instanceArray.map((elem) => {
            const $style = elem.shadowRoot.querySelector('#custom-style');
            $style.innerHTML = customStyle;
        });
    }
    get value() {
        const val = this.getAttribute('value');
        if (val) {
            return val.trim();
        }
        const txt = this.innerHTML;
        return txt.trim();
    }
    set value(val) {
        if (val) {
            this.setAttribute('value', val);
        }
        else {
            this.removeAttribute('value');
        }
    }
    get src() {
        return this.$img.src;
    }
    set src(val) {
        const areaRect = this.$area.getBoundingClientRect();
        if (!areaRect.width && areaRect.height) {
            this.$img.style.width = 'auto';
            this.$img.style.height = '100%';
        }
        else if (areaRect.width && !areaRect.height) {
            this.$img.style.width = '100%';
            this.$img.style.height = 'auto';
        }
        else {
            const fill = this.hasAttribute('fill');
            if (fill) {
                this.$img.style.width = '100%';
                this.$img.style.height = '100%';
            }
            else {
                this.$area.style.display = 'flex';
                this.$img.style.maxWidth = '100%';
                this.$img.style.maxHeight = '100%';
                this.$img.style.margin = 'auto';
            }
        }
        if (val) {
            this.$img.src = val.replace('#', '%23');
        }
        else {
            this.$img.removeAttribute('src');
        }
        if (this.channel) {
            this._updateChannel();
        }
    }
    get size() {
        const val = this.getAttribute('size');
        if (!val) {
            return '';
        }
        return val.trim();
    }
    set size(val) {
        val = val + '';
        if (val) {
            this.setAttribute('size', val);
        }
        else {
            this.removeAttribute('size');
        }
    }
    // 
    get placeholder() {
        return this.getAttribute('placeholder') || '';
    }
    set placeholder(val) {
        if (val) {
            this.setAttribute('placeholder', val);
        }
        else {
            this.removeAttribute('placeholder');
        }
    }
    get droppable() {
        return this.$area.droppable;
    }
    set droppable(val) {
        this.$area.droppable = val;
    }
    get disabled() {
        return this.hasAttribute('disabled');
    }
    set disabled(val) {
        if (val) {
            this.setAttribute('disabled', '');
        }
        else {
            this.removeAttribute('disabled');
        }
    }
    get readonly() {
        return this.hasAttribute('readonly');
    }
    set readonly(val) {
        if (val) {
            this.setAttribute('readonly', '');
        }
        else {
            this.removeAttribute('readonly');
        }
    }
    get channel() {
        if (this.hasAttribute('channel')) {
            return this.getAttribute('channel') || 'rgb';
        }
        return '';
    }
    set channel(val) {
        if (val === null) {
            this.removeAttribute('channel');
        }
        else {
            this.setAttribute('channel', val);
        }
    }
    // ////////////////////
    /**
     * 
     *  value  value  return 
     * @param func
     */
    static setSrcTranslator(func) {
        if (typeof func !== 'function') {
            return;
        }
        srcTranslator = func;
    }
    /**
     * 
     */
    _onConnected() {
        // 
        instanceArray.push(this);
        this._droppable = this.getAttribute('droppable') || '';
        this._placeholder = this.getAttribute('placeholder') || this._droppable || 'NONE';
        this._droppable && (this.$area.droppable = this._droppable);
        this._placeholder && (this.$placeholder.innerHTML = this._placeholder);
        !this.value && (this.setAttribute('title', this._droppable));
        // 
        if (this.shadowRoot) {
            const $style = this.shadowRoot.querySelector('#custom-style');
            if ($style) {
                $style.innerHTML = customStyle;
            }
        }
        // 
        this.$area.addEventListener('dragover', this._onAreaDragOver);
        this.$area.addEventListener('dragleave', this._onAreaDragLeave);
        this.$area.addEventListener('drop', this._onAreaDrop);
        if (this.channelInstance) {
            this.channelInstance.clear();
            this.channelInstance = undefined;
        }
        this._onConnectedCallback();
    }
    /**
     * 
     */
    _onDisconnected() {
        // 
        const index = instanceArray.indexOf(this);
        instanceArray.splice(index, 1);
        // 
        this.$area.removeEventListener('dragover', this._onAreaDragOver);
        this.$area.removeEventListener('dragleave', this._onAreaDragLeave);
        this.$area.removeEventListener('drop', this._onAreaDrop);
        this._onDisconnectedCallback();
    }
    /**
     * @description  attribute 
     */
    static get observedAttributes() {
        return [
            'value',
            'droppable',
            'size',
            'placeholder',
            'disabled',
            'readonly',
            'fill',
            'src',
            'path',
            'channel', // 
        ];
    }
    /**
     * @description attribute 
     * @param attr
     * @param oldVal
     * @param newVal
     */
    attributeChangedCallback(attr, oldVal, newVal) {
        switch (attr) {
            case 'value':
                if (oldVal === newVal) {
                    break;
                }
                this._onValueChanged(newVal);
                break;
            case 'droppable':
                this._onDroppableChanged(newVal);
                break;
            case 'size':
                this._onSizeChanged(newVal);
                break;
            case 'placeholder':
                this._onPlaceholderChanged(newVal);
                break;
            case 'channel':
                this._updateChannel();
                break;
        }
    }
    // ////////////////////
    // 
    /**
     *  value 
     */
    async _onValueChanged(val) {
        const url = await srcTranslator.call(this, val);
        if (this.getAttribute('value') === val) {
            this.src = url;
            if (this.$area.value !== val) {
                this.$area.value = val;
            }
            if (!val) {
                this.removeAttribute('value');
            }
        }
    }
    /**
     * @description  callback 
     */
    _onConnectedCallback() { }
    /**
     * @description  callback 
     */
    _onDisconnectedCallback() { }
    /**
     *  size 
     */
    _onSizeChanged(val) {
        if (val) {
            const sizeArr = val.split(',').map(item => item.trim());
            if (sizeArr.length > 1) {
                this.$img.style.width = sizeArr[0] + 'px';
                this.$img.style.height = sizeArr[1] + 'px';
                return;
            }
            this.$img.style.width = this.$img.style.height = sizeArr[0] + 'px';
        }
    }
    /**
     *  droppable 
     */
    _onDroppableChanged(val) {
        this._droppable = val;
        this._placeholder = this.$placeholder.innerHTML = val;
        this.$area.droppable = this._droppable;
    }
    /**
     *  placeholder 
     */
    _onPlaceholderChanged(val) {
        this._placeholder = val || 'NONE';
        this.$placeholder.innerHTML = this._placeholder;
    }
    // 
    /**
     *  area 
     */
    _onAreaDragOver() {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        $elem.$root.setAttribute('state', ($elem.hoving || $elem.additional) ? 'allow' : 'refused');
    }
    /**
     *  area 
     */
    _onAreaDragLeave() {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        $elem.$root.removeAttribute('state');
    }
    /**
     *  area
     */
    _onAreaDrop(event) {
        const $elem = this;
        if ($elem.$root.disabled || $elem.$root.readonly) {
            return;
        }
        const state = $elem.$root.getAttribute('state');
        $elem.$root.removeAttribute('state');
        if (state === 'refused') {
            return;
        }
        //  setData('value')
        const value = event.dataTransfer?.getData('value');
        if (value) {
            $elem.$root.value = value;
            $elem.$root.dispatch('confirm');
            $elem.$root.invalid = false;
            return;
        }
        //  event.dataTransfer 
        if (this.droppable.includes('file') && event.dataTransfer?.files.length) {
            const fileInfo = event.dataTransfer?.files[0];
            $elem.$root.value = fileInfo.path;
            $elem.$root.dispatch('confirm');
            $elem.$root.invalid = false;
            return;
        }
        if ($elem.hoving || $elem.additional) {
            const types = $elem.droppable;
            const list = DragArea.currentDragInfo.additional;
            if (list) {
                for (const item of list) {
                    if (types.includes(item.type)) {
                        $elem.$root.value = item.value;
                        $elem.$root.dispatch('confirm');
                        $elem.$root.invalid = false;
                        return;
                    }
                }
            }
        }
    }
    /**
     * @description  channel
     * */
    _updateChannel() {
        if (!this.channelInstance) {
            this.channelInstance = new Channel(this);
        }
        this.channelInstance.update();
    }
}
